define([
    'angular'
    ], function(angular) {
        return function(app, elem, attrs, scope) {
            app.factory('basicStaffMgmtService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
                return {
                    selectRbacUserByParam: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacuserscs/selectRbacUserByParam',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigModules/basicStaffMgmt/basicStaffMgmt.json'//假数据api
                        });
                    },
                    updateRbacUser: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacuserscs/updateRbacUser',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigModules/header/success.json'//假数据api
                       });
                    },
                    getOrg: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacorganizationcs/selectCascadeRbacOrganizationByCode',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigModules/basicStaffMgmt/basicStaffMgmt.json'//假数据api
                       });
                    },
                    selectDictionaryList: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdpubdictionarycs/selectPubDictionaryListByParam',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigModules/addNewStaff/addNewStaffDictionary.json'//假数据api
                        });
                    },
                    selectRbacRoleByParam:function(opts){
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacrolecs/selectRbacRoleByParam',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigWeb/basicConfigModules/systemRoleMgmt/selectRbacRoleByParam.json'//假数据api
                        });                    
                    },
                    resetPwd:function(opts){
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacuserscs/updateUserPassword',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigWeb/basicConfigModules/systemRoleMgmt/selectRbacRoleByParam.json'//假数据api
                        });                    
                    }
                };
            }]);
        }
    });