define([
  'angular',
  '../discoveryPreload/discoveryDetail.html',
], function (
  angular,
  html) {
  return function (app, elem, attrs, scope) {
    app.directive('discoveryDetailDirective', ['discoveryPreloadService', 'G', function (service, G) {
      return {
        template: html,
        restrict: 'EA',
        scope: {
          discoveryconfigid: '='
        },
        link: function postLink($scope, $element, $attrs) {
          $scope.fetchData = function () {
            service.detailPreload({
              discoveryconfigid: $scope.discoveryconfigid
            }).then(function (data) {
              if (data && data.data && data.data.result === 'success') {
                $scope.discoveryData = data.data.data;
                var version = $scope.discoveryData.version.replace(/A/, '安卓');
                version = version.replace(/&?I/, '  IOS');
                $scope.discoveryData.version = version;
              } else {
                G.alert(data.data.msg || '查询详情失败', {
                  type: 'error'
                });
              }
            }, function (data) {
              G.alert(data.data.msg || '查询详情失败', {
                type: 'error'
              });
            });
          }


          $('#details').on('show.bs.modal', function (e) {
            $scope.discoveryData = '';
            $scope.fetchData();
          });

        }
      };
    }]);
  }
});