define([
    '../systemDataDir/systemDataDirCtrl',
    '../systemDataDir/systemDataDirService',
    '../addNewDatadir/addNewDatadir',
    '../treeView/treeView',
    'scmsModules/tooltip/tooltip',
    '../systemDataDir/systemDataDir.html',
    '../systemDataDir/systemDataDir.css'
], function(
    ctrl,
    service,
    addNewDatadir,
    treeView,
    tooltip,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        addNewDatadir(app, elem, attrs, scope);
        treeView(app, elem, attrs, scope);
        tooltip(app, elem, attrs, scope);
        elem.append(html);
    }
});