define([
    'angular'
], function(
    angular
) {
    return function(app, elem, attrs, scope) {
        app.controller('basicDeptMgmtCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'basicDeptMgmtService', 'navsData',
            function($rootScope, $scope, $state, $stateParams, $timeout, G, service, navsData) {
            var searchLock = false;
            $scope.introduction='部门是各组织架内部的部门划分。总部，各城市可以根据自己管理需要设置一级部门->二级部门->三级部门->四级部门，部门名称根据实际职能可自定义。支持新增，修改，查看，删除。';
            $scope.topPos='right';
            $scope.staffList = {};
            $scope.keywords = '';
            $scope.showTop = false;
            // $scope.pageSize = G.getPageSize();

            function getAllDept(opts, defaultItem){
                service.getAllDept(opts).then(function(data) {
                    if(data&&data.data&&data.data.result==='success'){
                        var dataList = data.data.data;
                        dataList.push({
                            pardepartmentid:'TOP',
                            ehdrbacdepartmentid:0,
                            departmentname:G.userInfo.organizationname,
                            organizationcode:G.userInfo.organizationcode,
                            organizationname:G.userInfo.organizationname
                        });
                        $scope.initDept = G.toTree(dataList,'TOP', 'pardepartmentid', 'ehdrbacdepartmentid');
                        $scope.deptTree = new Array($scope.initDept);
                        $scope.selectedDept = defaultItem?defaultItem:$scope.initDept;
                        $scope.defaultItem = G.clone($scope.selectedDept);
                        getStaffList($scope.selectedDept);
                    }else{
                        G.errorAlert('查询失败', data);
                    }
                }, function(data){
                    G.errorAlert('查询失败', data);
                });
            }

            function getStaffList($item, currPage){
                var opts;
                $scope.currPage = currPage || 1;
                $scope.searchResult=false;
                if($item.pardepartmentid==='TOP'){
                    $scope.showTop = false;
                    opts={
                        keywords:$scope.keywords,
                        organizationcode: $item.organizationcode,
                        organizationflag: 1,
                        skipCount: ($scope.currPage - 1) * $scope.pageSize,
                        pageSize: $scope.pageSize
                    }
                }else {
                    $scope.showTop = true;
                    opts={
                        keywords:$scope.keywords,
                        ehdrbacdepartmentid: $item.ehdrbacdepartmentid, 
                        skipCount: ($scope.currPage - 1) * $scope.pageSize,
                        pageSize: $scope.pageSize
                    }                    
                }
                G.loading(true);
                service.getStaffList(opts).then(function(data) {
                    searchLock = false;
                    if(data&&data.data&&data.data.result==='success'){
                        if($scope.searchFlag){
                            if(!data.data.data||!data.data.data.length){
                                $scope.searchResult=true;
                            }
                        }
                        $scope.searchFlag = false;                        
                        $scope.staffList.data = data.data.data;
                        $scope.staffList.totalCount = data.data.count;
                    }else{
                        G.errorAlert('查询失败', data);
                    }
                    G.loading(false);
                },function(data){
                    G.loading(false);
                    searchLock = false;
                    G.errorAlert('查询失败', data);
                });
            }

            $scope.isTop = function(){
                if(!$scope.selectedDept){
                    return true;
                }else{
                    return $scope.selectedDept.pardepartmentid!=null?$scope.selectedDept.pardepartmentid==='TOP':true;
                }
                
            };

            $scope.search=function() {
                $scope.searchResult=false;
                $scope.searchFlag=true;
                $scope.keywordsCopy=G.clone($scope.keywords);
                getStaffList($scope.selectedDept,1);
                searchLock = true;
            }

            $scope.returnPre = function() {
                $scope.keywords = '';
                $scope.searchResult = false;
                getStaffList($scope.selectedDept,1);
            }            
            $scope.removeDept = function($event){
                var $alert = G.confirm({
                    $target: $($event.target),
                    msg: '确认删除？',   
                    position: 'bottom',
                    submit: function() {
                        service.updateDept({
                            ehdrbacdepartmentid: $scope.selectedDept.ehdrbacdepartmentid,
                            organizationcode: G.userInfo.organizationcode,
                            organizationname: G.userInfo.organizationname,
                            departmentstatus: 9
                        }).then(function(data) {
                            data = data.data;
                            $alert.hide();
                            if(data.result === 'success') {                                
                                init();
                                $scope.$broadcast('setRootActive');
                                G.alert('删除成功');
                            }else{
                                G.alert(data.msg || '操作失败');
                            }
                        }, function(data) {
                            G.alert(data.msg);
                        });  
                    }
                });
            };


            $scope.removeStaff = function(item, $event){
                var $alert = G.confirm({
                    $target: $($event.target),
                    msg: '确认移除？',
                    position: 'bottom',
                    submit: function() {
                        service.updateStaff({
                            ehdrbacusersid: item.ehdrbacusersid,
                            ehdrbacdepartmentid: $scope.selectedDept.ehdrbacdepartmentid
                        }).then(function(data) {
                            data = data.data;
                            $alert.hide();
                            if(data.result === 'success') {                                
                                getStaffList(item);
                                G.alert('删除成功');
                            }else {
                                G.alert(data.msg || '操作失败', {
                                    type: 'error'
                                });
                            }                    
                        }, function(data) {
                            G.alert(data.msg, {
                                type: 'error'
                            });
                        });  
                    }
                });
            };

            $scope.getDeptDetails = function($item){
                $scope.selectedDept = $item;
                $scope.keywords = '';
                getStaffList($item);
            };
            
            $scope.isUpdate = function(updateFlag){
                $scope.updateFlag = updateFlag;
            };

            function init(){
                $scope.permission = navsData.permission;
                getAllDept({organizationcode:G.userInfo.organizationcode});
            }

            $scope.$watch('currPage + pageSize', function(newValue, oldValue) {
                if(newValue && oldValue && newValue !== oldValue && !searchLock) {
                    if($scope.selectedDept){
                        getStaffList($scope.selectedDept,$scope.currPage);
                    }
                    searchLock = false;
                }
            });

            $scope.$on('addMarketSuccess', function(){
                getAllDept({organizationcode:G.userInfo.organizationcode}, $scope.selectedDept);
            })

            $scope.$on('updateOrg',function(){
                init();
            });

            init();
            
        }]);

    }
});