define([
  'angular'
], function (angular) {
  return function (app, elem, attrs, scope) {
    app.factory('discoveryPreloadService', ['$http', function ($http) {
      return {
        listPreload: '/goodstaxiAdmin/discoveryconfigcs/selectDiscoveryConfigByContion',
        detailPreload: function (data) {
          return $http({
            url: '/goodstaxiAdmin/discoveryconfigcs/selectDiscoveryConfigById',
            method: 'post',
            data: data,
          });
        },
        deletePreload: function (data) {
          return $http({
            url: '/goodstaxiAdmin/discoveryconfigcs/deleteDiscoveryConfigById',
            method: 'post',
            data: data,
          });
        }
      }
    }])
  }
});