define([
    '../addNewAreaDialog/addNewAreaDialog.css',
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('addNewAreaDialogService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                getMarketType: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdpubdictionarycs/selectPubDictionaryListByParam',
                        method: 'post',
                        data: options,
                        test: 'basicConfigModules/basicAreaMgmt/areaType.json'//假数据api
                    });
                },

                addMarket: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacmarketplacecs/insertRbacMarketPlace',
                        method: 'post',
                        data: options,
                        noconcurrent: true,
                        test: 'basicConfigModules/header/success.json'//假数据api
                    });
                },

                updateMarket: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacmarketplacecs/updateRbacMarketPlace',
                        method: 'post',
                        data: options,
                        noconcurrent: true,
                        test: 'basicConfigModules/header/success.json'//假数据api
                    });
                }
            };
        }]);
    }
});