define([
    '../basicStaffMgmt/basicStaffMgmtCtrl',
    '../basicStaffMgmt/basicStaffMgmtService',
    'scmsModules/pagination/paginationDirective',
    '../addNewStaff/addNewStaffDirective',
    '../addNewStaffSuccess/addNewStaffSuccessDirective',
    '../resetPwdSuccess/resetPwdSuccessDirective',
    '../staffDataPermission/staffDataPermissionDirective',
    '../treeView/treeView',
    'scmsModules/tooltip/tooltip',
    '../basicStaffMgmt/basicStaffMgmt.html',
    '../basicStaffMgmt/basicStaffMgmt.css'
], function(
    ctrl,
    service,
    paginationDirective,
    addNewStaffDirective,
    addNewStaffSuccessDirective,
    resetPwdSuccessDirective,
    staffDataPermissionDirective,
    treeView,
    tooltip,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        paginationDirective(app, elem, attrs, scope);
        addNewStaffDirective(app, elem, attrs, scope);
        addNewStaffSuccessDirective(app, elem, attrs, scope);
        resetPwdSuccessDirective(app, elem, attrs, scope);
        staffDataPermissionDirective(app, elem, attrs, scope);
        treeView(app, elem, attrs, scope);
        tooltip(app, elem, attrs, scope);
        elem.append(html);
    }
});