define([
    'angular',
    '../addNewUpgradeDialog/addNewUpgradeDialogService',
    '../addNewUpgradeDialog/addNewUpgradeDialog.html'
    ], function(
        angular,
        addNewUpgradeDialogService,
        html) {
        return function(app, elem, attrs, scope) {
            addNewUpgradeDialogService(app, elem, attrs, scope);
            app.directive('addNewUpgradeDialogDirective', ['G', 'addNewUpgradeDialogService', 'FileUploader', '$timeout', 'navsData', function(G, service, FileUploader, $timeout, navsData) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        currentItem:'=',
                        menuList:'='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude,$timeout, $log, $http, G) {

                        $scope.submit = function (){
                            var param = {
                                versioncode: $scope.formData.versioncode,
                                systemname: $scope.formData.selectMenu.resourcename,
                                program: $scope.formData.program,
                                description: $scope.formData.description,
                                attachmenturl: $scope.formData.attachmenturl,
                                navigations: $scope.formData.navigations
                            };
                            if($scope.currentItem){
                                param.versionmanageid = $scope.currentItem.versionmanageid;
                                modifyVersion(param);
                            }else{
                                addVersion(param);
                            }
                        };
                        function addVersion(param){
                            service.addVersion(param).then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    G.alert('添加成功');
                                    $element.modal('hide');
                                    $scope.$emit('refreshVersionList');
                                }else{
                                    G.errorAlert('添加失败', data);
                                }
                            },function(data){
                                G.errorAlert('添加失败', data);
                            });
                        }

                        function modifyVersion(param){
                            service.modifyVersion(param).then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    G.alert('更新成功');
                                    $element.modal('hide');
                                    $scope.$emit('refreshVersionList');
                                }else{
                                    G.errorAlert('更新失败', data);
                                }
                            },function(data){
                                G.errorAlert('更新失败', data);
                            });
                        }

                        function setUploadFile(){
                            $scope.uploader = new FileUploader({
                                url: '/grayReleaseAdmin/ehuodiappversioncs/upload'
                            });

                            $scope.uploader.onAfterAddingFile = function(fileItem) {
                                G.loading(true,{
                                    $container: $element.find('.modal-content')
                                });
                                $scope.formData.uploadFileName=fileItem._file.name;
                                angular.element('#versionPPT')[0].value = '';
                                fileItem.upload();
                            };
                            $scope.uploader.onCompleteItem = function(fileItem, response, status, headers) {
                                G.loading(false,{
                                    $container: $element.find('.modal-content')
                                });
                                if(response&&response.result==='success'){
                                    $scope.formData.attachmenturl = response.data.downloadurl + '?fileName=' + $scope.formData.uploadFileName;
                                }else{
                                    G.alert('文件上传失败',{type:'error'});
                                }
                            };
                        }
    
                        setUploadFile();

                        function initForm(){
                            $scope.menuListFromList = G.clone(navsData.data);
                            $scope.closeAllMenu();
                            $scope.errors = {};
                            if($scope.currentItem){
                                $scope.formData = G.clone($scope.currentItem);
                                $scope.formData.description = $scope.formData.description.replace(/<br>/g, '\n');
                                $scope.formData.uploadFileName = $scope.formData.attachmenturl.substring($scope.formData.attachmenturl.lastIndexOf('=') + 1);
                                angular.forEach($scope.menuListFromList, function(item){
                                    if(item.resourcename===$scope.formData.systemname){
                                        $scope.formData.selectMenu = item;
                                        $scope.formData.firstGroupMenu = item.children;
                                    }
                                });
                                if($scope.formData.navigations){
                                    angular.forEach(G.clone(navsData.navsList), function(item){
                                        if(item.resourceurl.indexOf($scope.formData.navigations)>-1){
                                            $scope.formData.displayNav = item.resourcename + '(' + $scope.formData.navigations + ')';
                                        }
                                    });
                                }
                                if(!$scope.formData.displayNav){
                                    $scope.formData.navigations = '';
                                }
                            }else{
                                $scope.formData = {};
                            }
                            G.clearAllError();
                        }

                        $scope.toggleModule = function(){
                            if($scope.formData.firstGroupMenu){
                                $scope.showFirstGroup = !$scope.showFirstGroup;
                                $scope.showThirdGroup = false;
                                $scope.showFourthGroup = false;
                            }
                        };

                        $scope.changeModule = function(){
                            $scope.clearNav();
                            angular.forEach($scope.menuListFromList, function(item){
                                if(item.resourcename===$scope.formData.selectMenu.resourcename){
                                    $scope.formData.firstGroupMenu = item.children;
                                }
                            });
                        };

                        $scope.selectSecond = function(second){
                            if(second.children){
                                $scope.thirdGroupMenu = second.children;
                                $scope.showThirdGroup = true;
                                if(second.resourceurl!==';'){
                                    $scope.formData.navigations = second.resourceurl
                                }
                            }else if(second.resourceurl){
                                $scope.formData.navigations = second.resourceurl.substring(second.resourceurl.lastIndexOf(';')+1);
                                $scope.formData.displayNav = second.resourcename + '(' + $scope.formData.navigations + ')';
                                $scope.closeAllMenu();
                            }
                        }

                        $scope.selectThird = function(third){
                            if(third.children){
                                $scope.fourthGroupMenu = third.children;
                                $scope.showFourthGroup = true;
                                if(third.resourceurl!==';'){
                                    $scope.formData.navigations = third.resourceurl
                                }
                            }else{
                                $scope.formData.navigations = third.resourceurl.substring(third.resourceurl.lastIndexOf(';')+1);
                                $scope.formData.displayNav = third.resourcename + '(' + $scope.formData.navigations + ')';
                                $scope.closeAllMenu();
                            }
                        }

                        $scope.selectFourth = function(fourth){
                            $scope.formData.navigations = fourth.resourceurl.substring(fourth.resourceurl.lastIndexOf(';')+1);
                            $scope.formData.displayNav = fourth.resourcename + '(' + $scope.formData.navigations + ')';
                            $scope.closeAllMenu();
                        };

                        $scope.closeAllMenu = function(){
                            $scope.showFirstGroup =false;
                            $scope.showThirdGroup = false;
                            $scope.showFourthGroup = false;
                        };

                        $scope.clearNav = function(){
                            $scope.formData.navigations = '';
                            $scope.formData.displayNav = '';
                            $scope.closeAllMenu();
                        };

                        $('#addNewUpgradeDialog').on('show.bs.modal',function(e){
                            $scope.uploadFileName = '';
                            $timeout(function(){
                                initForm();
                            });
                        });
                    }
                };
            }]);
}
});