define([
    'angular',
    '../errorCodeDialog/errorCodeDialogService',
    'scmsModules/tooltip/tooltip',
    '../errorCodeDialog/errorCodeDialog.html',
    '../errorCodeDialog/errorCodeDialog.css'
    ], function(
        angular,
        errorCodeDialogService,
        tooltip,
        html) {
        return function(app, elem, attrs, scope) {
            errorCodeDialogService(app, elem, attrs, scope);
            tooltip(app, elem, attrs, scope);
            app.directive('errorCodeDialog', ['errorCodeDialogService','G', '$q', '$timeout', function(service,G,$q,$timeout) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        currentItem: '='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude,$timeout, $log, $http, G) {

                        $scope.submit = function(){
                            var param = {
                                sysname: $scope.formData.sysname,
                                syscode: $scope.formData.syscode,
                                errorcode: $scope.formData.errorcode,
                                errormessage: $scope.formData.errormessage,
                                errordes: $scope.formData.errordes,
                                username: G.userInfo.username,
                                jobcard: G.userInfo.jobcard
                            }

                            if($scope.isModify){
                                param.errorconfigid = $scope.currentItem.errorconfigid;
                                service.modifyErrorConfigById(param).then(function(data){
                                    if(data&&data.data&&data.data.result==='success'){
                                        G.alert('配置修改成功');
                                        $('#errorCodeDialog').modal('hide');
                                        $scope.$emit('refreshList');
                                    }else{
                                        G.errorAlert('配置修改失败',data,$element.find('.modal-content'));
                                    }
                                },function(data){
                                    G.errorAlert('配置修改失败',data,$element.find('.modal-content'));
                                });
                            }else{
                                service.addErrorConfig(param).then(function(data){
                                    if(data&&data.data&&data.data.result==='success'){
                                        G.alert('新增配置成功');
                                        $('#errorCodeDialog').modal('hide');
                                        $scope.$emit('refreshList');
                                    }else{
                                        G.errorAlert('新增配置失败',data,$element.find('.modal-content'));
                                    }
                                },function(data){
                                    G.errorAlert('新增配置失败',data,$element.find('.modal-content'));
                                });
                            }
                        };

                        function init(){
                            $timeout(function(){
                                $scope.isModify = $scope.currentItem?true:false;
                                if($scope.isModify){
                                    $scope.formData = G.clone($scope.currentItem);
                                }else{
                                    $scope.formData = {};
                                }
                                G.clearAllError();
                            });
                        }

                        $('#errorCodeDialog').on('show.bs.modal', function(){
                            init();
                        })
                    }
                };
            }]);
    }
});