define([
    'angular'
    ], function(angular) {
        return function(app, elem, attrs, scope) {
            app.factory('staffDataPermissionService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
                return {   
                    selectRbacOrganizationByParam: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacorganizationcs/selectRbacOrganizationByParam',
                            method: 'post',
                            data: opts
                        });
                    },

                    updateRbacUser: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacuserscs/updateRbacUser',
                            method: 'post',
                            data: opts
                        });
                    }
                };  
            }]);
        }
    });