define([
    'angular'
    ], function(
        angular
        ) {
        return function(app, elem, attrs, scope) {
            app.controller('basicPositionMgmtCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'basicPositionMgmtService', 'navsData', 
                function($rootScope, $scope, $state, $stateParams, $timeout, G, service, navsData) {
                 var searchLock = false;
                 $scope.introduction = '● 岗位是指各组织内部的岗位设置，总部，各城市可以根据自己管理需要，设置不同岗位，岗位名称可自定义。总部人员不涉及具体业务操作，可默认固定岗位<br>● 支持新增，修改，查看，删除。';
                 $scope.topPos='right';
                 $scope.positionList = {};
                 $scope.pageSize = G.getPageSize();
                 
                 $scope.remove = function(item, $event){
                    var $alert = G.confirm({
                        $target: $($event.target),
                        msg: '确认删除？',
                        position: 'bottom',
                        submit: function() {
                            service.updatePosition({
                                ehdrbacpostid: item.ehdrbacpostid,
                                poststatus: 9
                            }).then(function(data) {
                                data = data.data;
                                $alert.hide();
                                if(data.result === 'success') {
                                    G.alert('删除成功!');                                
                                    getPositionList($scope.currPage);
                                }else {
                                    G.alert(data.msg || '操作失败');
                                }                    
                            }, function(data) {
                                G.alert(data.msg);
                            });  
                        }
                    });
                };

                function init(){
                    $scope.permission = navsData.permission;
                    getPositionList();
                }

                $scope.search = function(){
                    $scope.searchFlag = true;
                    $scope.searchResult=false;
                    $scope.keywordsCopy=G.clone($scope.keywords);
                    getPositionList(1);
                    searchLock = true;
                };
                $scope.returnPre = function() {
                    $scope.keywords = '';
                    $scope.searchResult = false;
                    getPositionList(1);
                }



                function getPositionList(currPage){
                    $scope.currPage = currPage || 1;
                    $scope.searchResult=false;
                    $scope.showAddGuide = false;
                    G.loading(true);
                    service.getPositionList({
                        postnamelike: $scope.keywords,
                        skipCount: ($scope.currPage - 1) * $scope.pageSize,
                        pageSize: $scope.pageSize,
                        iscontainrole:1
                    }).then(function(data){
                        searchLock = false;
                        if(data&&data.data&&data.data.result==='success'){
                            if($scope.searchFlag){
                                if(!data.data.data||!data.data.data.length){
                                    $scope.searchResult=true;
                                }
                            }
                            $scope.positionList.data = data.data.data;
                            $scope.positionList.totalCount = data.data.count;
                            if($scope.positionList.totalCount === 0 && $scope.keywords===''){
                                $scope.showAddGuide = true;
                            }
                        }else{
                            G.errorAlert('查询失败', data);
                        }
                        G.loading(false);
                    },function(data){
                        G.loading(false);
                        searchLock = false;
                        G.errorAlert('查询失败', data);
                    });
                }

                $scope.paginationChanged = function(){
                    getPositionList($scope.currPage);
                };

                $scope.$on('addPositionSuccess', function(){
                    getPositionList($scope.currPage);
                });

                $scope.toggle = function(position){
                    position.caretToggle = !position.caretToggle;
                };

                $scope.ready = function(last, position){
                    if(last){
                        $timeout(function(){
                            var height,
                            index = -1;
                            angular.forEach($scope.positionList.data, function(pos){
                                height = angular.element('.role-container')[++index].clientHeight;
                                if(height>51){
                                    pos.hasMore = true;
                                    pos.caretToggle = true;
                                }
                            });
                        },0);
                    }
                };

                $scope.selectposition=function(item){
                    $scope.currItem=item;
                    $scope.opType='edit';
                }


                init();

            }]);

}
});