define([
    'angular',
    '../addNewPositionDialog/addNewPositionDialogService',
    '../directiveFormInput/bcFormSelectMulti',
    '../addNewPositionDialog/addNewPositionDialog.html'
    ], function(
        angular,
        addNewPositionDialogService,
        bcFormSelectMulti,
        html) {
        return function(app, elem, attrs, scope) {
            addNewPositionDialogService(app, elem, attrs, scope);
            bcFormSelectMulti(app, elem, attrs, scope);
            app.directive('addNewPositionDialogDirective', ['G', 'addNewPositionDialogService', '$timeout',function(G, service, $timeout) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        currItem:'=',
                        opType:'='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude,$timeout, $log, $http, G) {

                        $scope.submit = function (){
                            var param = {
                                postname: $scope.positionName,
                                ehdrbacroleids:getRoleStr($scope.roles),
                                description: $scope.description
                            };
                            if($scope.opType==='edit'){
                                param.ehdrbacpostid=$scope.currItem.ehdrbacpostid;
                                updatePosition(param);
                            }else{
                                addPosition(param);
                            }
                        };

                        $scope.onItemClick = function(item){
                            $scope.positionName = item.text;
                        };

                        function addPosition(param){
                            service.addPosition(param).then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    G.alert('添加成功');
                                    $element.modal('hide');
                                    $scope.$emit('addPositionSuccess');
                                }else{
                                    G.errorAlert('添加失败', data);
                                }
                            },function(data){
                                G.errorAlert('添加失败', data);
                            });
                        }

                        function updatePosition(param){
                            service.updatePosition(param).then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    G.alert('更新成功');
                                    $element.modal('hide');
                                    $scope.$emit('addPositionSuccess');
                                }else{
                                    G.errorAlert('更新失败', data);
                                }
                            },function(data){
                                G.errorAlert('更新失败', data);
                            });
                        }

                        function formateList(list){
                            var distList = [];
                            angular.forEach(list, function(item){
                                distList.push({
                                    text: item.dictionaryname
                                });
                            });
                            return distList;
                        }


                        function getRoleStr(arr){
                            var ret='';
                            angular.forEach(arr, function(item){
                                ret=ret+','+item.ehdrbacroleid;
                            })

                            return ret&&ret.substr(1,ret.len);
                        }

                        function getDemoPosName(){
                            service.getDemoPosName({
                                parentdictionarycode: 'position'
                            }).then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    $scope.demoPosName = formateList(data.data.data);
                                }
                            });
                        }

                        function getRole(){
                            service.getAllRole().then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    $scope.roleList = data.data.data;
                                }                                
                            })
                        }

                        function initForm(){
                            $scope.positionName='';
                            $scope.roles=[];
                            $scope.description='';
                            G.clearAllError();
                        }

                        function initEdit() {
                            $scope.positionName=$scope.currItem.postname;
                            $scope.roles = matchRoles($scope.currItem.roleids);
                            $scope.description=$scope.currItem.description;
                        }

                        function matchRoles(ids){
                            if(!ids||typeof ids !=='string'){
                                return ;
                            }
                            var ret=[],
                            arr=ids.split(',');
                            angular.forEach(arr, function(item){
                                angular.forEach($scope.roleList, function(role){
                                    if(item==role.ehdrbacroleid){
                                        ret.push(role);
                                    }
                                })
                            })
                            return ret;
                        }

                        $('#addNewPositionDialog').on('show.bs.modal',function(e){
                            initForm();
                            $timeout(function(){
                              if($scope.opType==='edit'){
                                initEdit();
                                }   
                            },60)
                        });

                        $('#addNewPositionDialog').on('show.bs.modal',function(e){
                          
                        });

                        getRole();
                        getDemoPosName();

                    }
                };
            }]);
}
});