define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('systemDataDirService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                selectPubDictionaryListByParam:function(opts){
                    return $http({
                        url: '/ehuodiBedrockApi/ehdpubdictionarycs/selectPubDictionaryListByParam',
                        method: 'post',
                        data:opts,
                        test: 'basicConfigModules/systemDataDir/systemDataDir.json'//假数据api
                    });                    
                },
                updatePubDictionary:function(opts){
                    return $http({
                        url: '/ehuodiBedrockApi/ehdpubdictionarycs/updatePubDictionary',
                        method: 'post',
                        data:opts,
                        test: 'basicConfigModules/systemDataDir/updatePubDictionary.json'//假数据api
                    });                    
                }
            };
        }]);
    }
});