define([
    'angular'
    ], function(
        angular
        ) {
        return function(app, elem, attrs, scope) {
            app.controller('systemUpgradeMgmtCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'systemUpgradeMgmtService', 'navsData', 
                function($rootScope, $scope, $state, $stateParams, $timeout, G, service, navsData) {

                function init(){
                    $scope.apiUrl = service.queryVersionManageListUrl;
                    $scope.permission = navsData.permission;
                    $scope.searchParam = {
                        orderbyitem: 'updateDate DESC',
                        status: '1'
                    };
                    if($stateParams.program){
                        $scope.searchParam.keywords = $stateParams.program;
                    }
                    getUpgradeModules();
                }

                function getUpgradeModules(){
                    $scope.menuList = G.clone(navsData.data);
                    $scope.menuList.unshift({resourcename: '全部'});
                    $scope.selectMenu = $scope.menuList[0];
                }

                $scope.changeMenu = function(){
                    $scope.searchParam.systemname = $scope.selectMenu.resourcename==='全部'?'':$scope.selectMenu.resourcename;
                };

                $scope.showAddMenu = function(item){
                    $scope.currItem = item;
                    $('#addNewUpgradeDialog').modal({
                        backdrop: 'static'
                    });
                };

                $scope.changeOrder = function(){
                    if($scope.searchParam.orderbyitem==='updateDate DESC') {
                        $scope.setTime('updateDate ASC');
                    }else{
                        $scope.setTime('updateDate DESC');
                    }
                };
    
                $scope.setTime = function(order) {
                    if($scope.searchParam.orderbyitem!==order){
                        $scope.searchParam.orderbyitem = order;
                        $scope.fetch();
                    }
                };

                $scope.setCurrent = function(item){
                    $scope.currItem = item;
                }

                $scope.formatData = function(items){
                    angular.forEach(items, function(item){
                        item.description = item.description.replace(/\n/g, '<br>')
                    });
                };

                $scope.deleteVersion = function(item, $event){
                    var $alert = G.confirm({
                        $target: $($event.target),
                        msg: '撤下将删除当条更新记录，同时更新提醒将不展示，是否确认操作？',
                        position: 'bottom',
                        submit: function() {
                            service.modifyVersionManage({
                                versionmanageid: item.versionmanageid,
                                status: '9'
                            }).then(function(data) {
                                $alert.hide();
                                if(data.data.result === 'success') {
                                    G.alert('成功撤下!');
                                    $scope.fetch();
                                }else {
                                    G.errorAlert('操作失败',data);
                                }
                            }, function(data) {
                                G.errorAlert('操作失败',data);
                            });  
                        }
                    });
                };

                $scope.$on('refreshVersionList', function(){
                    $scope.fetch({currPage:1});
                });

                init();

            }]);

}
});