define([
    '../addNewDeptDialog/addNewDeptDialog.css',
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('addNewDeptDialogService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                getDemoDeptName: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdpubdictionarycs/selectPubDictionaryListByParam',
                        method: 'post',
                        data: options,
                        test: 'basicConfigModules/basicAreaMgmt/areaType.json'//假数据api
                    });
                },

                addDept: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacdepartmentcs/insertRbacDepartment',
                        method: 'post',
                        data: options,
                        noconcurrent: true,
                        test: 'basicConfigModules/header/success.json'//假数据api
                    });
                },
                updateDept: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacdepartmentcs/updateRbacDepartment',
                        method: 'post',
                        data: options,
                        noconcurrent: true,
                        test: 'basicConfigModules/header/success.json'//假数据api
                    });
                }
            };
        }]);
    }
});