define([
    'angular'
], function(
    angular
) {
    return function(app, elem, attrs, scope) {
        app.controller('basicOrgChartCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'basicOrgChartService', 
            function($rootScope, $scope, $state, $stateParams, $timeout, G, service) {
            $scope.orgTree = {};
            $scope.showTips = false;
            $scope.hoverFlag = false;
            $scope.orgIntro = '● 维护货嘀组织架构，可支持总部->事业部->大区->城市四层架构。如没有事业部和大区，可将城市直接挂到总部下面。<br>● 新增，修改，查看，删除需由系统管理员设置。';
            $scope.topPos='right';
            function init(){
                getOrgChart();
            }

            function getOrgChart(){
                service.getOrgChart({
                    isneedcount: '1'
                }).then(function(data){
                    if(data&&data.data&&data.data.result==='success'){
                        var dataList = data.data.data;
                        addTooltipDetails(dataList);
                        $scope.orgTree = G.toTree(dataList, '0', 'parorganizationcode', 'organizationcode');
                        formatTree($scope.orgTree);
                        $scope.orgTree = new Array($scope.orgTree);
                    }else{
                        G.alert('获取组织列表失败',{type:'error'});
                    }
                },function(){
                    G.alert('获取组织列表失败',{type:'error'});
                });
            }

            function formatTree(tree){
                if(!tree.children||tree.children.length<1){
                    return;
                }
                angular.forEach(tree.children, function(child, index){
                    if(index===0){
                        child.tipPos = 'right';
                    }else{
                        child.tipPos = 'left';
                    }
                    formatTree(child);
                });
            }

            function addTooltipDetails(dataList){
                angular.forEach(dataList, function(item, $index){
                    try{
                        // let countDesc = JSON.parse(item.childcountdesc);
                        item.countDesc = '';
                        if(Array.isArray(item.childcountdesc)&&item.childcountdesc.length>0){
                            angular.forEach(item.childcountdesc, function(count){
                                item.countDesc = item.countDesc + ',' + count.count + '个' + count.organizationtype ;
                            });
                        }
                        item.countDesc = item.countDesc.substring(1);
                    }catch(e){console.error(e)}
                    item.address = item.managerprovince + item.managercity + item.managerregion + item.managerstreet;
                    item.manager = item.managername;
                    if(item.managername && item.managermobilenumber){
                        item.manager = item.manager + '/' + item.managermobilenumber;
                    }
                    item.city = item.city?item.city:'';
                    item.organizationname = item.organizationname?item.organizationname:'';
                    item.organizationmodel = item.organizationmodel?item.organizationmodel:'';
                    item.activatedate = item.activatedate?item.activatedate:'';
                    item.manager = item.manager?item.manager:'';
                    item.address = item.address?item.address:'';
                    item.description = item.description?item.description:'';
                    if(item.organizationtype==='城市'){
                        item.tipDetails = '<div>所在城市：' + item.city + '</div>'
                            + '<div>城市名称：' + item.organizationname + '</div>'
                            + '<div>城市类型：' + item.organizationmodel + '</div>'
                            + '<div>开城时间：' + item.activatedate + '</div>'
                            + '<div>城市经理：' + item.manager + '</div>'
                            + '<div>城市状态：' + item.isactivated + '</div>'
                            + '<div>联系地址：' + item.address + '</div>'
                            + '<div>城市描述：' + item.description + '</div>';
                    }else{
                        item.tipDetails = '<div>组织名称：' + item.organizationname + '</div>'
                            + '<div>组织编码：' + item.organizationcode + '</div>'
                            + '<div>组织负责人：' + item.manager + '</div>'
                            + '<div>联系地址：' + item.address + '</div>'
                    }
                });
            }

            $scope.mouseEnter = function(){
                $scope.hoverFlag = true;
            };

            $scope.mouseLeave = function(){
                $scope.hoverFlag = false;
            };

            init();
            
        }]);

    }
});