/**
 */
define([
    'angular',
    '../imageUpload/imageUpload.css',
    '../imageUpload/imageUpload.html'
], function(
    angular, css, html) {
    return function(app, elem, attrs, scope) {
        app.directive('imageUploadDirective', ['G', '$http','$timeout', function(G, $http, $timeout) {
            return {
                    template: html,
                    require: '?ngModel',
                    scope: {
                        ngModel: '=',
                        dMax: '=',
                        dWidth: '=',
                        dHeight: '=',
                        dSize: '=',
                        dValue: '='
                    },
                    controller: function($scope, $element, $attrs) {
                        $scope.dMax = parseInt($scope.dMax, 10) || 1;
                        $scope.ngModel = $scope.ngModel || [];
                        $scope.urls = $scope.urls || [];
                        $scope.isDownloading = true;
                        var $file = $element.find('input');

                        //上传图片可多选
                        if($scope.dMax > 1) {
                            $file.attr('multiple', 'multiple');
                        }

                        //显示默认的图片
                        $scope.$watch('ngModel', function(newValue, oldValue) {
                            if(newValue && newValue.length) {
                                $scope.urls = [];
                                angular.forEach($scope.ngModel, function(item) {
                                    $scope.urls.push({
                                        filefullpath: item
                                    });
                                });
                                $scope.dValue = newValue.join(',');
                            }
                        });

                        $scope.loadImage = function(){
                            $scope.isDownloading = false;
                        };

                        //删除图片
                        $scope.remove = function($index) {
                            $scope.urls.splice($index, 1);
                            $scope.ngModel.splice($index, 1);
                            $scope.isDownloading = true;
                            $scope.dValue = $scope.ngModel.join(',') ? $scope.ngModel.join(',') : null;
                        }


                        //上传图片报错处理
                        var setError = function(type) {
                            G.alert($attrs.errormsg || '请按要求上传正确的图片', {
                                type: 'error',
                                speed: 5000
                            });
                            $scope.urls.splice($scope.urls.length -1, 1);
                        };

                        //上传图片成功，显示图片
                        var show = function(data) {
                            $timeout(function() {
                                var item = data.data;
                                if(!item) {
                                    setError(1);
                                }
                                // else if($scope.dHeight && item.height !== $scope.dHeight) {
                                //     setError(2);
                                // }
                                else if($scope.dSize && item.size/1024 > parseInt($scope.dSize, 10)) {
                                    setError(3);
                                }
                                else {
                                    $scope.urls[0]=item;
                                    $scope.ngModel = [];
                                    angular.forEach($scope.urls, function(item, index) {
                                        $scope.ngModel.push(item.filefullpath);
                                    });
                                    $scope.dValue = $scope.ngModel.join(',') ? $scope.ngModel.join(',') : null;
                                 }
                                $file.val('');
                            });
                        };


                      function judgeSize(file){
                          var data=new FormData();
                          var reader = new FileReader();
                          reader.readAsDataURL(file);
                          reader.onload = function(theFile) {
                              var image = new Image();
                              image.src = theFile.target.result;
                              image.onload = function() {
                                if($scope.dWidth && this.width !== $scope.dWidth) {
                                    setError(1);
                                }
                                else if($scope.dHeight && this.height !== $scope.dHeight) {
                                    setError(2);
                                }
                                else if($scope.dSize && file.size/1024 > parseInt($scope.dSize, 10)) {
                                    setError(3);
                                }else{
                                  data.append('img', file);
                                  $scope.urls.push({
                                      filefullpath: ''
                                  });
                                  uploadImage(data);
                                }
                              };
                          };
                        }

                        //上传图片动作
                        $file.bind('change', function(event) {
                            var file=event.currentTarget;
                            if(!file.files || (file.files && !file.files.length)) {
                                return;
                            }
                            angular.forEach(file.files, function(item, index) {
                              judgeSize(item);
                            });
                        });
                        function uploadImage(data){
                          $timeout(function() {
                              var xhr=new XMLHttpRequest();
                              xhr.open('post', '/goodstaxiAdmin/discoveryconfigcs/uploadImage ');
                              xhr.onreadystatechange=function(){
                                  if(xhr.readyState==4){
                                      if(xhr.status === 200) {
                                          var result=JSON.parse(xhr.responseText);
                                          show(result);
                                      }
                                      else if(xhr.status === 413) {
                                          $timeout(function() {
                                              setError(3);
                                              $scope.urls.splice($scope.urls.length -1, 1);
                                              $file.val('');
                                          });
                                      }
                                      else {
                                          $timeout(function() {
                                              G.alert('上传图片失败，请重试', {
                                                  type: 'error',
                                                  speed: 5000
                                              });
                                              $scope.urls.splice($scope.urls.length -1, 1);
                                              $file.val('');
                                          });
                                      }

                                  }
                              };
                              xhr.send(data);
                          }, 0)
                        }

                    },
                    link: function($scope, $element, $attrs, ngModel) {

                    }
                }
            }]);
        app.directive('imageonload', function () {
                return {
                    restrict: 'A', link: function (scope, element, attrs) {
                        element.bind('load', function () {
                            scope.$apply(attrs.imageonload);
                        });
                    }
                };
            })
        };
});
