import './index.less';
export default (app, elem, attrs, scope) => {
  app.controller(
    'activityEditCtrl',
    ['$scope', '$state','$stateParams', '$timeout','annualPartyService','G', ($scope, $state,$stateParams,$timeout,service,G) => {

      $scope.getLevel = lv => {
        const levelMap = {
          0: '简单',
          1: '中等',
          2: '复杂'
        };
        return levelMap[lv];
      };

      $scope.cancel = ()=>{
        $state.go('annualParty.activityList');
      };

      const {gameActivityId,activityName,activityAmount}=$stateParams;
      $scope.items = [];
      $scope.activityName = activityName||'';
      $scope.activityAmount = activityAmount||'';

      gameActivityId&&$('#crumbs .active span span').html('修改活动');
      gameActivityId&&fetchQuestions(gameActivityId);

      $scope.delete = (event,index)=>{
        var $alert = G.confirm({
          $target: $(event.target),
          msg: '确认移除该题目吗？',
          position: 'bottom',
          submit: function() {
            $timeout(()=>{
              $scope.items.splice(index,1);
              $alert.hide();
            });
          }
        });
      };

      function stringify(json, defaultValue) {
        if (json === null || json === undefined) {
          return defaultValue;
        }
        try {
          return JSON.stringify(json, function(key, value) {
            if (key === '$$hashKey') {
              return undefined;
            }

            return value;
          });
        } catch (e) {
          return defaultValue;
        }
      }

      $scope.$watch('items',(value,oldVale)=>{
        if(value.length){
          $scope.errorMsg = '';
        }
      },true);
      $scope.isFetching = false;
      $scope.submit = ()=>{
        const {
          activityName,
          activityAmount,
          items
        } = $scope;

        if(!items.length){
          $scope.errorMsg = '请添加试题';
          return;
        }

        if(items.length&&!$scope.isFetching){
          const method = gameActivityId?'update':'add';
          G.loading(true);
          $scope.isFetching = true;
          service.activity[method]({
            activityName,
            activityAmount,
            gameQuestionList:stringify(items),
            gameActivityId
          }).then(
            data=> {
              G.loading(false);
              $scope.isFetching = false;
              data = data.data;
              if (data.result == 'success') {
                G.alert('保存成功！');
                $state.go('annualParty.activityList');
              } else {
                G.alert(data.msg || '保存失败',{
                  type:'error'
                });
              }
            },
            data=> {
              data = data.data;
              G.loading(false);
              $scope.isFetching = false;
              G.alert(data.msg || '保存失败',{
                type:'error'
              });
            }
          );
        }
      };

      function fetchQuestions(id){
        // TODO更换接口
        service.activity.getQuestion({
          gameActivityId:id
        }).then(res=>{
          res = res.data;
          if(res.result=='success'){
            $scope.items = res.data;
          }else{
            $scope.items=[];
            G.alert(res.msg || '获取试题失败', { type: 'error' });
          }
        },res=>{
          G.alert(res.data.msg || '获取试题失败', { type: 'error' });
        });
      }
    }]
  );
};