define([
    'angular',
    'scmsModules/tooltip/tooltip',
    '../treeView/treeView.html',
    '../treeView/treeView.css'
    ], function(
        angular,
        tooltip,
        html) {
        return function(app, elem, attrs, scope) {
            tooltip(app, elem, attrs, scope);
            app.directive('treeView', ['$timeout', 
                function($timeout) {
                    return {
                        template: html,
                        restrict: 'EA',
                        replace: true,
                        scope: {
                            treeData: '=',
                            textField: '@',
                            linkKey: '@',
                            itemClicked: '&',
                            defaultItem:'=',
                            updateData:'=',
                            childrenKey:'@',
                            clickActiveDisable:'=',
                            tips: '@'
                        },
                        link: function postLink() {

                        },

                        controller: function($scope,$element,$attrs,$transclude,$log,$http,G){

                            $scope.itemExpended = function(item, $event){
                                item.$$isExpend = !item.$$isExpend;
                                $event.stopPropagation();
                            };

                            $scope.getItemIcon = function(item){
                                var isLeaf = $scope.isLeaf(item);
                                if(isLeaf){
                                    return '';
                                }
                                return item.$$isExpend ? 'fa fa-caret-up': 'fa fa-caret-down';   
                            };

                            $scope.isLeaf = function(item){
                                return !item[$scope.childrenKey||'children'] || !item[$scope.childrenKey||'children'].length; 
                            };

                            function expandBaner(tree,value,field,addfield,addValue) {
                                if(tree[field]===value){
                                    tree[addfield]=addValue;
                                    tree.active = true;
                                }
                                if(tree[$scope.childrenKey||'children'] && tree[$scope.childrenKey||'children'].length>0){
                                    for(var i=0,len=tree[$scope.childrenKey||'children'].length;i<len;i++){
                                        expandBaner(tree[$scope.childrenKey||'children'][i],value,field,addfield,addValue);
                                        if(tree[$scope.childrenKey||'children'][i][addfield]===addValue){
                                            tree[addfield]=addValue;
                                        }
                                    }
                                }
                            }

                            $timeout(function(){
                                addActive();
                            },60);

                            $scope.$watch('defaultItem',function(){
                                $timeout(function(){
                                    addActive();
                                },60);
                            })

                            $scope.$watch('updateData',function(){
                                $timeout(function(){
                                    addActive();
                                },60);
                            })

                            function addActive() {
                                if($scope.defaultItem){
                                    angular.forEach($scope.treeData, function(item,index){
                                        expandBaner(item,$scope.defaultItem[$scope.linkKey],$scope.linkKey,'$$isExpend',true);
                                    })
                                }else{
                                    if(!$scope.clickActiveDisable){
                                        angular.element('.tree-view span:eq(0)').addClass('active');
                                    }
                                }
                            }

                            $scope.warpCallback = function(callback, item, $event){
                                if(!$scope.clickActiveDisable){
                                    angular.element('.tree-view span').removeClass('active');
                                    angular.element($event.target).addClass('active');
                                }

                                ($scope[callback] || angular.noop)({
                                    $item:item,
                                    $event:$event
                                })};
                            }
                        };
                    }]);
};
});