define([
  'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('commonService', ['$http', '$rootScope', 'G', '$q', '$timeout', 'routerData', '$cookies', function($http,$rootScope,G,$q, $timeout, routerData, $cookies) {
            var service={
                userInfo: '',
                cityList: []
            };

            service.urls = {
                crm: {
                    'location': 'http://127.0.0.1:8100/',
                    'test': '/crm/',
                    'product': '/crm/'
                },
                woodpeckerWeb: {
                    'location': 'http://127.0.0.1:8200/',
                    'test': '/woodpeckerWeb/',
                    'product': '/woodpeckerWeb/'
                },
                center: {
                    'location': 'http://127.0.0.1:8300/',
                    'test': '/center/',
                    'product': '/center/'
                },
                goodstaxiAdmin: {
                    'location':'http://127.0.0.1:8500/motion/',
                    'test': '/orderMotionWeb/motion/',
                    'product': '/orderMotionWeb/motion/'
                },
                marketWeb: {
                    'location': 'http://127.0.0.1:8400/',
                    'test': '/marketWeb/',
                    'product': '/marketWeb/'
                },
                driverMonitorWeb: {
                    'location': 'http://127.0.0.1:8600/',
                    'test': '/qos/',
                    'product': '/qos/'
                },
                appPublishWeb: {
                    'location': 'http://127.0.0.1:8700/',
                    'test': '/appPublishWeb/',
                    'product': '/appPublishWeb/'
                },
                driverInsurance: {
                    'location': 'http://127.0.0.1:8800/',
                    'test': '/carInsurance/',
                    'product': '/carInsurance/'
                },
                basic: {
                    'location': 'http://127.0.0.1:8900/',
                    'test': '/basicConfigWeb/',
                    'product': '/basicConfigWeb/'
                }
            };
            service.pageList = [
                {
                    state: 'basic',
                    title: '基础管理',
                    className: 'basic',
                    description: '管理货嘀组织架构，维护城市，市场，部门，岗位，人员信息，维护用户角色权限和系统菜单。'
                },
                {
                    state: 'crm',
                    title: '客户管理',
                    className: 'crm',
                    description: '货嘀客户管理中心，内部管理工具，协助员工和管理人员管理货主，运营货主。'
                },
                {
                    state: 'goodstaxiAdmin',
                    title: '业务管理',
                    className: 'admin',
                    description: '货嘀业务管理后台，支持人工订单调度，订单监测，订单管理，货源管理。'
                },
                {
                    state: 'woodpeckerWeb',
                    title: '质控管理',
                    className: 'wood',
                    description: '货嘀风控管理，反刷单，反作弊，作弊订单管理，作弊账户管理，判断条件配置。'
                },
                {
                    state: 'marketWeb',
                    title: '营销管理系统',
                    className: 'market',
                    description: '货嘀营销配置管理，资金池管理，营销活动配置，黑名单配置。'
                },
                {
                    state: 'driverMonitorWeb',
                    title: 'QOS系统',
                    className: 'qos',
                    description: '货嘀服务质量管理，基于司机的所有服务质量管理功能，包括线上培训，金牌司机，服务得分等。'
                },
                {
                    state: 'appPublishWeb',
                    title: '灰度发布',
                    className: 'app',
                    description: '支持货嘀产品发布平滑过渡的发布方式，实现发布的功能在可控制的范围内用户使用。'
                },
                {
                    state: 'driverInsurance',
                    title: '车险系统',
                    className: 'driverInsurance',
                    description: '货嘀车险报价管理后台，查看询价清单明细，报价管理。'
                }
            ];
            (function(){
                var permissionsName = {
                    '啄木鸟': 'woodpeckerWeb',
                    '燎原平台': 'crm',
                    '调度中心': 'goodstaxiAdmin',
                    '订单管理1': 'goodstaxiAdmin',
                    '数据监测': 'goodstaxiAdmin',
                    '基础配置1': 'goodstaxiAdmin',
                    '货源管理': 'goodstaxiAdmin',
                    '用户管理': 'goodstaxiAdmin',
                    '营销活动': 'goodstaxiAdmin',
                    '大客户': 'crm',
                    '营销管理': 'marketWeb',
                    'QOS': 'driverMonitorWeb',
                    '灰度发布': 'appPublishWeb',
                    '车险系统': 'driverInsurance'
                };
                function setPermissions(name){
                    angular.forEach(service.pageList, function(page, index) {
                        if(page.state===name){
                            page.permission = 1;
                        }
                    });
                } 
                $http({
                    url: '/ehuodiCrmApi/logincs/getPermission',
                    method: 'post',
                    test: '/modules/header/permissions.json'//假数据api
                }).then(function(data){
                    data = data.data.data;
                    angular.forEach(data, function(item, index) {
                        if(permissionsName[item.funcname] && item.retList && item.retList.length) {
                            setPermissions(permissionsName[item.funcname]);
                        }
                    });
                    service.pageList[0].permission = 1;
                });
            })();

            service.setPermission = function(permission){
                var permissionResult = [];
                if(!permission){
                    permission = service.nextStatePermission;
                }
                if(permission){
                    angular.forEach(permission.children, function(url){
                        permissionResult[url.resourceurl] = true;
                    });
                }
                return permissionResult;
            }

            service.getUserId = function(options) {
                var deferred = $q.defer(),
                    promise = deferred.promise;
                $http({
                    url: '/ehuodiBedrockApi/ehdrbacuserscs/selectRbacUserByParam',
                    method: 'post',
                    data: options,
                    test: '/modules/header/user.json'//假数据api
                }).then(function(data){
                    if(data&&data.data&&data.data.result==='success'){
                        if(data.data.data.length<1){
                            deferred.resolve();
                        }else{
                            service.userInfo = data.data.data[0];
                            deferred.resolve(service.userInfo);
                        }
                    }
                });
                return promise;
            };

            service.getResource = function(options){
                var deferred = $q.defer(),
                    promise = deferred.promise;
                $http({
                    url: '/ehuodiBedrockApi/ehdrbacuserscs/selectRbacUserResourceList',
                    method: 'post',
                    data: options,
                    test: 'modules/systemMenuMgmt/systemMenuMgmt.json'//假数据api
                }).then(function(data){
                    if(data&&data.data&&data.data.result==='success'){
                        try{
                            deferred.resolve(data.data.data);
                        }catch(e){
                            deferred.reject();
                            G.alert('获取权限信息失败',{type: 'error'});
                        }
                    }else{
                        deferred.reject();
                        G.alert('获取权限信息失败',{type: 'error'});
                    }
                });
                return promise;
            };

            return service;
        }]);
  }
});