define([
    '../basicAreaMgmt/basicAreaMgmtCtrl',
    '../basicAreaMgmt/basicAreaMgmtService',
    'scmsModules/pagination/paginationDirective',
    '../addNewAreaDialog/addNewAreaDialogDirective',
    'scmsModules/tooltip/tooltip',
    '../basicAreaMgmt/basicAreaMgmt.html',
    '../basicAreaMgmt/basicAreaMgmt.css',
], function(
    ctrl,
    service,
    paginationDirective,
    addNewAreaDialogDirective,
    tooltip,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        paginationDirective(app, elem, attrs, scope);
        addNewAreaDialogDirective(app, elem, attrs, scope);
        tooltip(app, elem, attrs, scope);
        elem.append(html);
    }
});