define([
    'angular'
    ], function(
        angular
        ) {
        return function(app, elem, attrs, scope) {
            app.controller('tfLySysDataDirCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'tfLySysDataDirService', 'navsData', 
                function($rootScope, $scope, $state, $stateParams, $timeout, G, service, navsData) {
                
                function init(defaultItem){
                    $scope.permission = navsData.permission;
                    G.loading(true);
                    service.selectPubDictionaryListByParam({
                        returnformat:1
                    }).then(function(data) {
                        $scope.lists = data.data.data;
                        if($scope.lists.length===0){
                            $scope.dataDirs=[];
                            $scope.showGuide=true;
                            return;
                        }
                        // $scope.dataDirs=G.toTree($scope.lists,'TOP','parentdictionarycode','dictionarycode');
                        $scope.dataDirs = data.data.data;
                        if(!$scope.dataDirs[0]){
                            $scope.dataDirs = [$scope.dataDirs];
                        }
                        var item;
                        if(defaultItem){
                            $scope.defaultItem = G.clone(defaultItem);
                            for(var i=0,len=$scope.dataDirs.length;i<len;i++){
                                if(G.searchTree($scope.dataDirs[i],defaultItem.dictionaryname,'dictionaryname')){
                                    item=G.searchTree($scope.dataDirs[i],defaultItem.dictionaryname,'dictionaryname');
                                    break;
                                }
                            }
                        }else{
                            $scope.defaultItem = G.clone($scope.dataDirs[0]);
                        }
                        var updateData=item|| $scope.dataDirs[0];
                        $scope.updateRight(updateData);
                        G.loading(false);
                    }).catch(()=>{
                        G.loading(false);
                    });
                }    

                function removeTreeActive(){
                    angular.element('.tree-view span').removeClass('active');
                }

                $scope.updateRight=function($item){
                    $scope.detail=$item;
                    $scope.subStruc=$item.subutmsdictionary;
                    $scope.searchResult=false;
                    $scope.searchList=[];
                }

                $scope.add=function(flag){
                    $scope.opType='add';
                    if(flag){
                        $scope.currItem={dictionaryname:'TOP',dictionarycode:'TOP'};
                    }else{
                        $scope.currItem=$scope.detail;
                    }
                    $('#tfLyAddNewDataDir').modal({
                        backdrop:'static'
                    });
                }

                $scope.editDetail=function(item){
                    $scope.currItem=item;
                    $scope.opType='edit';
                    $('#tfLyAddNewDataDir').modal({
                        backdrop:'static'
                    });
                }

                $scope.$on('refresh',function(){
                    if($scope.currItem.utmsdictionaryid===$scope.detail.utmsdictionaryid){
                        $scope.detail = $scope.currItem;
                    }else{
                        angular.forEach($scope.subStruc, function(item,index){
                            if(item.utmsdictionaryid===$scope.currItem.utmsdictionaryid) {
                                $scope.subStruc[index]=$scope.currItem;
                            }
                        });
                    }

                })

                $scope.$on('add',function(){
                    init($scope.detail);
                })

                $scope.search=function(){
                    removeTreeActive();
                    $scope.searchResult=false;
                    $scope.searchList = [];
                    $scope.keywordsCopy=G.clone($scope.keywords);
                    $scope.searchList = treeSearch($scope.lists);
                    angular.forEach($scope.dataDirs,function(datadir){
                        closeAllTree(datadir);
                    })  
                    $scope.detail ='';
                    $scope.subStruc=[];                       
                    $scope.searchResult = true;
                }

                $scope.returnPre = function() {
                    $scope.searchResult = false;
                    init();
                }

                $scope.searchDetail=function(item){
                    removeTreeActive();    
                    $scope.updateRight(item);
                    $scope.defaultItem=G.clone(item);
                }

                $scope.moveUp=function($index,first){
                    if(first){
                        return;
                    }
                    updateOrder($index-1,$index);
                }

                $scope.moveDown=function($index,last){
                    if(last){
                        return;
                    }
                    updateOrder($index,$index+1);
                }


                function changeOrder(firstIndex,secondIndex){
                    var changeObj=$scope.subStruc[firstIndex];
                    $scope.subStruc[firstIndex] = $scope.subStruc[secondIndex];
                    $scope.subStruc[secondIndex]=changeObj;

                    var changeOrdernumber=$scope.subStruc[firstIndex].ordernumber;
                    $scope.subStruc[firstIndex].ordernumber = $scope.subStruc[secondIndex].ordernumber;
                    $scope.subStruc[secondIndex].ordernumber=changeOrdernumber;
                }

                function closeAllTree(tree){
                    if(tree.$$isExpend){
                        tree.$$isExpend=false;
                    }
                    if(tree.subutmsdictionary && tree.subutmsdictionary.length>0){
                        for(var i=0,len=tree.subutmsdictionary.length;i<len;i++){
                            closeAllTree(tree.subutmsdictionary[i]);
                        } 
                    }
                }
                function treeSearch(list,arr){
                    arr = list.reduce((total,item)=>{
                        if(item.dictionaryname.indexOf($scope.keywords)>-1){
                            total.push(item);
                        }
                        if(item.subutmsdictionary && item.subutmsdictionary.length>0){
                            total = treeSearch(item.subutmsdictionary,total);
                        }
                        return total;
                    },arr||[]);
                    return arr;
                }
                function updateOrder(firstIndex,secondIndex,flag){
                    service.updateUtmsDictionary({
                        utmsdictionaryid:$scope.subStruc[firstIndex].utmsdictionaryid,
                        ordernumber:$scope.subStruc[secondIndex].ordernumber
                    }).then(function(data){
                        if(data.data.result==='success'){
                            if(!flag){
                                updateOrder(secondIndex,firstIndex,1);
                            }else{
                                changeOrder(secondIndex,firstIndex);
                            }
                        }else{
                            G.alert('排序码移动失败',{type:'error'})
                        }                       
                    },function(data){
                        G.alert(data.data.msg,{type:'error'});
                    })
                }

                $scope.remove=function($event,item,$index) {
                    var $alert = G.confirm({
                        $target: $($event.target),
                        msg: '您确定删除该数据字典？',
                        position: 'bottom',
                        submit: function() {    
                            service.deleteUtmsDictionary({
                                utmsdictionaryid:item,
                            }).then(function(data) {
                                data = data.data;
                                if(data.result === 'success') {
                                    $alert.hide();
                                    init();
                                    G.alert('数据字典删除成功');
                                }
                                else {
                                    G.alert(data.msg || '组织架构删除失败', {
                                        type: 'error'
                                    });
                                    $alert.hide();
                                }
                            },
                            function() {
                                G.alert('数据字典删除失败', {
                                    type: 'error'
                                });
                                $alert.hide();
                            });
                        }
                    });
                }               
                $scope.removeFather=function($event,item,$index) {
                    var $alert = G.confirm({
                        $target: $($event.target),
                        msg: '您确定删除该数据字典？',
                        position: 'bottom',
                        submit: function() {    
                            service.deleteUtmsDictionary({
                                utmsdictionaryid:item,
                            }).then(function(data) {
                                data = data.data;
                                if(data.result === 'success') {
                                    $alert.hide();
                                    init(); 
                                    G.alert('数据字典删除成功');
                                }else {
                                    G.alert(data.msg || '组织架构删除失败', {
                                        type: 'error'
                                    });
                                    $alert.hide();
                                }
                            },
                            function() {
                                G.alert('数据字典删除失败', {
                                    type: 'error'
                                });
                                $alert.hide();
                            });
                        }
                    });
                }

                init();

            }]);

}
});