define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('basicDeptMgmtService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                getAllDept: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacdepartmentcs/selectRbacDepartmentByParam',
                        method: 'post',
                        data:opts,
                        test: 'basicConfigModules/basicDeptMgmt/basicDeptMgmt.json'//假数据api
                    });
                },

                getStaffList: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacuserscs/selectRbacUserByParam',
                        data: opts,
                        method: 'post',
                        test: 'basicConfigModules/basicStaffMgmt/basicStaffMgmt.json'//假数据api
                    });
                },

                updateDept: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacdepartmentcs/updateRbacDepartment',
                        data: opts,
                        method: 'post',
                        test: 'basicConfigModules/header/success.json'//假数据api
                    });
                },

                updateStaff: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacuserscs/removupdateeODMPByRbacUser',
                        data: opts,
                        method: 'post',
                        test: 'basicConfigModules/header/success.json'//假数据api
                    });
                }
            };
        }]);
    }
});