define([
  'angular'
], function (angular) {
  return function (app, elem, attrs, scope) {
    app.factory('basicPreloadService', ['$http', function ($http) {
      return {
        listPreload: '/goodstaxiAdmin/preloadurlcs/selectPreLoadUrlByContion',
        deletePreload: function (data) {
          return $http({
            url: '/goodstaxiAdmin/preloadurlcs/deletePreLoadUrl',
            method: 'post',
            data: data
          });
        },
        detailPreload:function(data){
          return $http({
            url: '/goodstaxiAdmin/preloadurlcs/selectPreLoadUrlByUrlKey',
            method: 'post',
            data: data
          });
        }
      }
    }])
  }
});