define([
    '../msgMgmt/msgMgmtCtrl',
    'scmsModules/ngDatePicker/datePickerRange',
    'scmsModules/table/table',
    'scmsModules/tooltip/tooltip',
    '../treeView/treeView',
    '../msgMgmt/sendMsgDialog',
    '../msgMgmt/msgMgmtService',
    '../msgMgmt/msgMgmt.html',
    '../msgMgmt/msgMgmt.less'
], function(
    ctrl,
    datePickerRange,
    table,
    tooltip,
    treeView,
    sendMsgDialog,
    service,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        datePickerRange(app, elem, attrs, scope);
        table(app, elem, attrs, scope);
        tooltip(app, elem, attrs, scope);
        treeView(app, elem, attrs, scope);
        sendMsgDialog(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        elem.append(html);
    }
});