define([
    'angular'
], function(
    angular
) {
    return function(app, elem, attrs, scope) {
        app.controller('lujingBiCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'navsData', function($rootScope, $scope, $state, $stateParams, $timeout, G, navsData) {
            
            function init(){
                G.loading(true);
                $scope.iframeSrc = decodeURIComponent($stateParams.href);
                var ifm= document.getElementById('myiframe');
                ifm.src = $scope.iframeSrc;
                ifm.onload = function(){
                    G.loading(false);
                    ifm.height=document.documentElement.clientHeight;
                }
                findMenu(navsData.currentNavs);
            }
            function findMenu(menu){
                var href = '';
                if(menu.children){
                    angular.forEach(menu.children,function(item){
                        findMenu(item);
                    })
                }else{
                    if(menu.href){
                        href = menu.href.substring(menu.href.indexOf('href=')+5);
                        if($scope.iframeSrc===decodeURIComponent(href)){
                            menu.isChecked = true;
                        }else{
                            menu.isChecked = false;
                        }
                    }
                }
                
            }

            $scope.open = function(){
                window.open($scope.iframeSrc);
            };

            init();
        }]);

    }
});