define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.controller('tfLyAccountMgmtCtrl', ['$scope','$timeout','G','$state','tfLyAccountMgmtService', 'navsData', function($scope,$timeout,G,$state,service, navsData) {
            $scope.fetchParam={};
            $scope.statisticsUtmsClient={};//顶部统计对象
            $scope.apiUrl=service.getApiUrl();
            $scope.add=()=>{
                $state.go('tfLyAddAccount.tfLyAddAccount');
            };
            $scope.edit=(item)=>{
                sessionStorage.setItem('tfLyAccountMgmt',angular.toJson(item));
                $state.go('tfLyAddAccount.tfLyAddAccount',{type:'edit'});
            };
            $scope.detail=(item)=>{
                sessionStorage.setItem('tfLyAccountDetail',angular.toJson(item));
                $state.go('tfLyAccountMgmt.tfLyAccountMgmtDetail');
            };
            $scope.reset=()=>{
                const resetVal = Object.keys($scope.fetchParam).reduce((total,item)=>{
                    total[item]='';
                    return total;
                },{});
                $scope.fetchParam=resetVal;
                $timeout(()=>{
                    $scope.fetch({currPage:1});
                });
            }

            $scope.changeCreateDate=()=>{
                let createDate = $scope.fetchParam.orderbyitem;
                if(createDate==='buy_create_date ASC'){
                    createDate='buy_create_date DESC';
                    $scope.setOrderbyitem(createDate);
                }
                if(createDate==='buy_create_date DESC'){
                    createDate='buy_create_date ASC';
                    $scope.setOrderbyitem(createDate);
                }
            }
            $scope.changeEndDate=()=>{
                let endDate = $scope.fetchParam.orderbyitem;
                if(endDate==='buy_end_date ASC'){
                    endDate='buy_end_date DESC';
                    $scope.setOrderbyitem(endDate);
                }
                if(endDate==='buy_end_date DESC'){
                    endDate='buy_end_date ASC';
                    $scope.setOrderbyitem(endDate);
                }
            }
            $scope.setOrderbyitem=(value)=>{
                $scope.fetchParam.orderbyitem=value;
                $scope.fetch({currPage:1});
            }
            let statisticsUtmsClient=()=>{
                service.statisticsUtmsClient().then((res)=>{
                    if(res.data.result==='success'){
                        $scope.statisticsUtmsClient=res.data.data
                    }
                });
            };
            statisticsUtmsClient();
        }]);
    }
});
              