define([
    'angular',
    '../orgChart/orgChart.html',
    '../orgChart/orgChart.css'
], function(
    angular,
    html) {
    return function(app, elem, attrs, scope) {
        app.directive('orgChart', ['$timeout', 
            function($timeout) {
            return {
                template: html,
                restrict: 'EA',
                replace: true,
                scope: {
                    treeData: '=',
                    textField: '@',
                    itemClicked: '&',
                    mouseOver: '&',
                    mouseLeave: '&'
                },
                link: function postLink() {
                    
                },

                controller: function($scope,$element,$attrs,$transclude,$log,$http,G){

                    $scope.itemExpended = function(item, $event){
                        item.$$isExpend = !item.$$isExpend;
                        toggleChildrenTips(item, item.$$isExpend);
                        $event.stopPropagation();
                    };

                    function toggleChildrenTips(array, isExpend){
                        if(Array.isArray(array.children)&&array.children.length>0){
                            angular.forEach(array.children, function(child){
                                child.hideTip = !isExpend;
                                toggleChildrenTips(child);
                            })
                        }
                    }
                     
                    $scope.isLeaf = function(item){
                        return !item.children || !item.children.length; 
                    };

                    $scope.getItemStyle = function(item, $index, $last){
                        var mod2 = $index%2;
                        if($index === 0){
                            return 'right';
                        }else if(mod2 === 0){
                            return 'right top';
                        }else if($last){
                            return 'left';
                        }else if(mod2 === 1){
                            return 'left top';
                        }                      
                    };

                    $scope.getIcon = function(item){
                        if($scope.isLeaf(item)){
                            return;
                        }else if(item.$$isExpend){
                            return 'fa fa-caret-down';
                        }else{
                            return 'fa fa-caret-up';
                        }
                    };

                    $scope.warpCallback = function(callback, item, $event){
                        ($scope[callback] || angular.noop)({
                             $item:item,
                             $event:$event
                         });
                    };

                    $scope.checkChildren = function(parent){
                        let result = true;
                        angular.forEach(parent.children, function(child){
                            if(Array.isArray(child.children)&&child.children.length>0){
                                result = false;
                            }
                        });
                        return result;
                    }

                    $scope.allLeaf = function(children){
                        let isLeaf = true;
                        angular.forEach(children, function(child){
                            if(child.children&&child.children.length>0){
                                isLeaf = false;
                            }
                        });
                        angular.forEach(children, function(child){
                            child.isLeafLevel = isLeaf;
                        });
                        return !isLeaf;
                    };
                }
            };
        }]);
    };
});