define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('basicAreaMgmtService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                getMarketList: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacmarketplacecs/selectRbacMarketPlaceByParam',
                        method: 'post',
                        data: options,
                        test: 'basicConfigModules/basicAreaMgmt/basicAreaMgmt.json'//假数据api
                    });
                },

                updateMarket: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacmarketplacecs/updateRbacMarketPlace',
                        method: 'post',
                        data: options,
                        test: 'basicConfigModules/header/success.json'//假数据api
                    });
                },

                getCityByUser: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacorganizationcs/selectCascadeRbacOrganizationByCode',
                        data: opts,
                        method: 'post',
                        test: '/modules/basicOrgChart/basicOrgChart.json'//假数据api
                    });
                }
            };
        }]);
    }
});