define([
    '../basicOrgChart/basicOrgChartCtrl',
    '../basicOrgChart/basicOrgChartService',
    '../orgChart/orgChart',
    'scmsModules/tooltip/tooltip',
    '../basicOrgChart/basicOrgChart.html',
    '../basicOrgChart/basicOrgChart.css'
], function(
    ctrl,
    service,
    orgChart,
    tooltip,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        orgChart(app, elem, attrs, scope);
        tooltip(app, elem, attrs, scope);
        elem.append(html);
    }
});