define([
    'angular'
], function (
    angular
) {
        return function (app, elem, attrs, scope) {
            app.controller('addBasicPreloadCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'addBasicPreloadService',
                function ($rootScope, $scope, $state, $stateParams, $timeout, G, service) {
                    
                    init();

                    $scope.cancel = function () {
                        $state.go('basicPreload.basicPreload');
                    };

                    
                    $scope.submit = function () {
                        var opts = $scope.formData;
                        // console.log(opts);
                        if ($stateParams.urlkey) {
                            update(opts);
                        } else {
                            add(opts);
                        }
                    };


                    function init() {
                        $scope.formData = {type:0};
                        $scope.isEdit = !!$stateParams.urlkey;

                        if ($stateParams.urlkey) {
                            service.detailPreload({ urlkey: $stateParams.urlkey }).then(function (data) {
                                if (data && data.data && data.data.result === 'success') {
                                    $scope.formData = data.data.data;
                                } else {
                                    G.alert(data.data.msg || '网络异常', {
                                        type: 'error'
                                    });
                                }
                            }, function (data) {
                                G.alert(data.data.msg || '网络异常', {
                                    type: 'error'
                                });
                            });
                        }
                    }


                    function update(opts) {
                        var isSend = false;
                        if(!isSend){
                            isSend = true;
                            service.updatePreload(opts).then(function (data) {
                                isSend = false;
                            if (data && data.data && data.data.result === 'success') {
                                G.alert(data.data.msg || '修改成功');
                                $state.go('basicPreload.basicPreload');
                            } else {
                                G.alert(data.data.msg || '修改失败', {
                                    type: 'error'
                                });
                            }
                        }, function (data) {
                            isSend = false;
                            G.alert(data.data.msg || '修改失败', {
                                type: 'error'
                            });
                        });
                        }
                        
                    }
                    function add(opts) {
                        var isSend = false;
                        if(!isSend){
                            isSend = true;
                            service.addBasicPreload(opts).then(function (data) {
                                isSend = false;
                            if (data && data.data && data.data.result === 'success') {
                                G.alert(data.data.msg || '新建成功');
                                $state.go('basicPreload.basicPreload');
                            } else {
                                G.alert(data.data.msg || '新建失败', {
                                    type: 'error'
                                });
                            }
                        }, function (data) {
                            isSend =false;
                            G.alert(data.data.msg || '新建失败', {
                                type: 'error'
                            });
                        });
                        }
                        
                    }
                }]);

        }
    });