import angular from 'angular';

export default function(app, elem, attrs, scope) {
  app.controller('activityLikesCtrl', [
    '$rootScope',
    '$scope',
    '$state',
    '$stateParams',
    '$timeout',
    'G',
    'activityLikesService',
    function($rootScope, $scope, $state, $stateParams, $timeout, G, service) {
      $scope.pageSize = 15;

      $scope.currentPage = 1;
      var init = true;
      getPictureCount().then(() => {
        if (init) {
          if ($scope.pictureCount.acriveFlag === '1') {
            getPicture().then(
              () => {
                getPictureCount();
              },
              () => {}
            );
          } else {
            getPictureList();
          }
          $timeout(() => {
            init = false;
          }, 500);
        }
      });

      $scope.checkItem = ($event, picture) => {
        picture.checked = !picture.checked;
      };
      $scope.checkAll = () => {
        $scope.pictureList.forEach(item => {
          item.checked = true;
        });
      };

      function getPicture() {
        return new Promise((resolve, reject) => {
          G.loading(true);
          localStorage.setItem('pictureChecked', false);
          if (localStorage.getItem('pictureList')) {
            let data = JSON.parse(localStorage.getItem('pictureList'));
            if (data && data.length) {
              $scope.pictureList = data;
              G.loading(false);
              reject();
            }
          } else {
            service.getPicture().then(
              data => {
                if (data.data.result === 'success') {
                  $scope.pictureList = data.data.data;
                  G.loading(false);
                  resolve();
                  if ($scope.pictureList && $scope.pictureList.length) {
                    localStorage.setItem(
                      'pictureList',
                      JSON.stringify(data.data.data)
                    );
                  }
                } else {
                  G.alert(data.data.msg, { type: 'error' });
                }
              },
              () => {
                G.alert('请求数据失败!', { type: 'error' });
              }
            );
          }
        });
      }
      function getPictureList() {
        G.loading(true);
        service
          .getPictureList({
            skipCount: ($scope.currentPage - 1) * $scope.pageSize,
            pageSize: $scope.pageSize,
          })
          .then(
            data => {
              if (data.data.result === 'success') {
                $scope.pictureList = data.data.data;
                $scope.totalCount = data.data.count;
                G.loading(false);
              } else {
                G.alert(data.data.msg, { type: 'error' });
              }
            },
            () => {
              G.alert('请求数据失败!', { type: 'error' });
            }
          );
      }
      function getPictureCount() {
        return new Promise((resolve, reject) => {
          service.getPictureCount().then(
            data => {
              if (data.data.result === 'success') {
                $scope.pictureCount = data.data.data;
                resolve();
              } else {
                G.alert(data.data.msg, { type: 'error' });
              }
            },
            () => {
              G.alert('请求数据失败!', { type: 'error' });
            }
          );
        });
      }

      $scope.checkPicture = () => {
        G.alert('确认审核通过本次勾选的照片？！', {
          type: 'confirm',
          callback: () => {
            var passPicIds = '',
              unPassPicIds = '';
            $scope.pictureList.forEach(d => {
              if (d.checked) {
                passPicIds += `,${d.id}`;
              } else {
                unPassPicIds += `,${d.id}`;
              }
            });
            service
              .checkPicture({
                passPicIds: passPicIds.slice(1),
                unPassPicIds: unPassPicIds.slice(1),
              })
              .then(data => {
                if (data.data.result === 'success') {
                  G.alert(data.data.msg);
                  getPictureCount();
                  localStorage.setItem('pictureChecked', true);
                } else {
                  G.alert(data.data.msg, { type: 'error' });
                }
              });
          },
        });
      };
      $scope.endAcitivity = () => {
        G.alert('确认结束该活动!', {
          type: 'confirm',
          callback: () => {
            service.endActivity().then(data => {
              if (data.data.result === 'success') {
                G.alert(data.data.msg);
                $scope.currentPage = 1;
                getPictureCount();
                getPictureList();
              } else {
                G.alert(data.data.msg, { type: 'error' });
              }
            });
          },
        });
      };
      $scope.nextPictures = () => {
        G.alert(
          '确认进行下一批照片审批？<br/><br/><ul><li style="color:red;">*该页面照片将不再出现在审核页！</li><li style="color:red;">*确认已完成当前照片审核操作？</li></ul>',
          {
            type: 'confirm',
            callback: () => {
              localStorage.removeItem('pictureList');
              if (!JSON.parse(localStorage.getItem('pictureChecked'))&&$scope.pictureList&&$scope.pictureList.length) {
                var unPassPicIds = '';
                $scope.pictureList.forEach(d => {
                  unPassPicIds += `,${d.id}`;
                });
                service
                  .checkPicture({
                    passPicIds: '',
                    unPassPicIds: unPassPicIds.slice(1),
                  })
                  .then(data => {
                    if (data.data.result === 'success') {
                      G.alert(data.data.msg);
                      getPicture().then(() => {
                        getPictureCount();
                      });
                    } else {
                      G.alert(data.data.msg, { type: 'error' });
                    }
                  });
              } else {
                getPicture().then(() => {
                  getPictureCount();
                });
              }
            },
          }
        );
      };
      $scope.fetch = () => {
        if (!init) {
          getPictureList();
        }
      };
      $timeout(() => {
        resize();
      }, 500);
      var time;
      $(window).on('resize', () => {
        if (time) {
          return;
        }
        time = setTimeout(() => {
          resize();
          time = null;
        }, 500);
      });
      function resize() {
        $timeout(() => {
          var picWidth = $('.picture')
            .eq(0)
            .width();
          $scope.picHeightValue = picWidth * (134 / 186);
        });
      }
    },
  ]);
}
