import html from './questionEdit.html';
export default async function(app, elem, attrs, scope) {
  app.directive('questionDialogDirective', [
    '$cookies',
    '$timeout',
    'annualPartyService',
    function($cookies, $timeout, service) {
      return {
        template: html,
        restrict: 'EA',
        replace: true,
        scope: {
          currentItem: '=',
          readonly: '=',
          onfetch: '&'
        },
        link: function postLink($scope, $element, $attrs) {},
        controller: function($scope, $element, $attrs, $transclude, G) {
          var questionDialog = $('#questionDialog');

          function parse(json, defaultValue) {
            if (json === null || json === undefined) {
              return defaultValue;
            }
            try {
              return JSON.parse(json);
            } catch (e) {
              return defaultValue;
            }
          }

          function stringify(json, defaultValue) {
            if (json === null || json === undefined) {
              return defaultValue;
            }
            try {
              return JSON.stringify(json, function(key, value) {
                if (key === '$$hashKey') {
                  return undefined;
                }

                return value;
              });
            } catch (e) {
              return defaultValue;
            }
          }
          $scope.isFetching = false;
          $scope.submit = function() {
            let {
              gameQuestionId,
              questionName,
              options,
              isOptionCodes,
              levelType
            } = $scope;
            options = options&&options.map(option=>{
              return {
                ...option,
                desc:option.desc.trim().replace(/\n+/g, '')
              };
            });

            const data = {
              gameQuestionId,
              questionName:questionName&&questionName.trim().replace(/\n+/g, ''),
              options: angular.toJson(options),
              isOptionCodes,
              levelType
            };

            if(!$scope.isFetching){
              G.loading(true);
              $scope.isFetching =true;
              const method = $scope.currentItem.gameQuestionId ? 'update' : 'add';
              service.question[method](data).then(
                data => {
                  G.loading(false);
                  $scope.isFetching = false;
                  data = data.data;
                  if (data.result == 'success') {
                    questionDialog.modal('hide');
                    G.alert('保存成功！');
                    if ($scope.onfetch) {
                      $scope.onfetch();
                    }
                  } else {
                    G.alert(data.msg || '保存失败', {
                      type: 'error'
                    });
                  }
                },
                data => {
                  G.loading(false);
                  $scope.isFetching = false;
                  G.alert(data.msg || '保存失败', {
                    type: 'error'
                  });
                }
              );
            }

          };

          $('#questionDialog').on('show.bs.modal', function() {
            G.clearAllError();
            $scope.errormsg = '';
            $timeout(() => {
              let {
                questionName,
                gameQuestionId,
                options,
                isOptionCodes,
                levelType
              } = $scope.currentItem;
              options = parse(options) || [
                { code: 'A', desc: '' },
                { code: 'B', desc: '' },
                { code: 'C', desc: '' }
              ];
              $scope.questionName = questionName;
              $scope.gameQuestionId = gameQuestionId;
              $scope.options = options;
              $scope.isOptionCodes = isOptionCodes || 'A';
              $scope.levelType = levelType || 1;

            }, 10);
          });
        }
      };
    }
  ]);
}
