define([
    'angular'
    ], function(
        angular
        ) {
        return function(app, elem, attrs, scope) {
            app.controller('basicStaffMgmtCtrl', ['$scope', '$state', '$stateParams', '$timeout', 'G', 'basicStaffMgmtService', 'navsData', '$q',
                function($scope, $state, $stateParams, $timeout, G, service, navsData, $q) {
                    var currentOrgFlag = 0;
                    $scope.introduction = '●总部，城市等各组织单元维护自己的员工信息，包括新增员工，修改，查看，删除。完善员工所属城市，区域，部门，角色等信息。';
                    $scope.topPos='right';
                    $scope.selectedRole = {};
                    $scope.selectedPosition = {};
                    $scope.userProfessionMap = [];
                    $scope.staffStatus = [
                        {
                            name: '生效中',
                            value: '1'
                        },{
                            name: '已作废',
                            value: '9'
                        },{
                            name: '全部',
                            value: ''
                        }
                    ];
                    $scope.selectedStatus = $scope.staffStatus[0];
                    
                    function init(){
                        $scope.searchType = 'navClick';
                        $scope.permission = navsData.permission;
                        $scope.showOrgGuide=false;
                        getRoleList();
                        service.getOrg({
                            organizationcode:G.userInfo.organizationcode,
                            returnformat:2
                        }).then(function(data){
                            $scope.orgList=data.data.data;
                            if(!$scope.orgList||!$scope.orgList.length){
                                $scope.showOrgGuide=true;
                                return;
                            }
                            var currOrg;
                            angular.forEach($scope.orgList,function(item){
                                if(item.organizationcode === G.userInfo.organizationcode ){
                                    currOrg =G.clone(item);
                                }
                            })
                            $scope.orgList=G.toTree($scope.orgList,currOrg.parorganizationcode,'parorganizationcode','organizationcode');
                            $scope.defaultItem=G.clone($scope.orgList);
                            $scope.updateRight($scope.orgList);
                            $scope.orgList=new Array($scope.orgList);
                        });
                        $scope.currPage=1;
                        // $scope.pageSize=G.getPageSize();
                        getOrgType().then(function(result){
                            if(result&&result.length>0){
                                angular.forEach(result,function(orgType){
                                    getProfession(orgType.dictionaryname).then(function(professions){
                                        if(professions&&professions.length>0){
                                            $scope.userProfessionMap.push({
                                                key: orgType.dictionaryname,
                                                value: professions
                                            });
                                        }
                                    })
                                })
                            }
                        });

                        getPositionList();
                    }

                    function getRoleList(){
                        service.selectRbacRoleByParam().then(function(data){
                            if(data&&data.data&&data.data.result==='success'){
                                $scope.roleList = data.data.data;
                                $scope.roleList.unshift({
                                    rolename: '选择角色',
                                    ehdrbacroleid: ''
                                });
                                $scope.selectedRole = $scope.roleList[0];
                            }
                        });
                    }

                    $scope.updateRight=function($item){
                        $scope.defaultItem = $item;
                        $scope.searchType = 'navClick';
                        $scope.currPage = 1;
                        $scope.keywords = '';
                        currentOrgFlag = 0;
                        $scope.selectedStruct=$item;
                        $scope.userStruct=$item;
                        getList(0);
                    }

                    function getList(orgFlag) {
                        orgFlag = orgFlag || currentOrgFlag; 
                        $scope.searchResult=false;
                        $scope.showFlag=false;
                        G.loading(true);
                        service.selectRbacUserByParam({
                            organizationcode: $scope.selectedStruct.organizationcode,
                            organizationflag: orgFlag,
                            ehdrbacpostid: $scope.selectedPosition.dictionarycode,
                            userstatus: $scope.selectedStatus.value,
                            ehdrbacroleid: $scope.selectedRole.ehdrbacroleid,
                            resultstyle: '2',
                            keywords:$scope.keywords,
                            skipCount: ($scope.currPage-1)*$scope.pageSize,
                            pageSize: $scope.pageSize
                        }).then(function(data){
                            $scope.totalCount=data.data.count;
                            $scope.staffList=data.data.data;
                            if((!$scope.staffList||!$scope.staffList.length)){
                                if($scope.selectedStatus.value===''&&$scope.selectedRole.ehdrbacroleid===''&&$scope.selectedPosition===''){
                                    $scope.showFlag=true;
                                }else{
                                    $scope.searchResult=true;
                                }
                            }
                            G.loading(false);
                        },function(){
                            G.loading(false);
                        });
                    }

                    $scope.paginationChanged = function(){
                        if($scope.selectedStruct){
                            getList();
                        }
                    };

                    $scope.setCurrent = function(item){
                        $scope.currentItem = item;
                    }

                    $scope.editDetail=function(data) {
                        $scope.opType='edit';
                        $scope.ehdrbacusersid = data.ehdrbacusersid;
                        setCurrentOrg(data);
                    }

                    function setCurrentOrg(user){
                        var organizationcode = G.userInfo.organizationcode;
                        $scope.userStruct = G.searchTree($scope.orgList[0],organizationcode,'organizationcode');
                    }

                    $scope.search =function() {
                        $scope.searchType = 'btnClick';
                        currentOrgFlag = 2;
                        $scope.defaultItem=G.clone($scope.orgList[0]);
                        $scope.searchResult=false;
                        $scope.keywordsCopy=G.clone($scope.keywords);
                        $scope.currPage=1;
                        G.loading(true);
                        service.selectRbacUserByParam({
                            organizationcode:$scope.selectedStruct.organizationcode,
                            organizationflag:2,
                            resultstyle: '2',
                            userstatus: $scope.selectedStatus.value,
                            ehdrbacroleid: $scope.selectedRole.ehdrbacroleid,
                            ehdrbacpostid: $scope.selectedPosition.dictionarycode,
                            keywords:$scope.keywords,
                            skipCount:$scope.currPage-1,
                            pageSize:$scope.pageSize
                        }).then(function(data){
                            $scope.totalCount=data.data.count;
                            $scope.staffList=data.data.data;
                            $scope.showFlag=false;
                            if(!data.data.data||!data.data.data.length){
                                $scope.searchResult=true;
                            }
                            G.loading(false);
                        },function(){
                            G.loading(false);
                        });
                    }

                    $scope.returnPre = function() {
                        $scope.keywords = '';
                        $scope.searchResult = false;
                        getList();
                    }


                    $scope.$on('refresh',function(){
                        getList();
                    });

                    $scope.toggleStatus=function($event,item) {
                        var status = item.userstatus==='1'?'9':'1';
                        var msg = item.userstatus==='1'?'您确定删除该员工？':'您确定激活该员工？';
                        var $alert = G.confirm({
                            $target: $($event.target),
                            msg: msg,
                            position: 'bottom',
                            submit: function() {    
                                service.updateRbacUser({
                                    ehdrbacusersid:item.ehdrbacusersid,
                                    userstatus:status
                                }).then(function(data) {
                                    data = data.data;
                                    if(data.result === 'success') {
                                        $alert.hide();
                                        G.alert('员工状态切换成功');
                                        getList();
                                    }else {
                                        G.alert(data.msg || '员工状态切换失败', {
                                            type: 'error'
                                        });
                                        $alert.hide();
                                    }
                                },
                                function() {
                                    G.alert('员工状态切换失败', {
                                        type: 'error'
                                    });
                                    $alert.hide();
                                });
                            }    
                        });
                    };

                    $scope.resetPwd=function($event,item) {
                        var $alert = G.confirm({
                            $target: $($event.target),
                            msg: '确定重置密码？',
                            position: 'bottom',
                            submit: function() {    
                                service.resetPwd({
                                    ehdrbacusersid:item.ehdrbacusersid,
                                    type:'1'
                                }).then(function(data) {
                                    data = data.data;
                                    if(data.result === 'success') {
                                        $alert.hide();
                                        $('#resetPwdSuccess').modal({backdrop:'static'});
                                    }else {
                                        G.alert(data.msg || '员工密码重置失败', {
                                            type: 'error'
                                        });
                                        $alert.hide();
                                    }
                                },
                                function() {
                                    G.alert('员工密码重置失败', {
                                        type: 'error'
                                    });
                                    $alert.hide();
                                });
                            }    
                        });
                    };

                    function getPositionList(){
                        service.selectDictionaryList({
                            parentdictionarycode: 'position'
                        }).then(function(data){
                            $scope.positionList = data.data.data;
                            $scope.positionList.unshift({
                                dictionaryname: '选择职位',
                                dictionarycode:''
                            });
                            $scope.selectedPosition = $scope.positionList[0];
                        });
                    }

                    function getProfession(organizationtype){
                        var deferred = $q.defer(),
                            promise = deferred.promise;
                        service.selectDictionaryList({
                            parentdictionarycode:'userprofession'+'_'+organizationtype
                        }).then(function(data){
                            deferred.resolve(data.data.data);
                        });
                        return promise;
                    }

                    function getOrgType(){
                        var deferred = $q.defer(),
                            promise = deferred.promise;
                        service.selectDictionaryList({
                            parentdictionarycode:'orgType'
                        }).then(function(data){
                            deferred.resolve(data.data.data);
                        });
                        return promise;
                    }

                    $scope.changeStatus = function(){
                        $scope.currPage=1;
                        getList();
                    };
                    
                    $scope.changeRole = function(){
                        $scope.currPage=1;
                        getList();
                    };

                    $scope.$on('updateOrg',function(){
                        init();
                    });

                    $scope.$on('refreshList', function(){
                        if($scope.searchType==='navClick'){
                            $scope.updateRight($scope.defaultItem);
                        }
                        if($scope.searchType==='btnClick'){
                            $scope.search();
                        }
                    });

                    init();

                }]);

    }
});