define([
    '../tfLyAddAccount/tfLyAddAccountCtrl',
    '../tfLyAddAccount/tfLyAddAccountService',
    '../basicSelectCityGroup/basicSelectCityGroupDirective',
    'scmsModules/pagination/paginationDirective',
    '../fileUploadDirective/fileUploadDirective',
    'scmsModules/ngDatePicker/datePicker',
    '../tfLyAddAccount/tfLyAddAccount.html',
    '../tfLyAddAccount/tfLyAddAccount.css'
],function(
    ctrl,
    service,
    basicSelectCityGroupDirective,
    paginationDirective,
    fileUploadDirective,
    datePicker,
    html,
    css){
    return (app,elem,attrs,scope)=>{
        datePicker(app,elem,attrs,scope);
        basicSelectCityGroupDirective(app,elem,attrs,scope);
        paginationDirective(app,elem,attrs,scope);
        fileUploadDirective(app,elem,attrs,scope);
        service(app,elem,attrs,scope);
        ctrl(app,elem,attrs,scope);
        elem.append(html);
    }
})