define([
  'angular'
], function (
  angular
) {
  return function (app, elem, attrs, scope) {
    app.controller('addDiscoveryPreloadCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'addDiscoveryPreloadService', function ($rootScope, $scope, $state, $stateParams, $timeout, G, service) {
      init();
      $scope.submit = function () {
        // if (!validateForm()) {
        //   return;
        // }
        var opts = $scope.formData;
        var version = switchVersion(opts.version);
        var citycode = switchCitys($scope.checkedItems).citycode;
        var cityname = switchCitys($scope.checkedItems).cityname;

        var params = {
          discoveryconfigid: $stateParams.discoveryconfigid || '',
          iconurl: opts.iconurl[0],
          citycode: citycode,
          cityname: cityname,
          name: opts.name,
          url: opts.url,
          order: opts.order,
          version: version,
          indexshow: opts.indexshow,
          indexorder: opts.indexorder
        };
        // console.log(params);
        
        if ($stateParams.discoveryconfigid) {
          update(params);
        } else {
          add(params);
        }
      };


      function init() {
        $scope.formData = {};
        $scope.errors = {};
        $scope.homesorts = [{
            'name': '1'
          },
          {
            'name': '2'
          },
          {
            'name': '3'
          },
          {
            'name': '4'
          }
        ];
        $scope.formData.indexshow = '0';
        // $scope.citys = [];
        $scope.isEdit = !!$stateParams.discoveryconfigid;

        if ($stateParams.discoveryconfigid) {
          service.detailPreload({
            discoveryconfigid: $stateParams.discoveryconfigid
          }).then(function (data) {
            if (data && data.data && data.data.result === 'success') {
              $scope.formData = data.data.data;
              $scope.citys = data.data.data.citycode && data.data.data.citycode.split(',');

              $scope.formData.iconurl = [$scope.formData.iconurl];
              $scope.formData.version = switchVersion($scope.formData.version);
            } else {
              G.alert(data.data.msg || '网络异常', {
                type: 'error'
              });
            }
          }, function (data) {
            G.alert(data.data.msg || '网络异常', {
              type: 'error'
            });
          });
        }
      }


      // 转换生效版本
      function switchVersion(version) {
        if (angular.isObject(version)) {
          var androidVersion = version[0] ? ('A' + version[0]) : '';
          var iosVersion = version[1] ? ('I' + version[1]) : '';
          // version = androidVersion+'&'+iosVersion;
          version = (androidVersion || iosVersion) ? (androidVersion + '&' + iosVersion) : '';
        } else if (version && angular.isString(version)) {
          version = version.replace(/[A-Za-z]/g, '').split('&');
        }
        return version
      }
      //转换城市
      function switchCitys(citys) {
        var citycode = '',
          cityname = '',
          citycodeArr = [],
          citynameArr = [];
        if (citys && angular.isArray(citys)) {
          citys.forEach(function (item) {
            citycodeArr.push(item.citycode);
            citynameArr.push(item.cityname);
          })
        }
        citycode = citycodeArr.join(',');
        cityname = citynameArr.join(',');
        return {
          citycode: citycode,
          cityname: cityname
        }
      }

      function update(params) {
        var isSend = false;
        if (!isSend) {
          isSend = true;
          service.updatePreload(params).then(function (data) {
            isSend = false;
            if (data && data.data && data.data.result === 'success') {
              G.alert(data.data.msg || '修改成功');
              $state.go('discoveryPreload.discoveryPreload');
            } else {
              G.alert(data.data && data.data.msg || '修改失败', {
                type: 'error'
              });
            }
          }, function (data) {
            isSend = false;
            G.alert(data.data && data.data.msg || '修改失败', {
              type: 'error'
            });
          });
        }
      }


      function add(params) {
        var isSend = false;
        if (!isSend) {
          isSend = true;
          service.addPreload(params).then(function (data) {
            isSend = false;
            if (data && data.data && data.data.result === 'success') {
              G.alert(data.data.msg || '新建成功');
              $state.go('discoveryPreload.discoveryPreload');
            } else {
              G.alert(data.data && data.data.msg || '新建失败', {
                type: 'error'
              });
            }
          }, function (data) {
            isSend = false;
            G.alert(data.data && data.data.msg || '新建失败', {
              type: 'error'
            });
          });
        }
      }


      //  function validateForm() {
      // if (!$scope.formData.iconurl[0]) {
      //   $scope.errors.image = "请上传图片";
      //   return false;
      // }

      // if (!$scope.formData.citycode[0] || !$scope.formData.citycode.length) {
      //   $scope.errors.city = "请选择城市";
      //   return false;
      // }
      //   var version = $scope.formData.version;
      //   if(!((version[0]&&version[1])||(!version[0]&&!version[1]))){
      //     $scope.errors.version="请填写版本号";
      //     return false;
      //   }

      //   return true;
      // }

      // $scope.$watch('formData.iconurl', function (newVal, oldVal) {
      //   if ($scope.formData.iconurl&&newVal!=oldVal) {
      //     $scope.errors.image = "";
      //   }
      // });

      // $scope.$watch('formData.citycode', function (newVal, oldVal) {
      //   if ($scope.citysChosen&&newVal!=oldVal) {
      //     $scope.errors.city = "";
      //   }
      // });



      $scope.cancel = function () {
        $state.go('discoveryPreload.discoveryPreload');
      };



    }]);

  }
});