define([
    'angular'
],function(angular){
    return function(app,elem,attrs,scope){
        /**
         * 定义指令（只能输入字母、数字、全角符号或汉字）
         */
        app.directive('wSymbolHz',function(){
            return {
                require:'ngModel',
                link:function(scope,elem,attrs,ngModelController){
                    var reg=/^[\w\u4e00-\u9fa5\uFF00-\uFFFF]*$/;
                    var parsers=function(value){
                        if(value&&value.match(reg)){
                            ngModelController.$setValidity('wSymbolHz', true);
                        }else{
                            ngModelController.$setValidity('wSymbolHz', false);
                        }
                        return value;
                    }
                    var formatters=function(value){
                        if(value!==null&&value!==undefined){
                            parsers(value.toString());
                        }
                        return value
                    }
                    ngModelController.$parsers.push(parsers);
                    ngModelController.$formatters.push(formatters);
                }
            }
        })
        app.controller('carbonEmissionCtrl',['$scope','G','carbonEmissionService','defaultErrorMessageResolver',function($scope,G,service,defaultErrorMessageResolver){
            $scope.submit=function(){
                var formData=G.clone($scope.items);
                var formData=formData.map((item)=>{
                    if(typeof item.energyValue==='object'){
                        item.energyValue=angular.toJson(item.energyValue);
                    }
                    return {
                        "energyKey": item.energyKey,
                        "energyValue": item.energyValue,
                        "describtion": item.describtion
                    };
                });
                formData=angular.toJson(formData);
                G.loading(true);
                service.updateAndOnline(
                    {configList:formData}
                ).then((res)=>{
                    G.loading(false);
                    if(res.data.result==='success'){
                        G.alert(res.data.msg,{type:res.data.result});
                    }else{
                        G.alert(res.data.msg,{type:res.data.result});
                    }
                }).catch(()=>{
                    G.loading(false);
                    G.alert('请稍后重试',{type:'error'});
                });
            }
            $scope.cancel=function(){
                selectOnlineConfig();
            }
            /**
             * 读取上线配置详情
             */
            function selectOnlineConfig(){
                G.loading(true);
                service.selectOnlineConfig().then((res)=>{
                    G.loading(false);
                    if(res.data.result==='success'){
                        $scope.items=res.data.data.reduce(function(total,item){
                            //四个固定字段按顺序排放，方便页面关联
                            if(item.energyKey==='页面标题'){
                                total[0]=item;
                                return total;
                            }
                            if(item.energyKey==='页面刷新间隔'){
                                total[1]=item;
                                return total;
                            }
                            if(item.energyKey==='排线优化里程比例'){
                                total[2]=item;
                                return total;
                            }
                            if(item.energyKey==='碳排放系数'){
                                total[3]=item;
                                return total;
                            }
                            item.energyValue=angular.fromJson(item.energyValue);
                            total.push(item);
                            return total;
                        },new Array(4));
                    }else{
                        G.alert(res.data.msg,{type:res.data.result});
                    }
                }).catch(()=>{
                    G.loading(false);
                    G.alert('请稍后重试',{type:'error'});
                });
            }
            selectOnlineConfig();
            defaultErrorMessageResolver.getErrorMessages().then(function (errorMessages) {
                errorMessages['wSymbolHz'] = '请输入汉字、字母、数字或全角符号';
              });
        }]);
    }
})