define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('systemErrorCodeService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                queryErrorConfigList: '/ehuodiBedrockApi/errorconfigcs/queryErrorConfigList',

                addLogConfig:function(opts){
                    return $http({
                        url: '/ehuodiBedrockApi/errorconfigcs/modifyErrorConfigById',
                        method: 'post',
                        data:opts
                    });                    
                },

                modifyErrorConfigById:function(opts){
                    return $http({
                        url: '/ehuodiBedrockApi/errorconfigcs/modifyErrorConfigById',
                        method: 'post',
                        data:opts
                    });
                }
            };
        }]);
    }
});