import './index.less';
export default (app, elem, attrs, scope) => {
  app.controller(
    'activityListCtrl',
    ['$scope', '$state','annualPartyService','G', ($scope, $state,service,G) => {

      $scope.apiUrl = service.activity.apiUrl;
      $scope.getStatus = statu => {
        const statusMap = {
          0: '未开始',
          1: '进行中',
          2: '已结束',
          3: '结算中'
        };
        return statusMap[statu];
      };


      $scope.edit=item=>{
        if(item.status===0){
          const {gameActivityId,activityName,activityAmount} = item;
          $state.go('annualParty.activityEdit', {gameActivityId,activityName,activityAmount});
        }
      };

      $scope.add=()=>{
        $state.go('annualParty.activityEdit');
      };

      $scope.startQuestion = item=>{
        if(item.status===1){
          $state.go('annualParty.activityHandle', {gameActivityId: item.gameActivityId,jobCard:item.jobCard});
        }
      };


      $scope.startActivity = function($event, item) {
        if(item.status===0){
          var $alert = G.confirm({
            $target: $($event.target),
            msg: '确认开始活动吗？',
            position: 'bottom',
            submit: function() {
              G.showWait();
              service.activity
                .start({
                  gameActivityId: item.gameActivityId
                })
                .then(
                  function(data) {
                    G.hideWait();
                    var data = data.data;
                    $alert.hide();
                    if (data.result === 'success') {
                      G.alert('活动已开始');
                      $scope.fetch();
                    } else {
                      G.alert(data.msg || '操作失败', { type: 'error' });
                    }
                  },
                  function() {
                    G.hideWait();
                    $alert.hide();
                    G.alert('操作失败', { type: 'error' });
                  }
                );
            }
          });
        }

      };

    }]
  );
};