import ctrl from './activityLikesCtrl';
import service from './activityLikesService';
import table from 'scmsModules/table/table';
import imageShow from 'scmsModules/imageShow/imageShow';
import html from './activityLikes.html';
import './activityLikes.css';

export default (app, elem, attrs, scope) => {
  ctrl(app, elem, attrs, scope);
  service(app, elem, attrs, scope);
  table(app, elem, attrs, scope);
  imageShow(app, elem, attrs, scope);
  elem.append(html);
};
