define([
    'angular'
], function (angular) {
    return function (app, elem, attrs, scope) {
        app.factory('listGeneratorService', ['$rootScope', '$http', 'G', function ($rootScope, $http, G) {
            return {
                getFetchUrl: function () {
                    return '/goodstaxiAdmin/getUrl';
                },
                foo: function (opts) {
                    return $http({
                        url: '/goodstaxiAdmin/',
                        method: 'post',
                        data: opts,
                        isDebug:true,
                        test: '/basicConfigWeb/basicConfigModules/listGenerator/listGenerator.json'
                    });
                }
            };
        }]);
    }
});