define([
    'angular'
],function(angular){
    return function(app,elem,attrs,scope){
        app.factory('carbonEmissionService',['$http',function($http){
            return {
                selectOnlineConfig:function(){
                    return $http({
                        url:'/businessOperationApi/energyConfig/selectOnlineConfigAdmin',
                        method:'post'
                    });
                },
                updateAndOnline:function(opts){
                    return $http({
                        url:'/businessOperationApi/energyConfig/updateAndOnline',
                        data:opts,
                        method:'post',
                    });
                }
            }
        }])
    }
})