define([
    'angular',
    '../directiveFormInput/bcFormSelectMulti.html',
    '../directiveFormInput/bcFormSelectMulti.css'
    ], function(
        angular,
        html) {
        return function(app, elem, attrs, scope) {
            app.directive('bcFormSelectMulti', ['$timeout', function($timeout) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope:{
                    optionsData:'=',  //传入的options [{name:'',value:''},{name:'',value:''},...]
                    selectedData:'=', //选中的值 
                    defaultName:'@',
                    selectWidth:'@',
                    displayName:'@'
                },
                link: function postLink(scope, element, attrs) {

                },
                controller:function($scope,$element){
                    $scope.selectedData=angular.isArray($scope.selectedData)?$scope.selectedData:[];
                    if($scope.selectWidth) {
                        $element.find('.form-select-multi').css('width',$scope.selectWidth+'px');
                        $element.find('.choice').css('width',$scope.selectWidth+'px');
                    }else{
                        $element.find('.form-select-multi').css('width', '100%');
                        $element.find('.choice').css('width', '100%');
                    }
                    $scope.$watch('selectedData',function(){
                        angular.forEach($scope.optionsData,function(optItem){
                            if($scope.selectedData&&$scope.selectedData.indexOf(optItem)>-1){
                                optItem.isactive=true;
                            }else{
                                optItem.isactive=false;
                            }
                        })
                    },true)

                    $scope.itemSelect=function(item,$event){
                        $scope.selectedData=angular.isArray($scope.selectedData)?$scope.selectedData:[];
                        if($scope.selectedData.indexOf(item)===-1){
                            $scope.selectedData.push(item);
                        }else{
                            $scope.itemCancel(item);
                        }

                    }
                    $scope.itemCancel=function(item) {
                        if(item.disabled){
                            return;
                        }
                        $scope.selectedData=angular.isArray($scope.selectedData)?$scope.selectedData:[];
                        var i=$scope.selectedData.indexOf(item);
                        $scope.selectedData.splice(i,1);
                    }

                    $scope.blur=function() {
                        $timeout(function(){
                            $scope.flag=false;
                        },200)
                    }
                }
            };
        }]);
        }
    });