define([
    'angular'
], function (
    angular
) {
        return function (app, elem, attrs, scope) {
            app.controller('systemMonitorCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'systemMonitorService', 'navsData', function($rootScope, $scope, $state, $stateParams, $timeout, G, service, navsData) {
                $scope.apiUrl = service.getFetchUrl();
                $scope.topMenus = navsData.userOwnNavList.filter(function(item){
                    return item.parentresourceid === 0;
                });
                $scope.fetchParam = {
                    sqlKey: 'ehuodiCrmMonitorStatisList',
                    orderkey: 'onlinedate',
                    ordercase: 'desc'
                };
                
                $scope.setOrder = function(orderkey, ordercase) {
                    $scope.fetchParam.orderkey = orderkey;
                    $scope.fetchParam.ordercase = ordercase;
                    $scope.fetch({ currPage: 1 });
                }

                $scope.search = function () {
                    $scope.fetch({ currPage: 1 });
                }
                $scope.showDetail = function(resourceid){
                    $state.go('systemMonitorDetail.systemMonitorDetail',{
                        resourceid: resourceid
                    });
                };

                $scope.getCount = async ()=>{
                    $scope.countParam = Object.assign({},$scope.fetchParam);
                    $scope.countParam.sqlKey = 'ehuodiCrmMonitorStatisCount';
                    for(let key in $scope.countParam){
                        if(!$scope.countParam[key]){
                            delete $scope.countParam[key];
                        }
                    };
                    let result =  await service.getCount($scope.countParam);
                    return result.data.data[0].count;
                };

            }]);
        }
    });
