define([
    'angular'
    ], function(angular) {
        return function(app, elem, attrs, scope) {
            app.factory('tfLyAddNewMenuService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
                return {        
                    insertRbacResource: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/utmsresourcecs/createUtmsResource',
                            method: 'post',
                            data:opts,
                            noconcurrent: true,
                        });
                    },

                    updateRbacResource: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/utmsresourcecs/updateUtmsResource',
                            method: 'post',
                            data:opts,
                            noconcurrent: true,
                        });
                    },
                };
            }]);
        }
    });