define([
    'angular',
    '../fileUploadDirective/fileUploadDirective.html',
    '../fileUploadDirective/fileUploadDirective.css',
], function(angular,html,css) {
    return function(app, elem, attrs, scope) {
        app.directive('fileUploadDirective', ['G','$timeout',function(G,$timeout) {
            return {
                template: html,
                restrict:'EA',
                replace:true,
                scope:{
                    ngModel:'=',
                    ngDisabled:'=',
                    isError:'=',
                    showErrorMsg:'@',
                    dMax:'@',
                    dType:'=',
                    size:'@',//单位MB
                    dWidth: '=',
                    dHeight: '=',
                    placeholder:'@',
                },
                controller:function($scope,$element,$attrs,$transclude,G){
                    $scope.dMax = parseInt($scope.dMax, 10) || 1;
                    $scope.ngModel = $scope.ngModel || ($scope.dMax==1?'':[]);
                    $scope.isDownloading = true;
                    var $file = $element.find('.file-input');
                    $scope.loadImage = function(){
                        $scope.isDownloading = false;
                    };


                    //上传图片报错处理
                    var setError = function(msg) {
                        G.alert($attrs.errormsg || msg || '请按要求上传正确的图片', {
                            type: 'error',
                            speed: 5000
                        });
                        $file.val('');
                    };

                    //上传图片成功，显示图片
                    var show = function(data) {
                        $scope.ngModel=data.data[0].imageurl;
                        console.log(data.data[0].imageurl);
                    };


                  function judgeSize(file){
                      var data=new FormData();
                      var reader = new FileReader();
                      reader.readAsDataURL(file);
                      reader.onload = function(theFile) {
　　                          var image = new Image();
                          image.src = theFile.target.result;
                          image.onload = function() {
                            if($scope.dWidth && this.width !== $scope.dWidth) {
                                setError(`尺寸应为${$scope.dWidth}*${$scope.dHeight}，请重新上传`);
                            }
                            else if($scope.dHeight && this.height !== $scope.dHeight) {
                                setError(`尺寸应为${$scope.dWidth}*${$scope.dHeight}，请重新上传`);
                            }
                            else if($scope.dSize && file.size/1024 > parseInt($scope.dSize, 10)) {
                                setError(`大小应在${$scope.dSize}MB以内，请重新上传`);
                            }else{
                              data.append('img', file);
                              uploadImage(data);
                            }
                          };
                      };
                    }

                    function judgeType(item){
                        if($scope.dType){
                            var name = item.name;
                            var upperCaseType = $scope.dType;
                            var typeOfItem = name.substring(name.lastIndexOf('.')+1);
                            if(!typeOfItem||upperCaseType.indexOf(typeOfItem)<0){
                                setError(`请上传${upperCaseType}格式`);
                                return false;
                            }
                        }
                        return true;
                    }
                    //上传图片动作
                    $file.bind('change', function(event) {
                        var file=event.currentTarget;
                        var _this=this;

                        if(!file.files || (file.files && !file.files.length)) {
                            return;
                        }
                        angular.forEach(file.files, function(item, index) {
                            if(judgeType(item)){
                                judgeSize(item);
                            };
                        });
                    });
                    function uploadImage(data){
                          $scope.showLoading=true;
                          var xhr=new XMLHttpRequest();
                          xhr.open("post", '/goodstaxiAdmin/imagecs/uploadImage');
                          xhr.onreadystatechange=function(){
                              if(xhr.readyState==4){
                                  $scope.showLoading=false;
                                  if(xhr.status === 200) {
                                      $timeout(function() {
                                          var result=JSON.parse(xhr.responseText);
                                          show(result);
                                      });
                                  }
                                  else if(xhr.status === 413) {
                                      $timeout(function() {
                                          setError('文件太大，请重新上传');
                                          $file.val('');
                                      });
                                  }
                                  else {
                                      $timeout(function() {
                                          G.alert('上传图片失败，请重试', {
                                              type: 'error',
                                              speed: 5000
                                          });
                                          $file.val('');
                                      });
                                  }

                              }
                          };
                          xhr.send(data);
                    }
                }
            }
        }]);
    }
});