define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('basicPositionMgmtService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                getPositionList: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacpostcs/selectRbacPostByParam',
                        method: 'post',
                        data: opts,
                        test: 'basicConfigModules/basicPositionMgmt/basicPositionMgmt.json'//假数据api
                    });
                },

                updatePosition: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacpostcs/updateRbacPost',
                        method: 'post',
                        data: opts,
                        test: 'basicConfigModules/header/success.json'//假数据api
                    });
                },    
                selectRoles: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacrolecs/selectRbacRoleByParam',
                        method: 'post',
                        data:opts,
                        test: 'basicConfigModules/systemRoleMgmt/selectRbacRoleByParam.json'//假数据api
                    });   
                }
            };
        }]);
    }
});