define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('basicSelectCityGroupService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                getProvince: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/baseconfigcs/selectSubAreaList',
                        method: 'post',
                        test: 'basicConfigModules/basicSelectCityGroup/province.json'//假数据api
                    });
                },

                getCity: function(data) {
                    return $http({
                        url: '/ehuodiBedrockApi/baseconfigcs/selectSubAreaList',
                        method: 'post',
                        data: {
                            code: data.value
                        },
                        test: 'basicConfigModules/basicSelectCityGroup/city.json'//假数据api
                    });
                },

                getRegion: function(data) {
                    return $http({
                        url: '/ehuodiBedrockApi/baseconfigcs/selectSubAreaList',
                        method: 'post',
                        data: {
                            code: data.value
                        },
                        test: 'basicConfigModules/basicSelectCityGroup/region.json'//假数据api
                    });
                }
            };
        }]);
    }
});