define([
    '../addNewMenu/addNewMenu.css',
    'angular'
    ], function(angular) {
        return function(app, elem, attrs, scope) {
            app.factory('addNewMenuService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
                return {        
                    insertRbacResource: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacresourcecs/insertRbacResource',
                            method: 'post',
                            data:opts,
                            noconcurrent: true,
                            test: 'basicConfigModules/addNewMenu/addNewMenu.json'//假数据api
                        });
                    },

                    updateRbacResource: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacresourcecs/updateRbacResource',
                            method: 'post',
                            data:opts,
                            noconcurrent: true,
                            test: 'basicConfigModules/addNewMenu/addNewMenu.json'//假数据api
                        });
                    },

                    selectPubDictionaryListByParam: function(options) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdpubdictionarycs/selectPubDictionaryListByParam',
                            method: 'post',
                            data: options
                        });
                    }
                };
            }]);
        }
    });