define([
    'angular'
    ], function(angular) {
        return function(app, elem, attrs, scope) {
            app.factory('updateSystemLogService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
                return {   
                    updateLogConfig:function(opts){
                        return $http({
                            url: '/ehuodiBedrockApi/logmonitorconfigcs/updateLogMonitorConfig',
                            method: 'post',
                            data:opts,
                            test: 'scmsModules/header/success.json'//假数据api
                        });                    
                    }
                };
            }]);
        }
    });