define([
    'angular'
    ], function(
        angular
        ) {
        return function(app, elem, attrs, scope) {
            app.controller('tfLySysMenuMgmtCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'tfLySysMenuMgmtService', 'navsData', function($rootScope, $scope, $state, $stateParams, $timeout, G, service, navsData) {
                $scope.menuType='P';
                $scope.changeMenu=(type)=>{
                    $scope.menuType=type;
                    init();
                }
                function init(defaultItem){
                    $scope.permission = navsData.permission;
                    $scope.adjustBtn = '调整层级';
                    $scope.editFlag = false;
                    getMenuList(defaultItem);
                }

                function getMenuList(defaultItem){
                    $scope.showWebGuide = false;
                    G.loading(true);
                    service.selectRbacResourceByParam({
                        versionscode:$scope.menuType
                    }).then(function(data) {
                        G.loading(false);
                        $scope.webMenuList = [];
                        $scope.webMenuList.push({
                            parentresourceid:'TOP',
                            utmsresourceid:0,
                            resourcename:'传化联运菜单',
                            resourcetype:0,
                            resourceurl:';',
                            ordernumber:0,
                            resourceicon:'',
                            versionscodes:$scope.menuType,
                        });
                        $scope.lists = data.data.data;
                        if($scope.lists.length===0){
                            $scope.menus=[];
                            $scope.showWebGuide=true;
                        }
                        angular.forEach($scope.lists, function(menu){
                            if(menu.resourcetype===1){
                                menu.hide = true;
                            }
                            $scope.webMenuList.push(menu);
                        });
                        
                        $scope.webMenus=G.toTree($scope.webMenuList,'TOP','parentresourceid','utmsresourceid');
                        if(!$scope.webMenus[0]){
                            $scope.webMenus=[$scope.webMenus];
                        }

                        var item;
                        if(defaultItem){
                            $scope.defaultItem = G.clone(defaultItem);
                            for(var i=0,len=$scope.webMenus.length;i<len;i++){
                                if(G.searchTree($scope.webMenus[i],defaultItem.utmsresourceid,'utmsresourceid')){
                                    item=G.searchTree($scope.webMenus[i],defaultItem.utmsresourceid,'utmsresourceid');
                                    break;
                                }
                            }
                            if(!item){
                                for(var i=0,len=$scope.webMenus.length;i<len;i++){
                                    if(G.searchTree($scope.webMenus[i],defaultItem.parentresourceid,'utmsresourceid')){
                                        item=G.searchTree($scope.webMenus[i],defaultItem.parentresourceid,'utmsresourceid');
                                        break;
                                    }
                                }
                            }
                        }else{
                            $scope.defaultItem = $scope.webMenus[0];
                            item = $scope.defaultItem;
                        }
                        var updateData = item || $scope.webMenus[0];
                        $scope.updateRight(updateData);
                   });
                }
                $scope.updateRight=function($item){
                    $scope.detail = $item;
                    $scope.nestableTree = $scope.webMenus;
                    replaceResourceType($scope.detail);
                    $scope.subStruc = $item.children;
                    angular.forEach($scope.subStruc, function(item){
                        replaceResourceType(item);
                    })
                }

                $scope.adjustOrderBtn = function(){
                    if($scope.editFlag===false){
                        $('#nestableTree').nestable({
                            group: 1,
                            maxDepth: 10
                        }).on('change', updateOutput);
                        $scope.editFlag = true;
                        $scope.adjustBtn = '取消';
                    }else{
                        $scope.editFlag = false;
                        $scope.adjustBtn = '调整层级';
                        $('#nestableTree').unbind('mousedown');
                        // init($scope.detail);
                    }
                };

                $scope.$on('confirmChange',function(){
                    updateOrder($scope.posObj);
                    $scope.editFlag = false;
                    $scope.adjustBtn = '调整层级';
                });

                $scope.$on('cancelChange',function(){
                    $scope.editFlag = false;
                    $scope.adjustBtn = '调整层级';
                    init($scope.detail);
                })
                function updateOutput(e, posObj){
                    var currentItem = {},
                        dragable = true;
                    $scope.posObj = posObj;
                    angular.forEach($scope.lists, function(item){
                        if(posObj.id===item.utmsresourceid){
                            currentItem = item;
                        }
                    });
                    $scope.confirmMsg = currentItem;
                    if(posObj.parentId===undefined){
                        dragable = false;
                        G.alert('禁止修改根节点', {type:'error'});
                        init($scope.detail);
                    }else if(posObj.parentUrl!==';'&&posObj.parentUrl!=='；'){
                        dragable = false;
                        G.alert('禁止移动到功能页面下', {type:'error'});
                        init($scope.detail);
                    }else if(posObj.parentId==='MOBILE'||[1,2,3,4].indexOf(posObj.id)>-1){
                        dragable = false;
                        G.alert('禁止修改移动风暴一级菜单', {type:'error'});
                        init($scope.detail);
                    }
                    if(posObj.hasChange&&dragable){
                        $('#confirmDialog').modal({
                            backdrop: 'static'
                        });
                    }
                }

                function updateOrder(posObj){
                    var ordernumber = 0,
                        params = {},
                        newOrderNum = (new Date().getTime()+'').substr(-8);
                    if(!posObj.prevOrder){
                        if(posObj.nextOrder){
                            ordernumber = posObj.nextOrder > 3600000?posObj.nextOrder - 3600000:Math.floor(posObj.nextOrder / 2);
                        }else{
                            ordernumber = Number(newOrderNum);
                        }
                    }else if(!posObj.nextOrder){
                        ordernumber = posObj.prevOrder + 3600000;
                    }else{
                        ordernumber = Math.floor((posObj.nextOrder + posObj.prevOrder) / 2);
                    }
                    params.utmsresourceid = posObj.id;
                    params.parentresourceid = posObj.parentId;
                    params.ordernumber = ordernumber;
                    updateUtmsResourceOrder(params);
                }

                function updateUtmsResourceOrder(params){
                    service.updateUtmsResourceOrder(params).then(function(data){
                        if(data&&data.data&&data.data.result==='success'){
                            G.alert('修改成功');
                            init($scope.detail);
                        }else{
                            G.errorAlert('修改失败', data);
                            init($scope.detail);
                        }
                    },function(data){
                        G.errorAlert('修改失败', data)
                    });
                }

                function replaceResourceType(item){
                    if(item.resourcetype===0) {
                        item.resourcetypeObj={name:'菜单',value:0}
                    }else{
                        item.resourcetypeObj={name:'按钮',value:1}
                    }
                }

                $scope.$on('refreshMenu', function(){
                    init($scope.detail);
                });

                $scope.editDetail=function(item){
                    $scope.currItem=item;
                    $scope.opType='edit';
                    $('#tfLyAddNewMenu').modal({
                        backdrop:'static'
                    });
                }
                $scope.addDetail=function(){
                    $scope.opType='add';
                    $('#tfLyAddNewMenu').modal({
                        backdrop:'static'
                    });
                }

                $scope.remove=function($event,item,$index) {
                    if(item.children&&item.children.length>0){
                        return;
                    }
                    var $alert = G.confirm({
                        $target: $($event.target),
                        msg: '您确定删除该资源？',
                        position: 'bottom',
                        submit: function() {    
                            service.deleteUtmsResource({
                                utmsresourceid:item.utmsresourceid,
                            }).then(function(data) {
                                data = data.data;
                                if(data.result === 'success') {
                                    $alert.hide();
                                    $scope.subStruc.splice($index,1);
                                    init();

                                    G.alert('资源删除成功');
                                }
                                else {
                                    G.alert(data.msg || '资源删除失败', {
                                        type: 'error'
                                    });
                                    $alert.hide();
                                }
                            },
                            function() {
                                G.alert('数据字典删除失败', {
                                    type: 'error'
                                });
                                $alert.hide();
                            });
                        }
                    });
                }

                $scope.removeFather=function($event,item,$index) {
                    if(item.children&&item.children.length>0){
                        return;
                    }
                    var $alert = G.confirm({
                        $target: $($event.target),
                        msg: '您确定删除该资源？',
                        position: 'bottom',
                        submit: function() {    
                            service.deleteUtmsResource({
                                utmsresourceid:item.utmsresourceid,
                            }).then(function(data) {
                                data = data.data;
                                if(data.result === 'success') {
                                    $alert.hide();
                                    init();
                                    G.alert('资源删除成功');
                                }
                                else {
                                    G.alert(data.msg || '资源删除失败', {
                                        type: 'error'
                                    });
                                    $alert.hide();
                                }
                            },
                            function() {
                                G.alert('数据字典删除失败', {
                                    type: 'error'
                                });
                                $alert.hide();
                            });
                        }
                    });
                }
                init();
            }]);

}
});