define([
    'angular'
    ], function(
        angular
        ) {
        return function(app, elem, attrs, scope) {
            app.controller('siteMapCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'siteMapService', 'navsData', 
                function($rootScope, $scope, $state, $stateParams, $timeout, G, service, navsData) {

                function init(){
                    getRoleList();
                }

                function selectRbacResourceByParam(){
                    service.selectRbacResourceByParam({
                        applicationflag: 'BG'
                    }).then(function(data){
                        if(data.data.data.length===0){
                            $scope.menus=[];
                            return;
                        }
                        $scope.lists = [];
                        angular.forEach(data.data.data, function(node, index){
                            if(node.resourcetype===0){
                                angular.forEach(navsData.userOwnNavList, function(item){
                                    if(item.ehdrbacresourceid===node.ehdrbacresourceid){
                                        node.active = true;
                                    }
                                });
                                node.href = node.resourceurl.lastIndexOf(';')>0?node.resourceurl.substring(node.resourceurl.lastIndexOf(';') + 1):'';
                                node.icon = node.resourceurl.substring(node.resourceurl.indexOf(';') + 1, node.resourceurl.lastIndexOf(';'));
                                node.businessAttributesArray = [];
                                let attributesArray = [];
                                try{
                                    attributesArray = JSON.parse(node.targetattributes);
                                }catch(e){console.error(e);}
                                
                                if(Array.isArray(attributesArray)&&attributesArray.length>0){
                                    angular.forEach(attributesArray, function(item){
                                        node.businessAttributesArray.push(item.businessattributes);
                                    });
                                    $scope.lists.push(node);
                                }
                                
                            }
                        });
                        angular.forEach($scope.lists, function(menu){
                            angular.forEach($scope.roleList, function(role){
                                if(role.children){
                                    angular.forEach(role.children, function(child){
                                        if(menu.businessAttributesArray.indexOf(child.dictionaryname)>-1){
                                            if(child.menuList){
                                                menu.permission = checkPermission(menu.resourceurl);
                                                child.menuList.push(menu);
                                            }else{
                                                child.menuList = new Array(menu);
                                            }
                                        }
                                    })
                                }
                            });
                        });
                    });
                }

                function checkPermission(url){
                    let result = false;
                    angular.forEach(navsData.navsList, function(item){
                        if(item.resourceurl===url){
                            result = true;
                        }
                    });
                    return result;
                }

                function getRoleList(){
                    service.selectPubDictionaryListByParam().then(function(data){
                        service.selectPubDictionaryListByParam({
                            parentdictionarycode: 'targetRole',
                            returnformat: 1
                        }).then(function(data){
                            $scope.roleList = [];
                            if(data&&data.data&&data.data.data){
                                let result = data.data.data;
                                $scope.roleList = G.toTree(result,'targetRole','parentdictionarycode','dictionarycode');
                            }
                            selectRbacResourceByParam();
                        })
                    });
                }

                $scope.gotoModule = function(menu){
                    if(menu.permission){
                        window.open(menu.href);
                    }else{
                        $('#siteNoPermission').modal();
                    }
                };

                $scope.ready = function(last, role){
                    if(last&&role.children){
                        if(role.children.length===1){
                            role.width = {'width': '100%'};
                        }else{
                            role.width = role.children.length===2?{'width': '50%'}:{'width': '33%'};
                        }
                    }
                };
                init();

            }]);

}
});