define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('tfLyAddAccountService', ['$http', function($http) {
            return {
                selectRbacUserByParam:(opts)=>{
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacuserscs/selectRbacUserByParam',
                        method:'post',
                        data:opts
                    })
                },
                createUtmsClient:opts=>{
                    return $http({
                        url :'/ehuodiBedrockApi/utmsclientcs/createUtmsClient',
                        method:'post',
                        data:opts
                    })
                },
                updateUtmsClient:opts=>{
                    return $http({
                        url :'/ehuodiBedrockApi/utmsclientcs/updateUtmsClient',
                        method:'post',
                        data:opts
                    })
                },
                selectUtmsDictionaryList:opts=>{
                    return $http({
                        url :'/ehuodiBedrockApi/utmsdictionarycs/selectUtmsDictionaryList',
                        method:'post',
                        data:opts
                    })
                },
            }
        }]);
    }
});