define([
    'angular'
], function(
    angular
) {
    return function(app, elem, attrs, scope) {
        app.controller('systemLogStatisticsCtrl', ['$rootScope', '$scope', '$timeout', 'G', 'navsData', 'systemLogStatisticsService', function($rootScope, $scope, $timeout, G, navsData, service) {


            $scope.formatData = function(items){
                var reviewers = [], 
                    userorgpros = [],
                    visibleUsers = [],
                    comments = [],
                    contextObj = {};
                angular.forEach(items, function(item){
                    //创建人
                    item.displayCreateUser = item.createuser?item.createuser.username + '/' + item.createuser.jobcard:'';

                    //点评人
                    reviewers = item.reviewusers;
                    item.displayReviewers = '';
                    angular.forEach(reviewers, function(reviewer){
                        item.displayReviewers += ',' + reviewer.reviewusername + '/' + reviewer.reviewjobcard;
                    });
                    item.displayReviewers = item.displayReviewers.substring(1);

                    //点评内容
                    comments = item.leafworkreport;
                    item.displayComments = '';
                    angular.forEach(comments, function(comment){
                        if(comment.createuser){
                            item.displayComments += comment.createuser.username + '/' + comment.createuser.jobcard + ':';
                        }
                        item.displayComments += comment.context + '<br>';
                    });

                    //抄送人
                    visibleUsers = item.visibleusers;
                    item.displayVisibleUsers = '';
                    angular.forEach(visibleUsers, function(visibleUser){
                        item.displayVisibleUsers += ',' + visibleUser.visibleusername + '/' + visibleUser.visiblejobcard;
                    });
                    item.displayVisibleUsers = item.displayVisibleUsers.substring(1);

                    //组织
                    if(item.createuser&&item.createuser.userorgpros&&item.createuser.userorgpros.length>0){
                        userorgpros = item.createuser.userorgpros;
                        item.displayOrg = userorgpros[0].organizationname;
                    }

                    //日志内容
                    try{
                        contextObj = JSON.parse(item.context);
                        if(contextObj.jrwc){
                            item.displayjrwc = contextObj.jrwc.replace(/\n/g, '<br>');
                        }
                        if(contextObj.wwc){
                            item.displaywwc = contextObj.wwc.replace(/\n/g, '<br>');
                        }
                        if(contextObj.xxt){
                            item.displayxxt = contextObj.xxt.replace(/\n/g, '<br>');
                        }
                    }catch(e){
                        console.warn(e);
                    }

                    //日志类型
                    switch(item.worktype){
                        case 'DAY':
                            item.displayWorktype = '日报';
                            item.display1stLabel = '今日完成工作: ';
                            item.display2ndLabel = '未完成工作: ';
                            item.display3rdLabel = '需协调工作: ';
                            break;
                        case 'WEEK':
                            item.displayWorktype = '周报';
                            item.display1stLabel = '本周完成工作: ';
                            item.display2ndLabel = '未完成工作: ';
                            item.display3rdLabel = '需协调工作: ';
                            break;
                        case 'MONTH':
                            item.displayWorktype = '月报';
                            item.display1stLabel = '本月完成工作: ';
                            item.display2ndLabel = '未完成工作: ';
                            item.display3rdLabel = '需协调工作: ';
                            break;
                        case 'MEETING':
                            item.displayWorktype = '会议';
                            item.display1stLabel = '会议主题: ';
                            item.display2ndLabel = '会议结论: ';
                            item.display3rdLabel = '待办事项: ';
                            break;
                    }
                })
            };

            $scope.$watch('organizationcode', (newVal, oldVal) => {
                if(newVal && newVal !== oldVal && $scope.isInit) {
                    $scope.searchParam.organizationcodes = ($scope.organizationcode === '88888888'?'':$scope.organizationcode);
                    $scope.search();
                }
            });

            $scope.sourceFormatter = function (data) {
                return {
                    value: data.organizationcode,
                    name: data.organizationname
                };
            }

            $scope.setCurrent = function(item){
                $scope.currentItem = item;
            };

            $scope.search = function(){
                $scope.fetch({currPage:1});
            };

            $scope.changeWorkType = function(){
                $scope.searchParam.worktype = $scope.selectWorkType.value;
            };

            $scope.export = function($index) {
                var param = angular.extend({}, $scope.searchParam, {
                    skipCount: 0,
                    pageSize: 10001
                });

                if(!param.datestart){
                    delete param.datestart;
                }
                if(!param.dateend){
                    delete param.dateend;
                }
                if(!param.organizationcodes){
                    delete param.organizationcodes
                }

                service.getWorkReportList(param).then(function(data) {
                    if(data.data.count>10000){
                        alert('导出数据太多，请分段导出');
                    }else if(data.data.count<1){
                        alert('没有数据');
                    }else{
                        if (param.datestart) {param.datestart = param.datestart + ' 00:00:00';}
                        if (param.dateend) {param.dateend = param.dateend + ' 23:59:59';}
                        G.download('/ehuodiBedrockApi/workreportcs/exportWorkReportList', param,'GET');
                    }
                });
            };

            function init(){
                $scope.apiUrl = service.getWorkReportListUrl;
                $scope.isInit = true;
                $timeout(function(){
                    $scope.workType = [
                        {name: '全部', value: ''},
                        {name: '日报', value: 'DAY'},
                        {name: '周报', value: 'WEEK'},
                        {name: '月报', value: 'MONTH'},
                        {name: '会议', value: 'MEETING'}
                    ];
                    $scope.selectWorkType = $scope.workType[0];
                });
            }

            init();
        }]);
    }
});