define([
    'angular',
    '../systemSigninStatistics/signinDetailsDialog.html',
    '../systemSigninStatistics/signinDetailsDialog.css'
], function(
    angular,
    html,css) {
    return function(app, elem, attrs, scope) {
        app.directive('signinDetailsDialog', ['$timeout', function($timeout) {
            return {
                template: html,
                restrict: 'EA',
                replace: true,
                scope: {
                    currentItem: '='
                },
                link: function postLink($scope, $element, $attrs) {

                },

                controller: function($scope,$element,$attrs,$transclude,$log,$http,G){

                    function init(){
                        $timeout(function(){
                            $scope.bigImg = false;
                        });
                    }

                    $scope.changeImg = function(src){
                        $scope.bigImg = !$scope.bigImg;
                        $scope.imgSrc = src;
                    };

                    $('#signinDetailsDialog').on('show.bs.modal',function(){
                        init();
                    });

                }
            };
        }]);
    }
});
