define([
    'angular',
    '../nestableTree/nestableTree.html',
    '../nestableTree/nestableTree.css'
    ], function(
        angular,
        html) {
        return function(app, elem, attrs, scope) {
            app.directive('nestableTree', ['$timeout', 
                function($timeout) {
                    return {
                        template: html,
                        restrict: 'EA',
                        replace: true,
                        scope: {
                            treeData: '=',
                            textField: '@',
                            linkKey: '@',
                            order: '@',
                            itemClicked: '&',
                            defaultItem:'=',
                            updateData:'=',
                            url:'@'
                        },
                        link: function postLink() {

                        },

                        controller: function($scope,$element,$attrs,$transclude,$log,$http,G){

                            $scope.$watch('treeData',function(newValue, oldValue){
                                if(newValue&&oldValue){
                                    $('#nestableTree').data('nestable','');
                                }
                            });

                            $scope.checkChildren = function(item){
                                if(!item.children || item.children.length<1){
                                    return false;
                                }

                                var allChildrenHidden = true;
                                angular.forEach(item.children, function(child){
                                    if(!child.hide){
                                        allChildrenHidden = false;
                                    }
                                });

                                return !allChildrenHidden;
                            }
                        }
                    };
                }]);
    };
});