define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('systemLogStatisticsService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                getWorkReportList: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/workreportcs/queryWorkReportList',
                        method: 'post',
                        data: opts
                    });
                },
                getWorkReportListUrl: '/ehuodiBedrockApi/workreportcs/queryWorkReportList',

                queryWorkReport: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/workreportcs/queryWorkReport',
                        method: 'post',
                        data: opts
                    });
                }
            };
        }]);
    }
});