define([
    'angular',
    '../addNewRole/addNewRoleService',
    '../addNewRole/addNewRoleDirective.html',
    '../addNewRole/addNewRoleDialog.css',
    ], function(
        angular,
        service,
        html) {
        return function(app, elem, attrs, scope) {
            service(app, elem, attrs, scope);
            app.directive('addNewRoleDirective', ['addNewRoleService','G',function(service,G) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        editFlag: '=',
                        current: '='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude, $log, $http, G) {

                        $('#addNewRole').on('show.bs.modal',function(){
                            $scope.$apply(function(){
                                $scope.errors = {};
                                $scope.roleName = $scope.editFlag?$scope.current.rolename:'';
                                G.clearAllError();
                            })
                        });

                        $scope.submit=function() {
                            if($scope.editFlag){
                                service.updateRbacRole({
                                    ehdrbacroleid: $scope.current.ehdrbacroleid,
                                    rolename:$scope.roleName
                                }).then(function(data){
                                    if(data.data.result=='success'){
                                        G.alert('修改角色成功!');
                                        $('#addNewRole').modal('hide');
                                        $scope.$emit('refresh');
                                    }else{
                                        $scope.errors.data = data.data.msg;
                                    }

                                })
                            }else{
                                service.insertRbacRole({
                                    rolename:$scope.roleName
                                }).then(function(data){
                                    if(data.data.result=='success'){
                                        G.alert('新建角色成功!');
                                        $('#addNewRole').modal('hide');
                                        $scope.$emit('refresh');
                                    }else{
                                        $scope.errors.data = data.data.msg;
                                    }

                                })
                            }
                            
                        }

                    }
                };
            }]);
        }
    });