define([
    'angular',
    '../addNewStaff/addNewStaffService',
    '../directiveFormInput/formSelect',
    '../directiveFormInput/bcFormSelectMulti',
    'scmsModules/multiselect/multiselect',
    '../addNewStaff/addNewStaff.html',
    '../addNewStaff/addNewStaff.css'
    ], function(
        angular,
        addNewStaffService,
        formSelect,
        bcFormSelectMulti,
        multiselect,
        html) {
        return function(app, elem, attrs, scope) {
            addNewStaffService(app, elem, attrs, scope);
            formSelect(app, elem, attrs, scope);
            bcFormSelectMulti(app, elem, attrs, scope);
            multiselect(app, elem, attrs, scope);
            app.directive('addNewStaffDirective', ['addNewStaffService','G', '$q', '$timeout', function(service,G,$q,$timeout) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        ehdrbacusersid:'=',
                        currStruct:'=',
                        opType:'=',
                        userProfessionMap: '=',
                        permission:'=',
                        newJobcard:'='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude,$timeout, $log, $http, G) {

                        function initForm(){
                            $scope.errors = {};
                            $scope.formData = {
                                dept: [],
                                market: [],
                                roles: []
                            };
                            $scope.selectedPosition = {};
                            $scope.userInfo = G.getUserInfo();

                            var promise = getDept().then(function(){
                                var newPromise = getMarket();
                                return newPromise;
                            }).then(function(){
                                var newPromise = getAllOrg();
                                return newPromise;
                            }).then(function(){
                                var newPromise = getRoleList($scope.userInfo.ehdrbacroleids);
                                return newPromise;
                            }).then(function(){
                                var newPromise = getOrganization();
                                return newPromise;
                            }).then(function(){
                                var newPromise = getPositionList();
                                return newPromise;
                            });
                            return promise;
                        }

                        $('#addNewStaff').on('show.bs.modal', function (e) {
                            $scope.$apply(function(){
                                G.loading(true,{
                                    $container: $element.find('.modal-content')
                                });
                                initForm().then(function(){
                                    if($scope.opType==='edit') {
                                        $scope.formData.title='修改';
                                        service.selectUser({
                                            ehdrbacusersids:$scope.ehdrbacusersid,
                                            resultstyle: '2',
                                            ispermission: 1
                                        }).then(function(data){
                                            if(data.data.result==='success') {
                                                data=data.data.data[0];
                                                $scope.formData.dataStore = data;
                                                $scope.formData.jobcard = data.jobcard;
                                                $scope.formData.username = data.username;
                                                $scope.formData.usertelephonenumber = data.usermobilenumber;
                                                formateFormList();
                                            }
                                        },function(){
                                            G.loading(false,{
                                                $container: $element.find('.modal-content')
                                            });
                                        })
                                    }else{
                                        $scope.formData.title='新增';
                                        G.loading(false,{
                                            $container: $element.find('.modal-content')
                                        });
                                    }
                                });
                                
                            })
                        })
                        
                        $('#addNewStaff').on('hide.bs.modal',function(e){    
                            G.clearAllError();
                        });

                        function formateFormList(){
                            if($scope.formData.dataStore){
                                $scope.formData.userprofession = getMatchItem($scope.formData.dataStore.userprofession,'dictionarycode',$scope.userprofessions);
                                $scope.formData.roles = getMatchArray($scope.formData.dataStore.ehdrbacroleids,'ehdrbacroleid',$scope.roleList);
                                $scope.formData.dept = getMatchArray($scope.formData.dataStore.ehdrbacdepartmentids,'ehdrbacdepartmentid',$scope.deptList);
                                $scope.formData.market = getMatchArray($scope.formData.dataStore.ehdrbacmarketplaceids,'ehdrbacmarketplaceid',$scope.marketList);
                                $scope.formData.structGroup = getMatchOrg($scope.formData.dataStore.userorgpros);

                                angular.forEach($scope.positionList, function(position){
                                    if($scope.formData.dataStore.postnames===position.dictionaryname){
                                        $scope.selectedPosition = position;
                                    }
                                });

                                if($scope.formData.dataStore.ehdrbacroleids){
                                   var ownRoleids = G.clone($scope.formData.dataStore.ehdrbacroleids).split(',');
                                    angular.forEach($scope.roleList, function(role){
                                        if(ownRoleids.indexOf(role.ehdrbacroleid+'')>-1){
                                            ownRoleids.splice(ownRoleids.indexOf(role.ehdrbacroleid+''),1);
                                        }
                                    });
                                    if(ownRoleids.length){
                                        service.getRoleList({
                                            ehdrbacroleids: ownRoleids.join(',')
                                        }).then(function(data){
                                            var ownRoleList = data.data.data;
                                            angular.forEach(ownRoleList, function(item){
                                                item.disabled = true;
                                            });
                                            $scope.formData.roles = $scope.formData.roles.concat(ownRoleList);
                                            G.loading(false,{
                                                $container: $element.find('.modal-content')
                                            });
                                        },function(){
                                            G.loading(false,{
                                                $container: $element.find('.modal-content')
                                            });
                                        })
                                    }else{
                                        G.loading(false,{
                                            $container: $element.find('.modal-content')
                                        });
                                    } 
                                }else{
                                    G.loading(false,{
                                        $container: $element.find('.modal-content')
                                    });
                                }
                            }
                        }

                        $scope.addOneOrg = function(){
                            if($scope.formData&&$scope.formData.structGroup.length<5){
                                $scope.formData.structGroup.push({
                                    struct: G.clone($scope.currStruct),
                                    userprofession: ''
                                }); 
                            }else{
                                G.alert('最多关联5个组织',{type:'error'});
                            }
                        };

                        $scope.deleteOneOrg = function(index){
                            $scope.formData.structGroup.splice(index, 1);
                        };

                        $scope.changeOrg = function(item){
                            item.userprofessions = getProfession(item.struct.organizationtype);
                            item.userprofessionList = [];
                            item.selectedUserprofession = [];
                            angular.forEach(item.userprofessions, function(profession){
                                item.userprofessionList.push({
                                    name: profession.dictionaryname,
                                    code: profession.dictionarycode
                                });
                            });
                        };

                        function getRoleList(roleStr){
                            var deferred = $q.defer(),
                                promise = deferred.promise;
                            service.getRoleList({
                                ehdrbacroleids: roleStr
                            }).then(function(data){
                                $scope.roleList=data.data.data;
                                deferred.resolve();
                            })
                            return promise;
                        }

                        function getProfession(organizationtype){
                            var result;
                            angular.forEach($scope.userProfessionMap,function(item){
                                if(item.key===organizationtype){
                                    result = item.value;
                                }
                            });
                            return result;
                        }
                        function getDept(){
                            var deferred = $q.defer(),
                                promise = deferred.promise;
                            service.getAllDept({
                                organizationcode:$scope.currStruct.organizationcode
                            }).then(function(data){
                                $scope.deptList=data.data.data;
                                deferred.resolve();
                            })
                            return promise;
                        }

                        function getAllOrg() {
                            var deferred = $q.defer(),
                                promise = deferred.promise;
                            service.getAllOrg({
                                organizationcode:G.userInfo.organizationcode,
                                returnformat:2
                            }).then(function(data){
                                $scope.structList=data.data.data;
                                if($scope.opType!=='edit') {
                                    $scope.currentStruct=getCurrStruct($scope.currStruct);
                                    var item = {
                                        struct: $scope.currentStruct,
                                        userprofessions: getProfession($scope.currentStruct.organizationtype),
                                        userprofessionList: []
                                    };
                                    angular.forEach(item.userprofessions, function(profession){
                                        item.userprofessionList.push({
                                            name: profession.dictionaryname,
                                            code: profession.dictionarycode
                                        });
                                    });
                                    $scope.formData.structGroup = [item];
                                }
                                deferred.resolve();
                            })
                            return promise;
                        }

                        function getPositionList(){
                            var deferred = $q.defer(),
                                promise = deferred.promise;
                            service.selectDictionaryList({
                                parentdictionarycode: 'position'
                            }).then(function(data){
                                $scope.positionList = data.data.data;
                                deferred.resolve();
                            });
                            return promise;
                        }

                        function getOrganization() {
                            var deferred = $q.defer(),
                                promise = deferred.promise;
                                service.getAllOrg({
                                    organizationcode:'88888888',
                                    returnformat:2
                                }).then(function(data){
                                $scope.organizationList=data.data.data;
                                deferred.resolve();
                            })
                            return promise;
                        }

                        function getCurrStruct(struct){
                            var result = {};
                            angular.forEach($scope.structList, function(item){
                                if(item.organizationcode===struct.organizationcode){
                                    result = item;
                                }
                            });
                            return result;
                        }

                        function getMarket(){
                            var deferred = $q.defer(),
                                promise = deferred.promise;
                            service.getMarketList({
                                organizationcode:$scope.currStruct.organizationcode
                            }).then(function(data){
                                $scope.marketList=data.data.data;
                                deferred.resolve();
                            })   
                            return promise; 
                        }

                        $scope.$watch('formData.struct',function(newVal,oldVal){
                            if(newVal&&oldVal){
                                getDept();
                                getMarket();
                                $scope.formData.dept=[];
                                $scope.formData.market=[];
                            }
                        },true);

                        function getMatchItem(selected,param,list){
                            var result;
                            angular.forEach(list, function(item,index){
                                if(item[param]==selected) {
                                    result = item;
                                }
                            })
                            return result;
                        }

                        function getMatchOrg(userorgpros){
                            var result = [],
                                professions = [],
                                findOwn,
                                userprofessionList = [],
                                selectedProfession = {},
                                tempProfession = {};

                            angular.forEach(userorgpros, function(userProfession){
                                findOwn = false;
                                angular.forEach($scope.structList, function(struct){
                                    if(struct.organizationcode===userProfession.organizationcode){
                                        professions = getProfession(struct.organizationtype);
                                        findOwn = true;
                                        userprofessionList = [];
                                        angular.forEach(professions,function(profession){
                                            tempProfession = G.clone(profession);
                                            tempProfession.name = profession.dictionaryname;
                                            tempProfession.code = profession.dictionarycode;
                                            userprofessionList.push(tempProfession);
                                            if(userProfession.professioncode===profession.dictionarycode){
                                                selectedProfession = tempProfession;
                                                selectedProfession.isChecked = true;
                                            }
                                        });
                                        if(result.length<1){
                                            result.push({
                                                struct: struct,
                                                userprofessionList: userprofessionList,
                                                selectedUserprofession: [selectedProfession]
                                            });
                                        }else{
                                            if(result[result.length-1].struct.organizationcode===userProfession.organizationcode){
                                                result[result.length-1].selectedUserprofession.push(selectedProfession);
                                            }else{
                                                result.push({
                                                    struct: struct,
                                                    userprofessionList: userprofessionList,
                                                    selectedUserprofession: [selectedProfession]
                                                });
                                            }
                                        }
                                    }
                                });
                                if(!findOwn){
                                    angular.forEach($scope.organizationList, function(struct){
                                        if(struct.organizationcode===userProfession.organizationcode){
                                            professions = getProfession(struct.organizationtype);
                                            angular.forEach(professions,function(profession){
                                                if(userProfession.professioncode===profession.dictionarycode){
                                                    if(result.length<1){
                                                        result.push({
                                                            struct: struct,
                                                            userprofessionStr: profession.dictionaryname,
                                                            userprofessionCode: profession.dictionarycode,
                                                            isOwn: true
                                                        });
                                                    }else{
                                                        if(result[result.length-1].struct.organizationcode===userProfession.organizationcode){
                                                            result[result.length-1].userprofessionStr += ',' + profession.dictionaryname;
                                                            result[result.length-1].userprofessionCode += ',' + profession.dictionarycode;
                                                        }else{
                                                            result.push({
                                                                struct: struct,
                                                                userprofessionStr: profession.dictionaryname,
                                                                userprofessionCode: profession.dictionarycode,
                                                                isOwn: true
                                                            });
                                                        }
                                                    }
                                                }
                                            });
                                        }
                                    });
                                }
                            });
                            return result;
                        }

                        function getMatchArray(str,field,lists){
                            if(!str){
                                return '';
                            }
                            var arr=str.split(','),
                            result=[];
                            angular.forEach(arr, function(item){
                                angular.forEach(lists,function(checkedItem){
                                    if(checkedItem[field]==item){
                                        result.push(checkedItem);
                                    }
                                })
                            })
                            return result;
                        }

                        function arrayToString(arr,feild) {
                            if(!arr.length){
                                return;
                            }
                            var ret='';
                            for(var i=0,len=arr.length;i<len;i++) {
                                if(arr[i][feild]){
                                    ret=ret+','+arr[i][feild];    
                                }
                            }
                            return ret.slice(1);
                        }

                        function isDuplicated(array){
                            var i;
                            if(array){
                                array.sort();
                            }
                            for (i = 0; i < array.length; i++) {
                                if(array[i]&&array[i]===array[i+1]){
                                    return true;
                                }
                            }
                            return false;
                        }

                        function checkProfession(array){
                            var result = false;
                            angular.forEach(array, function(item){
                                if((!item.selectedUserprofession||item.selectedUserprofession.length<1) && !item.isOwn){
                                    result = true;
                                }
                            });
                            return result;
                        }

                        $scope.submit=function(){
                            var structArray = [];
                            angular.forEach($scope.formData.structGroup,function(item){
                                structArray.push(item.struct.organizationcode);
                            });
                            if(checkProfession($scope.formData.structGroup)){
                                $scope.errors.org = '请选择工种';
                                return;
                            }
                            if(!$scope.selectedPosition.dictionarycode){
                                $scope.errors.position = '请选择职位';
                                return;
                            }
                            if(isDuplicated(structArray)){
                                $scope.errors.org = '组织不能重复';
                                return;
                            }
                            var userorgpros = [],
                                orderIndex = 0,
                                codeArray = [],
                                nameArray = [];

                            angular.forEach($scope.formData.structGroup, function(item){
                                if(item.isOwn){
                                    codeArray = item.userprofessionCode.split(',');
                                    nameArray = item.userprofessionStr.split(',');
                                    angular.forEach(codeArray, function(code, index){
                                        userorgpros.push({
                                            ordernumber: ++orderIndex,
                                            organizationcode: item.struct.organizationcode,
                                            organizationname: item.struct.organizationname,
                                            professioncode: code,
                                            professionname: nameArray[index]
                                        });
                                    });
                                }else{
                                    angular.forEach(item.selectedUserprofession, function(profession){
                                        userorgpros.push({
                                            ordernumber: ++orderIndex,
                                            organizationcode: item.struct.organizationcode,
                                            organizationname: item.struct.organizationname,
                                            professioncode: profession.code,
                                            professionname: profession.name
                                        });
                                    });
                                }
                                
                            });

                            var param = {
                                username:$scope.formData.username,
                                usermobilenumber:$scope.formData.usertelephonenumber,
                                ehdrbacdepartmentids:arrayToString($scope.formData.dept,'ehdrbacdepartmentid'),
                                departmentnames: arrayToString($scope.formData.dept,'departmentname'),
                                ehdrbacmarketplaceids:arrayToString($scope.formData.market,'ehdrbacmarketplaceid'),
                                marketplacenames:arrayToString($scope.formData.market,'marketplacename'),
                                ehdrbacroleids:arrayToString($scope.formData.roles,'ehdrbacroleid'),
                                ehdrbacrolenames: arrayToString($scope.formData.roles,'rolename'),
                                userorgpros: JSON.stringify(userorgpros),
                                postnames: $scope.selectedPosition.dictionaryname,
                                ehdrbacpostids: $scope.selectedPosition.dictionarycode
                            };

                            if($scope.opType==='edit'){
                                param.ehdrbacusersid = $scope.ehdrbacusersid;
                                service.updateUser(param).then(function(data){
                                    if(data.data.result=='success'){
                                        if($scope.ehdrbacusersid===G.userInfo.ehdrbacusersid){
                                            G.userInfo.username = $scope.formData.username;
                                            G.userInfo.usermobilenumber = $scope.formData.usertelephonenumber;
                                        }
                                        $('#addNewStaff').modal('hide');
                                        G.alert('修改员工成功!');
                                        $scope.$emit('refresh');
                                    }else{
                                        G.alert(data.data.msg,{type:'error',$container:$element.find('.modal-content')});
                                    }
                                })
                            }else{
                                service.insertUser(param).then(function(data){
                                    if(data.data.result=='success'){
                                        $scope.newJobcard = data.data.data;
                                        $('#addNewStaff').modal('hide');
                                        $('#addNewStaffSuccess').modal('show');
                                    }else{
                                        G.alert(data.data.msg,{type:'error',$container:$element.find('.modal-content')});
                                    }
                                })
                            }
                        }
                    }
                };
            }]);
    }
});