define([
    './addBasicPreloadCtrl',
    './addBasicPreloadService',
    './addBasicPreload.html',
    './addBasicPreload.css'
], function (
    ctrl,
    service,
    html) {
        return function (app, elem, attrs, scope) {
            ctrl(app, elem, attrs, scope);
            service(app, elem, attrs, scope);
            elem.append(html);
        }
    });