define([
    '../systemOrgMgmt/systemOrgMgmtCtrl',
    '../systemOrgMgmt/systemOrgMgmtService',
    '../addNewStruct/addNewStructDirective',
    '../addNewCity/addNewCityDirective',
    '../chooseOrg/chooseOrgDirective',
    '../basicSelectCityGroup/basicSelectCityGroupDirective',
    '../treeView/treeView',
    'scmsModules/tooltip/tooltip',
    '../nestableTree/nestableTree',
    '../confirmDialog/confirmDialog',
    '../systemOrgMgmt/systemOrgMgmt.html',
    '../systemOrgMgmt/systemOrgMgmt.css'
], function(
    ctrl,
    service,
    addNewStructDirective,
    addNewCityDirective,
    chooseOrgDirective,
    basicSelectCityGroupDirective,
    treeView,
    tooltip,
    nestableTree,
    confirmDialog,
    html) {
    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        addNewStructDirective(app, elem, attrs, scope);
        addNewCityDirective(app, elem, attrs, scope);
        chooseOrgDirective(app, elem, attrs, scope);
        basicSelectCityGroupDirective(app, elem, attrs, scope);
        treeView(app, elem, attrs, scope);
        tooltip(app, elem, attrs, scope);
        nestableTree(app, elem, attrs, scope);
        confirmDialog(app, elem, attrs, scope);
        elem.append(html);
    }
});