define([
    'angular'
    ], function(
        angular
        ) {
        return function(app, elem, attrs, scope) {
            app.controller('systemOrgMgmtCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'systemOrgMgmtService', 'navsData',
                function($rootScope, $scope, $state, $stateParams, $timeout, G, service, navsData) {

                function init(defaultItem){
                    $scope.permission = navsData.permission;
                    $scope.showGuide=false;
                    $scope.adjustBtn = '调整层级';
                    $scope.editFlag = false;
                    $scope.lists = [];
                    G.loading(true);
                    service.selectRbacOrganizationByParam().then(function(data){
                        $scope.lists = data.data.data;
                        $scope.orgList = G.clone($scope.lists);
                        if($scope.orgList.length===0){
                            $scope.showGuide=true;
                            return;
                        }
                        $scope.orgList=G.toTree($scope.orgList,'0','parorganizationcode','organizationcode');
                        if(defaultItem){
                            var item = G.searchTree($scope.orgList,defaultItem.organizationname,'organizationname');
                            $scope.defaultItem =G.clone(defaultItem);
                        }else{
                            $scope.defaultItem=G.clone($scope.orgList);
                        }
                        var updateData=item|| $scope.orgList;
                        $scope.updateRight(updateData);
                        $scope.orgList=new Array($scope.orgList);
                        G.loading(false);
                    });
                }
                $scope.updateRight=function($item){
                    $scope.detail=$item;
                    $scope.subStruc=$item.children;
                }

                $scope.editDetail=function(item,type){
                    $scope.selectedItem=item;
                    $scope.opType='edit';
                }

                $scope.adjustOrderBtn = function(){
                    if($scope.editFlag===false){
                        $('#nestableTree').nestable({
                            group: 1,
                            maxDepth: 10
                        }).on('change', updateOutput);
                        $scope.editFlag = true;
                        $scope.adjustBtn = '取消';
                    }else{
                        $scope.editFlag = false;
                        $scope.adjustBtn = '调整层级';
                    }
                };

                $scope.$on('confirmChange',function(){
                    updateOrder($scope.posObj);
                    $scope.editFlag = false;
                    $scope.adjustBtn = '调整层级';
                });

                $scope.$on('cancelChange',function(){
                    $scope.editFlag = false;
                    $scope.adjustBtn = '调整层级';
                    init($scope.detail);
                })

                function updateOutput(e, posObj){
                    var currentItem = {};
                    $scope.posObj = posObj;
                    angular.forEach($scope.lists, function(item){
                        if(posObj.id==item.organizationcode){
                            currentItem = item;
                        }
                    })
                    $scope.confirmMsg = currentItem;
                    if(!posObj.parentId){
                        G.alert('禁止修改根节点',{type:'error'});
                        init($scope.detail);
                    }
                    if(posObj.hasChange&&posObj.parentId){
                        $('#confirmDialog').modal({
                            backdrop: 'static'
                        });
                    }
                }

                function updateOrder(posObj){
                    var ordernumber = 0,
                        params = {},
                        newOrderNum = (new Date().getTime()+'').substr(-8);
                    if(!posObj.prevOrder){
                        if(posObj.nextOrder){
                            ordernumber = posObj.nextOrder > 3600000?posObj.nextOrder - 3600000:Math.floor(posObj.nextOrder / 2);
                        }else{
                            ordernumber = Number(newOrderNum);
                        }
                    }else if(!posObj.nextOrder){
                        ordernumber = posObj.prevOrder + 3600000;
                    }else{
                        ordernumber = Math.floor((posObj.nextOrder + posObj.prevOrder) / 2);
                    }
                    params.ehdrbacorganizationid = $scope.confirmMsg.ehdrbacorganizationid;
                    params.organizationcode = posObj.id;
                    params.parorganizationcode = posObj.parentId;
                    params.ordernumber = ordernumber;
                    updateRbacOrganization(params);
                }

                function updateRbacOrganization(params){
                    service.updateRbacOrganization(params).then(function(data) {
                        data = data.data;
                        if(data.result === 'success') {
                            G.alert('修改成功');
                            init($scope.detail);
                        }else {
                            init($scope.detail);
                            G.errorAlert('修改失败', data)
                        }
                    },function(data) {
                        G.errorAlert('修改失败', data)
                    });
                }

                $scope.$on('refresh',function(){
                    init($scope.detail);
                });

                $scope.remove=function($event,item,type) {
                    var $alert = G.confirm({
                        $target: $($event.target),
                        msg: '您确定删除该组织架构？',
                        position: 'bottom',
                        submit: function() {    
                            service.updateRbacOrganization({
                                ehdrbacorganizationid:item,
                                organizationstatus:9
                            }).then(function(data) {
                                data = data.data;
                                if(data.result === 'success') {
                                    $alert.hide();
                                    G.alert('组织架构删除成功');
                                    init();
                                }else {
                                    G.alert(data.msg || '组织架构删除失败', {
                                        type: 'error'
                                    });
                                    $alert.hide();
                                }
                            },
                            function() {
                                G.alert('组织架构删除失败', {
                                    type: 'error'
                                });
                                $alert.hide();
                            });
                        }    
                    });
                }

                init();
            }]);

        }
    });