define([
    '../systemMonitorDetail/systemMonitorDetailCtrl',
    '../systemMonitorDetail/systemMonitorDetailService',
    'scmsModules/pagination/paginationDirective',
    'scmsModules/table/table',
    'scmsModules/datePicker/datePickerDirective',
    '../systemMonitorDetail/systemMonitorDetail.html',
    '../systemMonitorDetail/systemMonitorDetail.css'
], function (
    ctrl,
    service,
    paginationDirective,
    table,
    datePickerDirective,
    html) {
        return function (app, elem, attrs, scope) {
            ctrl(app, elem, attrs, scope);
            service(app, elem, attrs, scope);
            paginationDirective(app, elem, attrs, scope);
            table(app, elem, attrs, scope);
            datePickerDirective(app, elem, attrs, scope);
            elem.append(html);
        }
    });