define([
    'angular',
    '../roleTreeView/roleTreeView.html',
    '../roleTreeView/roleTreeView.css'
    ], function(
        angular,
        html) {
        return function(app, elem, attrs, scope) {
            app.directive('roleTreeView', ['$timeout', 
                function($timeout) {
                    return {
                        template: html,
                        restrict: 'EA',
                        replace: true,
                        scope: {
                            treeData: '=',
                            textField: '@',
                            linkKey: '@',
                            itemClicked: '&',
                            defaultItem:'=',
                            updateData:'='
                        },
                        link: function postLink() {

                        },

                        controller: function($scope,$element,$attrs,$transclude,$log,$http,G){

                            $scope.itemExpended = function(item, $event){
                                item.$$isExpend = !item.$$isExpend;
                                $event.stopPropagation();
                            };

                            $scope.checked = function(item){
                                var parent = {},
                                    self = item;
                                self.ownedbyrole = self.ownedbyrole===0?1:0;
                                if(self.ownedbyrole){
                                    while(self.parentObj){
                                        parent = self.parentObj;
                                        parent.ownedbyrole = 1;
                                        self = parent;
                                    }
                                }
                                checkChildren(item);
                            };

                            function checkChildren(item){
                                angular.forEach(item.children,function(child){
                                    child.ownedbyrole = item.ownedbyrole;
                                    checkChildren(child);
                                });
                            }

                            $scope.getItemIcon = function(item){
                                checkIsLeaf(item);
                                if(item.isLeaf||item.isAllButton){
                                    return '';
                                }
                                return item.$$isExpend ? 'fa fa-minus': 'fa fa-plus';   
                            };

                            function checkIsLeaf(item){
                                item.isLeaf = false;
                                if(!item.children || !item.children.length){
                                    item.isLeaf = true;
                                }else if(item.children){
                                    checkChildrenType(item);
                                }
                            }

                            function checkChildrenType(item){
                                var isAllButton = true,
                                    hasButton = false;
                                angular.forEach(item.children, function(child){
                                    if(child.resourcetype===0){
                                        isAllButton = false;
                                    }
                                    if(child.resourcetype===1){
                                        hasButton = true;
                                    }
                                });
                                item.isAllButton = isAllButton;
                                item.hasButton = hasButton;
                            }

                            function expandBaner(tree,value,field,addfield,addValue) {
                                if(tree[field]===value){
                                    tree[addfield]=addValue;
                                    tree.active = true;   
                                }
                                if(tree.children && tree.children.length>0){
                                    for(var i=0,len=tree.children.length;i<len;i++){
                                        expandBaner(tree.children[i],value,field,addfield,addValue);
                                        if(tree.children[i][addfield]===addValue){
                                            tree[addfield]=addValue;
                                        }
                                    }
                                }
                            }

                            $timeout(function(){
                                addActive();
                            },60);

                            $scope.$watch('defaultItem',function(){
                                $timeout(function(){
                                    addActive();
                                },60);
                            })

                            $scope.$watch('updateData',function(){
                                $timeout(function(){
                                    addActive();
                                },60);
                            })

                            function addActive() {
                                if($scope.defaultItem){
                                    angular.forEach($scope.treeData, function(item,index){
                                        expandBaner(item,$scope.defaultItem[$scope.linkKey],$scope.linkKey,'$$isExpend',true);
                                    })
                                }else{
                                    angular.element('.tree-view span:eq(0)').addClass('active');                                    
                                }
                            }

                            $scope.warpCallback = function(callback, item, $event){
                                angular.element('.tree-view span').removeClass('active');
                                angular.element($event.target).addClass('active');

                                ($scope[callback] || angular.noop)({
                                    $item:item,
                                    $event:$event
                                })};
                            }
                        };
                    }]);
};
});