import './index.less';
export default (app, elem, attrs, scope) => {
  app.controller(
    'activityHandleCtrl',
    ['$scope', '$state', '$stateParams','annualPartyService','G', ($scope, $state, $stateParams,service,G) => {
      const {gameActivityId,jobCard} = $stateParams;
      $scope.getLevel = lv => {
        const levelMap = {
          0: '简单',
          1: '中等',
          2: '复杂'
        };
        return levelMap[lv];
      };

      $scope.setClass = index=>{
        let clazz = '';
        if($scope.currentQuestionNum==index+1){
          clazz='qs-current';
        }else if($scope.currentQuestionNum>index+1){
          clazz='qs-did';
        }else if($scope.currentQuestionNum<index+1){
          clazz='qs-nodid';
        }
        return clazz;
      };

      $scope.isFetching = false;

      $scope.sendQuestion = ()=>{
        if($scope.status===0&&!$scope.isFetching){
          $scope.isFetching = true;
          service.question.send({}).then(res=>{
            $scope.isFetching = false;
            if(res&&res.data&&res.data.result=='success'){
              G.alert(res.data.msg || '发送成功');
              fetchQuestionList();
            }else{
              G.alert(res.data.msg || '发送失败', { type: 'error' });
            }
          },res=>{
            $scope.isFetching = false;
            G.alert(res.data.msg || '发送失败', { type: 'error' });
          });
        }
      };

      $scope.endQuestion = (event)=>{
        let isFetching = false;
        if($scope.status===1){
          var $alert = G.confirm({
            $target: $(event.target),
            msg: '确认公布答案吗？',
            position: 'bottom',
            submit: function() {
              G.showWait();
              if(!isFetching){
                isFetching = true;
                service.question.end({
                  // jobCard,
                  // gameQuestionId:id
                }).then(res=>{
                  isFetching = false;
                  G.hideWait();
                  $alert.hide();
                  if(res&&res.data&&res.data.result=='success'){
                    G.alert(res.data.msg || '结束成功');
                    fetchQuestionList();
                  }else{
                    G.alert(res.data.msg || '网络异常', { type: 'error' });
                  }
                },res=>{
                  isFetching = false;
                  G.hideWait();
                  $alert.hide();
                  G.alert(res.data.msg || '网络异常', { type: 'error' });
                });
              }

            }
          });
        }

      };

      $scope.endGame = ()=>{
        const info = '<p>你确定要结束活动吗？结束活动后，将计算奖金分配并在移动风豹<br>暂停活动。请确保当前已满足结束活动的条件！</p>';
        $.alert(info, {
          type: 'confirm',
          callback: function () {
            service.activity.end({
              gameActivityId:gameActivityId
            }).then(res=>{
              if(res&&res.data&&res.data.result=='success'){

                G.alert(res.data.msg || '结束成功');
                $state.go('annualParty.activityList');
              }else{
                G.alert(res.data.msg || '网络异常', { type: 'error' });
              }
            },res=>{
              G.alert(res.data.msg || '网络异常', { type: 'error' });
            });
          }
        });
      };


      // $scope.sendQuestion()
      fetchQuestionList();

      function fetchQuestionList(){
        G.loading(true);
        service.activity.getCurrent({gameActivityId}).then(res=>{
          G.loading(false);
          res = res.data;
          if(res.result=='success'){
            const {questionList,currentQuestionNum,status,surviveCount,eliminationCount} =res.data;
            $scope.items = questionList;
            $scope.currentQuestionNum = currentQuestionNum||1;
            $scope.status = status;
            $scope.surviveCount = surviveCount;
            $scope.eliminationCount = eliminationCount;
          }else{
            $scope.items=[];
            G.alert(res.msg || '网络异常', { type: 'error' });
          }
        },res=>{
          G.loading(false);
          G.alert(res.data.msg || '网络异常', { type: 'error' });
        });
      }


    }]
  );
};