define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('roleAuthorityService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                selectRbacRoleById:function(opts){
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacrolecs/selectRbacRoleById',
                        method: 'post',
                        data:opts,
                        test: 'basicConfigWeb/basicConfigModules/systemRoleMgmt/selectRbacRoleById.json'//假数据api
                    });                    
                }, 
                updateRbacRole:function(opts){
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacrolecs/updateRbacRole',
                        method: 'post',
                        data:opts,
                        test: 'basicConfigWeb/basicConfigModules/header/success.json'//假数据api
                    });                    
                }
            };
        }]);
    }
});