import ctrl from './activityEdit/index';
import service from './common/service';
import addQuestionDirective from './activityEdit/addQuestion';
import html from './activityEdit/index.html';

module.exports = (app, elem, attrs, scope) => {
  ctrl(app, elem, attrs, scope);
  service(app, elem, attrs, scope);
  addQuestionDirective(app, elem, attrs, scope);
  elem.append(html);
};