define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('addNewDatadirService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {       
                   insertPubDictionary: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdpubdictionarycs/insertPubDictionary',
                        method: 'post',
                        data:opts,
                        noconcurrent: true,
                        test: 'basicConfigModules/header/success.json'//假数据api
                    });
                },  
                updatePubDictionary: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdpubdictionarycs/updatePubDictionary',
                        method: 'post',
                        data:opts,
                        noconcurrent: true,
                        test: 'basicConfigModules/addNewDatadir/addNewDatadir.json'//假数据api
                    });
                }
            };
        }]);
    }
});