define('basicConfigModules/discoveryPreload/discoveryPreload',[
  './discoveryPreloadCtrl',
  './discoveryPreloadService',
  './discoveryDetailDriective',
  'scmsModules/table/table',
  './discoveryPreload.html',
  './discoveryPreload.css'
], function(ctrl, service,discoveryDetailDriective,table, html) {
  return function(app, elem, attrs, scope) {
    ctrl(app, elem, attrs, scope);
    service(app, elem, attrs, scope);
    discoveryDetailDriective(app,elem,attrs,scope);
    table(app,elem,attrs, scope);
    elem.append(html);
  };
});