define('basicConfigModules/systemLogConfig/systemLogConfig', [
    '../systemLogConfig/systemLogConfigCtrl',
    'scmsModules/table/table',
    '../updateSystemLog/updateSystemLogDirective',
    '../systemLogConfig/systemLogConfigService',
    '../systemLogConfig/systemLogConfig.css',
    '../systemLogConfig/systemLogConfig.html'
], function(
    ctrl,
    table,
    updateSystemLogDirective,
    service,
    css,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        table(app, elem, attrs, scope);
        updateSystemLogDirective(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        elem.append(html);
    }
});