define([
    'angular'
], function(
    angular
) {
    return function(app, elem, attrs, scope) {
        app.controller('roleAuthorityCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'roleAuthorityService', 'navsData', 
            function($rootScope, $scope, $state, $stateParams, $timeout, G, service, navsData) {
            
            function init(){
                $scope.tableType = 'bc';
                selectRbacRoleById();
            }

            function selectRbacRoleById(){
                service.selectRbacRoleById({
                    ehdrbacroleid: $stateParams.ehdrbacroleid,
                    returnformat: 3
                }).then(function(data){
                    if(data&&data.data&&data.data.result==='success'){
                        $scope.resources = data.data.data.resources;
                        $scope.webMenus = [];
                        $scope.mobileMenus = [];
                        angular.forEach($scope.resources, function(menu){
                            menu.$$isExpend = true;
                            if(menu.applicationflag==='BG'){
                                $scope.webMenus.push(menu);
                            }else if(menu.applicationflag==='AP'){
                                $scope.mobileMenus.push(menu);
                            }
                        });
                        $scope.webMenus.push(
                            {parentresourceid:'TOP', ehdrbacresourceid:0, resourcename:'货嘀菜单', resourcetype:0, resourceurl:';', ownedbyrole:1, $$isExpend:true}
                        );
                        $scope.mobileMenus.push(
                            {parentresourceid:'ROOT', ehdrbacresourceid:'MOBILE', resourcename:'移动风豹', resourcetype:0, resourceurl:';', ordernumber:0, $$isExpend:true},
                            {parentresourceid:'MOBILE', ehdrbacresourceid:1, resourcename:'首页', resourcetype:0, resourceurl:';', ordernumber:0, $$isExpend:true},
                            {parentresourceid:'MOBILE', ehdrbacresourceid:2, resourcename:'工作', resourcetype:0, resourceurl:';', ordernumber:1, $$isExpend:true},
                            {parentresourceid:'MOBILE', ehdrbacresourceid:3, resourcename:'业务', resourcetype:0, resourceurl:';', ordernumber:2, $$isExpend:true},
                            {parentresourceid:'MOBILE', ehdrbacresourceid:4, resourcename:'我的', resourcetype:0, resourceurl:';', ordernumber:3, $$isExpend:true}
                        );
                        $scope.webMenus = G.toTree($scope.webMenus,'TOP','parentresourceid','ehdrbacresourceid', null, true);
                        $scope.webMenus = new Array($scope.webMenus);
                        $scope.mobileMenus = G.toTree($scope.mobileMenus,'ROOT','parentresourceid','ehdrbacresourceid', null, true);
                        $scope.mobileMenus = new Array($scope.mobileMenus);
                        $scope.resourceTree = $scope.webMenus;
                    }
                });
            }

            function resoureToArray(tree, resourceList){
                resourceList = resourceList||[];
                if(tree.ehdrbacresourceid&&tree.ownedbyrole){
                    resourceList.push(tree.ehdrbacresourceid);
                }
                angular.forEach(tree.children,function(child){
                    resoureToArray(child, resourceList);
                });
                return resourceList;
            }

            $scope.save=function(){
                var resourceTree = resoureToArray($scope.webMenus[0]).concat(resoureToArray($scope.mobileMenus[0]));
                service.updateRbacRole({
                    ehdrbacroleid:$stateParams.ehdrbacroleid,
                    resourceids:resourceTree.join(',')
                }).then(function(data){
                    if(data.data.result!=='success'){
                        G.alert(data.data.msg,{type:'error'});
                        return;
                    }
                    G.alert(data.data.msg);
                },function(data){
                    G.alert('角色修改失败',{type:'error'});
                })
            };

            $scope.switchTab = function(type){
                $scope.tableType = type;
                if(type==='bc'){
                    $scope.resourceTree = $scope.webMenus;
                }else{
                    $scope.resourceTree = $scope.mobileMenus;
                }
            };

            init();
            
        }]);

    }
});