// import './mock';
export default (app, elem, attrs, scope) => {
  app.factory('annualPartyService', [
    'G',
    '$http',
    '$q',
    function(G, $http, $q) {
      const method = 'POST';
      const pkg = '/annualPartyGameApi';

      const selectGameQuestionList = `${pkg}/gameQuestioncs/selectGameQuestionList`;
      const addGameQuestion = `${pkg}/gameQuestioncs/addGameQuestion`;
      const updateGameQuestion = `${pkg}/gameQuestioncs/updateGameQuestion`;
      const deleteGameQuestion = `${pkg}/gameQuestioncs/deleteGameQuestion`;

      const selectGameActivityList = `${pkg}/gameActivitycs/selectGameActivityList`;
      const addGameActivity = `${pkg}/gameActivitycs/addGameActivity`;
      const updateGameActivity = `${pkg}/gameActivitycs/updateGameActivity`;

      const startGameActivity = `${pkg}/gameActivitycs/startGameActivity`;
      const endGameActivity = `${pkg}/gameActivitycs/endGameActivity`;

      const selectGameQuestionByActivity = `${pkg}/gameQuestioncs/selectGameQuestionByActivity`
      const selectCurrentGameQuestion = `${pkg}/gameQuestioncs/selectCurrentGameQuestionList`
      const sendGameQuestion = `${pkg}/gameQuestioncs/sendGameQuestion`;
      const endGameQuestion = `${pkg}/gameQuestioncs/endGameQuestion`;



      // 统一接口处理
      function request(config) {
        var deferred = $q.defer();
        $http(config).then(function(res) {
          if (res.status === 200) {
            if (res.data.result === 'success') {
              deferred.resolve(res.data);
            } else {
              deferred.reject(res.data);
              $.alert(res.data.msg, { type: 'error' });
            }
          }
        });
        return deferred.promise;
      }

      return {
        question: {
          apiUrl: selectGameQuestionList,
          get(data) {
            return $http({
              url: selectGameQuestionList,
              method: 'post',
              data
            });
          },
          add(data) {
            return $http({
              url: addGameQuestion,
              method: 'post',
              data
            });
          },
          update(data) {
            return $http({
              url: updateGameQuestion,
              method: 'post',
              data
            });
          },
          delete(data) {
            return $http({
              url: deleteGameQuestion,
              method: 'post',
              data
            });
          },
          send(data){
            return $http({
              url: sendGameQuestion,
              method: 'post',
              data
            });
          },
          end(data){
            return $http({
              url: endGameQuestion,
              method: 'post',
              data
            });
          }
        },
        activity: {
          apiUrl: selectGameActivityList,
          get(data) {
            return $http({
              url: selectGameActivityList,
              method: 'post',
              data
            });
          },
          getQuestion(data){
            return $http({
              url: selectGameQuestionByActivity,
              method: 'post',
              data
            });
          },
          getCurrent(data){
            return $http({
              url: selectCurrentGameQuestion,
              method: 'post',
              data
            });
          },
          add(data) {
            return $http({
              url: addGameActivity,
              method: 'post',
              data
            });
          },
          update(data) {
            return $http({
              url: updateGameActivity,
              method: 'post',
              data
            });
          },
          start(data){
            return $http({
              url: startGameActivity,
              method: 'post',
              data
            });
          },
          end(data){
            return $http({
              url: endGameActivity,
              method: 'post',
              data
            });
          }
        }
      };
    }
  ]);
};
