define([
    'angular',
    '../directiveFormInput/formSelect.html',
    '../directiveFormInput/formSelect.css'
], function(
    angular,
    html) {
    return function(app, elem, attrs, scope) {
        app.directive('formSelect', ['$timeout', function($timeout) {
            return {
                template: html,
                restrict: 'EA',
                replace: true,
                scope:{
                    optionsData:'=',  //传入的options [{name:'',value:''},{name:'',value:''},...]
                    selectedData:'=', //选中的值 
                    defaultName:'@',
                    selectWidth:'@',
                    displayName:'@'
                },
                link: function postLink(scope, element, attrs) {

                },
                controller:function($scope,$element,$timeout){
                    $scope.displayName = $scope.displayName?$scope.displayName:'name';
                    if($scope.selectWidth) {
                        $element.find('.formSelect').css('width',$scope.selectWidth+'px');
                        $element.find('.choiceList').css('width',$scope.selectWidth+'px');
                    }else{
                        $element.find('.formSelect').css('width','100%');
                        $element.find('.choiceList').css('width','100%');
                    }

                    $scope.itemSelect=function(item){
                        $scope.selectedData=item;
                        $scope.flag=false;
                    }

                    $scope.blur = function(){
                        $timeout(function(){
                            $scope.flag=false
                        },200)
                    }

                }
            };
        }]);
    }
});