define([
    'angular'
], function(
    angular
) {
    return function(app, elem, attrs, scope) {
        app.controller('msgMgmtCtrl', ['$rootScope', '$scope', '$timeout', 'G', '$state', 'navsData', 'msgMgmtService', function($rootScope, $scope, $timeout, G, $state, navsData, service) {

            $scope.permission = navsData.permission;

            $scope.switchTab = function(type){
                $scope.searchParam.direction = type;
                $scope.fetchList();
            };

            $scope.switchType = function(key, type){
                $scope.searchParam[key] = type || '';
                $scope.fetchList();
            };

            $scope.fetchList = function(){
                $scope.fetch({currPage:1});
            };

            $scope.$on('refreshMsgList', function(){
                $scope.fetchList();
            });

            $scope.toggleMark = function(item){
                item.hover = !item.hover;
            };

            $scope.formatData = function(items){
                $scope.displaySysCount = items.systemtodocount>0?items.systemtodocount:'';
                $scope.displayArtCount = items.artificialtodocount>0?items.artificialtodocount:'';
                angular.forEach(items.workthingvolist, function(item){
                    item.displaySender = item.worktype==='SYSTEM'?'系统消息': item.createusername + '/' + item.createjobcard;
                    item.tip = `发送：${item.visiblecount}<br>已阅：${item.dealcount}`;
                });
            };

            $scope.formatParam = function(param){
                if(param.direction==='SEND'){
                    param.worktype = '';
                    param.status = '';
                    param.keyword = '';
                }
            }

            $scope.handleMsg = function(e, item){
                e.stopPropagation();
                if(item){
                    $scope.markAsRead(item);
                }
            };

            $scope.sendMsg = function(){
                $('#sendMsgDialog').modal({
                    backdrop: 'static'
                });
            };

            $scope.markAsRead = function(item, e, gotoDetails){
                if(e){
                    e.stopPropagation();
                }
                service.dealMsg({
                    workthingid: item.workthingid
                }).then(function(data){
                    if(gotoDetails){
                        $state.go('msgMgmt.msgDetails',{id: item.workthingid});
                    }
                    if(e){
                        $rootScope.$broadcast('updateMsgCount');
                    }
                    if(data.data&&data.data.result==='success'){
                        item.status = 'DONE';
                        if(item.worktype==='ARTIFICIAL'){
                            $scope.displayArtCount-- ;
                        }else{
                            $scope.displaySysCount-- ;
                        }
                    }
                },function(){});
            };

            $scope.gotoDetails = function(item){
                $scope.markAsRead(item, null, true);
            };

            function checkIfNeedTip(){
                var textEdit = angular.element('.text-ellipsis');
                var clientWidth = 0, scrollWidth = 0, index = 0;  
                angular.forEach(textEdit, function(item){
                    clientWidth = item.clientWidth;
                    scrollWidth = item.scrollWidth;
                    index = item.id;
                    if(clientWidth<scrollWidth){
                        $scope.lists.workthingvolist[index].tipShow = true;
                    }
                });
            }

            $scope.ready = function(last){
                if(last){
                    $timeout(function(){
                        checkIfNeedTip();
                    },0);
                }
            };

            function init(){
                $scope.apiUrl = service.selectWorkThingListUrl;
                $scope.searchParam = {
                    direction: 'RECEIVE'
                }
            }

            init();
        }]);
    }
});