define([
    '../basicSelectCityGroup/basicSelectCityGroup.css',
    'angular',
    '../basicSelectCityGroup/basicSelectCityGroupService',
    '../basicSelectCityGroup/basicSelectCityGroup.html'
    ], function(
        css,
        angular,
        basicSelectCityGroupService,
        html) {
       return function(app, elem, attrs, scope) {
          basicSelectCityGroupService(app, elem, attrs, scope);
          app.directive('basicSelectCityGroupDirective', [function() {
             return {
                template: html,
                restrict: 'EA',
                replace: true,
                scope: {
                    checkedData: '=', // 可以不传，也可以传入一个{province:'浙江省',city:'杭州市',region:'滨江区'}的对象 在选择完区后，对象
                    //格式为{province:'浙江省',provinceCode:'223345',city:'杭州市',cityCode:'222',region:'滨江区',,regionCode:'66777'}
                    //checkedData的值只有在选择“区”一级时才会被重设
                    updateData:'='//用于更新初始化指令
                },
                link: function postLink($scope, $element, $attrs) {

                },

                controller: function($scope,$element,$attrs,$transclude,$log,$http,G, $timeout, basicSelectCityGroupService){

                    $scope.$watch('updateData',function(){
                        init();
                    })

                    function init(){
                       $scope.lists = [[],[],[]];
                       $scope.checkedData = $scope.checkedData || {};
                       $scope.flag=[];
                       $scope.model1='';
                       $scope.model2='';
                       $scope.model3='';
                       $scope.initCity=true;
                       $scope.initRegion=true;
                       setList();
                   }

                   function matchItem(name,index){
                    var newData;
                    angular.forEach($scope.lists[index],function(item,_){
                        if(item.name===name){
                            newData=item;
                        }
                    })
                    return newData;

                   } 
 
                   var parseData = function(data, namekey) {
                        var i, k, newData = [];
                        for(i = 0, k = data.length; i < k; i++) {
                         newData.push({
                            name: data[i][namekey],
                            value: data[i]['code']
                         });
                        }
                        return newData;
                    };

                 function setList(value,index){
                    if(!value){
                       basicSelectCityGroupService.getProvince().then(function(data) {
                        $scope.lists[0] = parseData(data.data.data, 'province');
                        if($scope.checkedData.province&&(!$scope.checkedData.provinceCode)){
                             $scope.model1=matchItem($scope.checkedData.province,0);
                             if($scope.model1){
                                setList($scope.model1,1);  
                             }
                             return; 
                        }

                        $scope.lists[1]=[];
                        $scope.lists[2]=[];
                        return;
                    });
                    }
                    if(index===1) {
                        basicSelectCityGroupService.getCity(value).then(function(data) {
                            $scope.lists[1] = parseData(data.data.data, 'city');
                            if($scope.lists[1].length===0) {
                                $scope.checkedData={}  
                                $scope.checkedData.province = $scope.model1.name;
                                $scope.checkedData.provinceCode = $scope.model1.value;
                                $scope.checkedData.city = $scope.model1.name;
                                $scope.checkedData.cityCode = $scope.model1.value;
                                $scope.checkedData.region = $scope.model1.name;
                                $scope.checkedData.regionCode = $scope.model1.value;
                                return;
                            }
                            if($scope.initCity&&$scope.checkedData.city&&(!$scope.checkedData.cityCode)){
                                $scope.model2=matchItem($scope.checkedData.city,1);
                                if($scope.model2){
                                    setList($scope.model2,2);
                                }
                                $scope.initCity=false;
                                return;
                            }
                            $scope.lists[2]=[];
                        });
                    }else if(index===2){
                      basicSelectCityGroupService.getRegion(value).then(function(data) {
                        $scope.lists[2] = parseData(data.data.data, 'region');
                           if($scope.lists[2].length===0) {
                                $scope.checkedData={}  
                                $scope.checkedData.province = $scope.model1.name;
                                $scope.checkedData.provinceCode = $scope.model1.value;
                                $scope.checkedData.city = $scope.model2.name;
                                $scope.checkedData.cityCode = $scope.model2.value;
                                $scope.checkedData.region = $scope.model2.name;
                                $scope.checkedData.regionCode = $scope.model2.value;
                                return;
                            }
                            if($scope.initRegion&&$scope.checkedData.region&&(!$scope.checkedData.regionCode)){
                                $scope.model3=matchItem($scope.checkedData.region,2);
                                $scope.initRegion=false;
                            }
                    }); 
                  }
                }

                //checkedSelect(0,'province',item.name,'model1')
                $scope.checkedSelect=function(index, checkedKey, checkedValue, modelKey) {
                    $scope.flag[index]=false;

                    var data = $scope.lists[index];

                    angular.forEach(data, function(item, index) {
                        if(checkedValue === item.name) {
                            $scope[modelKey] = item;
                        }
                    })    

                    if(index===0) {
                        $scope.model2=false;
                        $scope.model3=false;
                        $scope.checkedData.city = false;
                        $scope.checkedData.cityCode = false;
                        $scope.checkedData.region = false;
                        $scope.checkedData.regionCode = false;  
                    }else if(index===1){
                        $scope.model3=false;
                        $scope.checkedData.region = false;
                        $scope.checkedData.regionCode = false;  
                    }else if(index===2){
                            $scope.checkedData={}  
                            $scope.checkedData.province = $scope.model1.name;
                            $scope.checkedData.provinceCode = $scope.model1.value;
                            $scope.checkedData.city = $scope.model2.name;
                            $scope.checkedData.cityCode = $scope.model2.value;
                            $scope.checkedData.region = $scope.model3.name;
                            $scope.checkedData.regionCode = $scope.model3.value;
                    }
                    setList($scope[modelKey],index+1);
                }




                }
            };
        }]);
}
});