define([
  '../addDiscoveryPreload/addDiscoveryPreloadCtrl',
  '../addDiscoveryPreload/addDiscoveryPreloadService',
  'scmsModules/datePicker/datePickerDirective',
  '../imageUpload/imageUploadDirective',
  '../citysSelectMultiple/citysSelectMultipleDirective',
  '../addDiscoveryPreload/addDiscoveryPreload.html',
  '../addDiscoveryPreload/addDiscoveryPreload.css'
], function (
  ctrl,
  service,
  datePickerDirective,
  imageUploadDirective,
  citysSelectMultipleDirective,
  html) {
  return function (app, elem, attrs, scope) {
    ctrl(app, elem, attrs, scope);
    service(app, elem, attrs, scope);
    datePickerDirective(app, elem, attrs, scope);
    imageUploadDirective(app, elem, attrs, scope);
    citysSelectMultipleDirective(app, elem, attrs, scope);
    elem.append(html);
  }
});