define([
    'angular'
], function(
    angular
) {
    return function(app, elem, attrs, scope) {
        app.controller('systemLogConfigCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'systemLogConfigService', 
            function($rootScope, $scope, $state, $stateParams, $timeout, G, service) {
            
            $scope.apiUrl = service.getLogListUrl;

            function getAllModules(){
                service.selectRbacResourceByParam({
                    applicationflag: 'BG'
                }).then(function(data) {
                    $scope.lists = data.data.data;
                    $scope.modules=G.toTree($scope.lists,0,'parentresourceid','ehdrbacresourceid');
                    if(!$scope.modules[0]){
                        $scope.modules=[$scope.modules];
                    }
               });
            }

            $scope.changeModules = function(){
                $scope.pages = [];
                $scope.selectedPage = '';
                $scope.actions = [];
                $scope.selectedAction = '';
                if($scope.selectedModule){
                    getAllPage($scope.selectedModule.children);
                }
            }

            function getAllPage(modules){
                angular.forEach(modules, function(module){
                    if(module.resourcetype===0&&module.resourceurl!==';'){
                        $scope.pages.push(module);
                    }
                    if(module.children){
                        getAllPage(module.children);
                    }
                });
            }

            $scope.changePage = function(){
                $scope.actions = [];
                $scope.selectedAction = '';
                if($scope.selectedPage){
                    getAllActions($scope.selectedPage.children);
                }
            }

            function getAllActions(pages){
                angular.forEach(pages, function(resource){
                    if(resource.resourcetype===1){
                        $scope.actions.push(resource);
                    }
                    if(resource.children){
                        getAllActions(resource.children);
                    }
                });
            }

            function getDataParams(){
                service.selectDictionaryList({
                    parentdictionarycode:'bcLogConfigData'
                }).then(function(data){
                    $scope.dataParams = data.data.data;
                });
            }

            $scope.submit = function(){
                service.addLogConfig({
                    module: $scope.selectedModule.resourcename,
                    page: $scope.selectedPage.resourcename,
                    action: $scope.selectedAction.resourcename,
                    linkurl: $scope.requestUrl,
                    coredatacfgs: $scope.selectedParam&&$scope.selectedParam.dictionarycode,
                    username: G.userInfo.username,
                    jobcard: G.userInfo.jobcard
                }).then(function(data){
                    if(data&&data.data&&data.data.result==='success'){
                        $scope.fetch({currPage:1});
                        $scope.selectedModule = '';
                        $scope.selectedPage = '';
                        $scope.pages = '';
                        $scope.selectedAction = '';
                        $scope.actions = '';
                        $scope.selectedParam = '';
                        $scope.requestUrl = '';
                    }else{
                        G.errorAlert('新增配置失败',data);
                    }
                },function(data){
                    G.errorAlert('新增配置失败',data);
                });
            };

            $scope.setCurrent = function(item){
                $scope.currentItem = item;
            };

            $scope.toggleStatus=function($event,item) {
                var status = item.status==='ENABLED'?'DISABLED':'ENABLED';
                var msg = item.status==='DISABLED'?'确定开启？':'确定关闭？';
                var $alert = G.confirm({
                    $target: $($event.target),
                    msg: msg,
                    position: 'bottom',
                    submit: function() {    
                        service.updateLogConfig({
                            logmonitorconfigid:item.logmonitorconfigid,
                            status:status,
                            username: G.userInfo.username,
                            jobcard: G.userInfo.jobcard
                        }).then(function(data) {
                            data = data.data;
                            if(data.result === 'success') {
                                $alert.hide();
                                G.alert('状态切换成功');
                                $scope.fetch();
                            }else {
                                G.alert(data.msg || '状态切换失败', {
                                    type: 'error'
                                });
                                $alert.hide();
                            }
                        },
                        function() {
                            G.alert('状态切换失败', {
                                type: 'error'
                            });
                            $alert.hide();
                        });
                    }    
                });
            };

            $scope.$on('refreshList', function(){
                $scope.fetch({currPage:1});
            })

            getAllModules();
            getDataParams();
        }]);
    }
});