define([
    '../msgMgmt/msgDetailsCtrl',
    '../msgMgmt/msgMgmtService',
    'scmsModules/table/table',
    '../msgMgmt/msgDetails.html',
    '../msgMgmt/msgDetails.less'
], function(
    ctrl,
    service,
    table,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        table(app, elem, attrs, scope);
        elem.append(html);
    }
});