define([
  'angular'
], function (angular) {
  return function (app, elem, attrs, scope) {
    app.controller('discoveryPreloadCtrl',['$scope', 'G', 'discoveryPreloadService','$state', 'navsData',function ($scope, G, service,$state,navsData) {
          $scope.permission = navsData.permission || {};
          
          $scope.apiUrl = service.listPreload;

          $scope.search = function(){
             $scope.fetch({ currPage: 1 });
          };

          $scope.add = function(){
            $state.go('addDiscoveryPreload.addDiscoveryPreload');
          };
          $scope.modify = function(id){
            $state.go('addDiscoveryPreload.addDiscoveryPreload',{discoveryconfigid:id});
          }

          // 删除
          $scope.remove = function (order) {
            $.alert('此操作将永久删除该条预加载，是否继续？', {
              type: 'confirm',
              title: '预加载删除',
              callback: function () {
                var isSend = false;
                if(!isSend){
                    isSend = true;
                  service.deletePreload({
                  order: order
                }).then(function (data) {
                  isSend = false;
                  if (data && data.data && data.data.result === 'success') {
                    G.alert(data.data.msg || '删除成功');
                    $scope.fetch({ currPage: $scope.currPage });
                  } else {
                    G.alert(data.data.msg || '删除失败', {type: 'error'});
                  }
                }, function (data) {
                  isSend = false;
                  G.alert(data.data.msg || '删除失败', {type: 'error'});
                });
                }
              }
            });
          };


          $scope.select = function(id){
            id && ($scope.discoveryconfigid = id);
          }

          $scope.$on('updateList', function () {
            $scope.fetch({ currPage: 1 });
          });

        }])
  };
});