define([
    'angular'
], function(
    angular
) {
    return function(app, elem, attrs, scope) {
        app.controller('ownStaffListCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'ownStaffListService', 'navsData', 
            function($rootScope, $scope, $state, $stateParams, $timeout, G, service, navsData) {
        
            $scope.staffList = {};
            $scope.pageSize = G.getPageSize();
            $scope.introduction = '如果要增加员工，在员工管理->选择员工->修改市场页面完成。';
            $scope.topPos='right';

            $scope.remove = function(item, $event){
                var $alert = G.confirm({
                    $target: $($event.target),
                    msg: '确认删除？',
                    position: 'bottom',
                    submit: function() {
                        service.removeStaff({
                            ehdrbacusersid: item.ehdrbacusersid,
                            ehdrbacmarketplaceid: $scope.marketplaceid
                        }).then(function(data) {
                            data = data.data;
                            $alert.hide();
                            if(data.result === 'success') {                                
                                getStaffList($scope.currPage);
                                G.alert('移除成功');
                            }else {
                                G.alert(data.msg || '操作失败');
                            }                    
                        }, function(data) {
                            G.alert(data.msg);
                        });  
                    }
                });
            };

            function init(){
                $scope.permission = navsData.permission;
                $scope.marketplaceid = $stateParams.marketplaceid;
                $scope.marketplacename = $stateParams.marketplacename;
                getStaffList();
            }

            $scope.returnMarket=function() {
                $state.go('basicAreaMgmt.basicAreaMgmt');
            }
            function getStaffList(currPage){
                $scope.currPage = currPage || 1;
                service.getStaffList({
                    ehdrbacmarketplaceid: $scope.marketplaceid,
                    skipCount: ($scope.currPage - 1) * $scope.pageSize,
                    pageSize: $scope.pageSize
                }).then(function(data){
                    if(data&&data.data&&data.data.result==='success'){
                        $scope.staffList.data = data.data.data;
                        $scope.staffList.totalCount = data.data.count;
                    }else{
                        G.errorAlert('查询失败', data);
                    }
                },function(data){
                    G.errorAlert('查询失败', data);
                });
            }

            $scope.$watch('currPage + pageSize', function(newValue, oldValue) {
                if(newValue && oldValue && newValue !== oldValue) {
                    getStaffList($scope.currPage);
                }
            });

            $scope.$on('addMarketSuccess', function(){
                getStaffList();
            });

            init();
            
        }]);

    }
});