define([
    'angular',
    '../resetPwdSuccess/resetPwdSuccess.html',
    '../resetPwdSuccess/resetPwdSuccess.css'
    ], function(
        angular,
        html) {
        return function(app, elem, attrs, scope) {
            app.directive('resetPwdSuccessDirective', ['G', '$q', function(G,$q) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        newJobcard:'='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude,$timeout, $log, $http, G) {                        
                        $('#resetPwdSuccess').on('hide.bs.modal',function(e){    
                            $scope.$emit('refresh');
                        });
                    }
                };
            }]);
    }
});