define([
    'angular'
    ], function(angular) {
        return function(app, elem, attrs, scope) {
            app.factory('chooseOrgService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
                return {
                    selectDictionaryList: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdpubdictionarycs/selectPubDictionaryListByParam',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigModules/chooseOrg/chooseOrgDictionary.json'//假数据api
                        });
                    }
                };
            }]);
        }
    });