define([
    'angular',
    '../msgMgmt/msgMgmtService',
    '../msgMgmt/sendMsgDialog.html',
    '../msgMgmt/sendMsgDialog.less'
], function(
    angular,
    msgMgmtService,
    html) {
    return function(app, elem, attrs, scope) {
        msgMgmtService(app, elem, attrs, scope);
        app.directive('sendMsgDialog', ['$timeout', 'msgMgmtService', 'navsData', '$rootScope', function($timeout, service, navsData, $rootScope) {
            return {
                template: html,
                restrict: 'EA',
                replace: true,
                scope: {
                    currentItem: '='
                },
                link: function postLink($scope, $element, $attrs) {

                },

                controller: function($scope,$element,$attrs,$transclude,$log,$http,G){

                    $scope.sendMsg = function(){
                        let ccList = [];
                        $scope.selectedList.map(function(item){
                            ccList.push({
                                type: item.username?'USER':'ORGANIZATION',
                                code: item.username?item.jobcard:item.organizationcode
                            });
                        });
                        service.sendMsg({
                            worktype: 'ARTIFICIAL',
                            visibles: JSON.stringify(ccList),
                            context: $scope.formData.context,
                            url: $scope.formData.displayNav
                        }).then(function(data){
                            if(data.data&&data.data.result==='success'){
                                // alert.hide();
                                G.alert('消息发送成功');
                                $element.modal('hide');
                                $scope.$emit('refreshMsgList');
                                $rootScope.$broadcast('updateMsgCount');
                            }else{
                                G.alert(data.data.msg || '消息发送失败', {type:'error'});
                            }
                        }, function(data){
                            G.alert(data || '消息发送失败', {type:'error'});
                        });
                    }

                    $scope.toggleModule = function(){
                        $scope.showFirstGroup = !$scope.showFirstGroup;
                        $scope.showThirdGroup = false;
                        $scope.showFourthGroup = false;
                    };

                    $scope.selectSecond = function(second){
                        if(second.children){
                            $scope.thirdGroupMenu = second.children;
                            $scope.showThirdGroup = true;
                            if(second.resourceurl!==';'){
                                $scope.formData.navigations = second.resourceurl
                            }
                        }else if(second.resourceurl){
                            $scope.formData.navigations = second.resourceurl.substring(second.resourceurl.lastIndexOf(';')+1);
                            $scope.formData.displayNav = second.resourcename + '(' + $scope.formData.navigations + ')';
                            $scope.closeAllMenu();
                        }
                    }

                    $scope.selectThird = function(third){
                        if(third.children){
                            $scope.fourthGroupMenu = third.children;
                            $scope.showFourthGroup = true;
                            if(third.resourceurl!==';'){
                                $scope.formData.navigations = third.resourceurl
                            }
                        }else{
                            $scope.formData.navigations = third.resourceurl.substring(third.resourceurl.lastIndexOf(';')+1);
                            $scope.formData.displayNav = third.resourcename + '(' + $scope.formData.navigations + ')';
                            $scope.closeAllMenu();
                        }
                    }

                    $scope.selectFourth = function(fourth){
                        $scope.formData.navigations = fourth.resourceurl.substring(fourth.resourceurl.lastIndexOf(';')+1);
                        $scope.formData.displayNav = fourth.resourcename + '(' + $scope.formData.navigations + ')';
                        $scope.closeAllMenu();
                    };

                    $scope.closeAllMenu = function(){
                        $scope.showFirstGroup =false;
                        $scope.showThirdGroup = false;
                        $scope.showFourthGroup = false;
                    };

                    $scope.clearNav = function(){
                        $scope.formData.navigations = '';
                        $scope.formData.displayNav = '';
                        $scope.closeAllMenu();
                    };

                    $scope.updateRight=function($item, e){
                        $scope.isSearchResult = false;
                        $scope.selectOrg = $item;
                        getStaffList($scope.selectOrg);
                    }

                    $scope.toggleOrgList = function(){
                        $scope.showOrgStaffList = !$scope.showOrgStaffList;
                        $scope.closeAllMenu();

                    };

                    $scope.hideCC = function(){
                        // $scope.orgStaff = {};
                        $scope.showOrgStaffList = false;
                    };

                    $scope.search = function(keywords){
                        if(!keywords){
                            return;
                        }
                        service.getStaffList({
                            organizationcode: 88888888,
                            organizationflag: 2,
                            resultstyle: 2,
                            userstatus: 1,
                            keywords: keywords,
                            skipCount: 0,
                            pageSize: 100
                        }).then(function(data){
                            if(data.data&&data.data.result==='success'){
                                $scope.storeStaffList = G.clone($scope.staffList);
                                $scope.staffList = data.data.data;
                                $scope.isSearchResult = true;
                                $scope.selectedUserList.map(function(user){
                                    $scope.staffList.map(function(staff){
                                        if(staff.jobcard===user.jobcard){
                                            staff.isChecked = true;
                                        }
                                    })
                                });
                                $scope.selectedOrgList.map(function(org){
                                    $scope.staffList.map(function(staff){
                                        if(isStaffInOrg(staff, org)){
                                            staff.isChecked = true;
                                        }
                                    })
                                });
                            }
                        });
                    };

                    $scope.fillCCList = function(e){
                        if(e){e.stopPropagation()};
                        $scope.selectedList = $scope.selectedOrgList.concat($scope.selectedUserList);
                        if($scope.selectedList&&$scope.selectedList.length>8){
                            $scope.displaySelectedList = $scope.selectedList.slice(0,7);
                            let alreadyCount = $scope.displaySelectedList.reduce(function(pre,cur){
                                if(cur.count){
                                    pre += cur.count;
                                }else{
                                    pre += 1
                                }
                                return pre;
                            },0)
                            $scope.displaySelectedList.push({
                                username: '+' + ($scope.totalCount - alreadyCount),
                                type: 'more'
                            });
                        }else{
                            $scope.displaySelectedList = $scope.selectedList;
                        }
                        $scope.showOrgStaffList = false;
                    };

                    $scope.returnToOrg = function(){
                        $scope.isSearchResult = false;
                        $scope.staffList = G.clone($scope.storeStaffList);
                    };

                    function isStaffInOrg(staff, org){
                        return staff.userorgpros&&staff.userorgpros.length>0&&staff.userorgpros[0].organizationcode===org.organizationcode;
                    }

                    function partialToAll(org){
                        $scope.selectedOrgList.push(org);
                        $scope.selectedUserList = $scope.selectedUserList.filter(function(item){
                            let result = !isStaffInOrg(item, org);
                            return result;
                        });
                    }

                    // function staffInUserList(staff){
                    //     let result = {
                    //         contain: false,
                    //         index: 0
                    //     };
                    //     $scope.selectedUserList.map(function(user, index){
                    //         if(user.jobcard===staff.jobcard){
                    //             result = {
                    //                 contain: true,
                    //                 index: index
                    //             };
                    //         }
                    //     });
                    //     return result;
                    // }

                    function staffInList(staff, array, param, isOrg){
                        let result = {
                            contain: false,
                            index: 0,
                            item: {}
                        };
                        array.map(function(item, index){
                            if(isOrg){
                                if(isStaffInOrg(staff, item)){
                                    result = {
                                        contain: true,
                                        index: index,
                                        item: item
                                    };
                                }
                            }else{
                                if(item[param]===staff[param]){
                                    result = {
                                        contain: true,
                                        index: index,
                                        item: item
                                    };
                                }
                            }
                            
                        });
                        return result;
                    }

                    function unCheckOrg(arr, org){
                        if(arr.organizationcode===org.organizationcode){
                            org.isChecked = false;
                        }else if(arr.children){
                            arr.children.map(function(child){
                                unCheckOrg(child, org);
                            })
                        }
                    }

                    function doCheckOrg(arr, org){
                        if(arr.organizationcode===org.organizationcode){
                            org.isChecked = true;
                        }else if(arr.children){
                            arr.children.map(function(child){
                                unCheckOrg(child, org);
                            })
                        }
                    }

                    $scope.doCheck = function(staff, fromOrg){
                        if(fromOrg&&(!$scope.selectOrg.staffList||$scope.selectOrg.staffList.length<1)){
                            return;
                        }
                        if($scope.isSearchResult){
                            let organizationcode = staff.userorgpros&&staff.userorgpros.length>0?staff.userorgpros[0].organizationcode:staff.organizationcode;
                            let userResult = staffInList(staff, $scope.selectedUserList, 'jobcard');
                            let orgResult = staffInList(staff, $scope.selectedOrgList, 'organizationcode', true);
                            staff.isChecked = !staff.isChecked;
                            if(staff.isChecked){
                                if(!$scope.orgStaff[organizationcode]){
                                    $scope.selectedUserList.push(staff);
                                }else{
                                    let allCheck = true;
                                    $scope.orgStaff[organizationcode].staffList.map(function(user){
                                        if(user.jobcard===staff.jobcard){
                                            user.isChecked = true;
                                        }
                                        if(!user.isChecked){
                                            allCheck = false;
                                        }
                                    });
                                    $scope.orgStaff[organizationcode].isChecked = allCheck;
                                    if($scope.orgStaff[organizationcode].isChecked){
                                        $scope.storeStaffList.map(function(user){
                                            if(user.jobcard===staff.jobcard){
                                                user.isChecked = true;
                                            }
                                        });
                                        // doCheckOrg($scope.orgList[0], $scope.selectedOrgList[orgResult.index]);
                                        partialToAll($scope.orgStaff[organizationcode]);
                                    }else{
                                        $scope.selectedUserList.push(staff);
                                    }
                                }
                            }else{
                                if(userResult.contain){
                                    $scope.selectedUserList.splice(userResult.index, 1);
                                }else{
                                    
                                    orgResult.staffList = orgResult.item.staffList.filter(function(item){
                                        return item.jobcard !== staff.jobcard;
                                    });
                                    $scope.orgStaff[organizationcode].staffList.map(function(user){
                                        if(user.jobcard===staff.jobcard){
                                            user.isChecked = false;
                                        }
                                    });
                                    $scope.storeStaffList.map(function(user){
                                        if(user.jobcard===staff.jobcard){
                                            user.isChecked = false;
                                        }
                                    });
                                    unCheckOrg($scope.orgList[0], $scope.selectedOrgList[orgResult.index]);
                                    $scope.selectedOrgList.splice(orgResult.index,1);
                                    $scope.selectedUserList = orgResult.staffList.concat($scope.selectedUserList);
                                }
                            }
                        }else{
                            if(staff){
                                let initStatus = {
                                    staff: staff.isChecked,
                                    org: $scope.selectOrg.isChecked
                                };
                                staff.isChecked = !staff.isChecked;
                                $scope.selectOrg.isChecked = allChecked($scope.staffList)?true:false;
                                if($scope.selectOrg.isChecked){
                                    partialToAll($scope.selectOrg);
                                }else{
                                    if(!initStatus.org){
                                        if(staff.isChecked){
                                            $scope.selectedUserList.push(staff);
                                        }else{
                                            $scope.selectedUserList.splice($scope.selectedUserList.indexOf(staff), 1);
                                        }
                                    }else{
                                        $scope.selectedOrgList = $scope.selectedOrgList.filter(function(item){
                                            return item.organizationcode!==$scope.selectOrg.organizationcode;
                                        });
                                        $scope.selectedUserList = $scope.selectedUserList.concat($scope.staffList.filter(function(item){
                                            return item.isChecked;
                                        }));
                                    }
                                }
                            }else{
                                $scope.selectOrg.isChecked = !$scope.selectOrg.isChecked;
                                angular.forEach($scope.staffList, function(item){
                                    item.isChecked = $scope.selectOrg.isChecked;
                                });
    
                                if($scope.selectOrg.isChecked){
                                    partialToAll($scope.selectOrg);
                                }else{
                                    $scope.selectedOrgList = $scope.selectedOrgList.filter(function(item){
                                        return item.organizationcode!==$scope.selectOrg.organizationcode;
                                    });
                                }
                            }
                        }
                        countTotal();
                    };

                    function countTotal(){
                        $scope.totalCount = $scope.selectedOrgList.reduce(function(pre, cur){
                            return pre + cur.count;
                        },0) + $scope.selectedUserList.length;
                    }

                    function allChecked(array){
                        let result = true;
                        angular.forEach(array, function(staff){
                            if(!staff.isChecked){
                                result = false;
                            }
                        });
                        return result;
                    }

                    function getStaffList(org){
                        if($scope.orgStaff[org.organizationcode]){
                            $scope.staffList = $scope.orgStaff[org.organizationcode].staffList;
                        }else{
                            G.loading(true, {$container: $('.org-staff-list')});
                            service.getStaffList({
                                organizationcode: org.organizationcode,
                                organizationflag: 0,
                                userstatus: 1,
                                resultstyle: 2,
                                skipCount: 0,
                                pageSize: 3000
                            }).then(function(data){
                                G.loading(false, {$container: $('.org-staff-list')});
                                if(data.data&&data.data.result==='success'){
                                    $scope.staffList = data.data.data;
                                    org.count = data.data.count;
                                    org.staffList = $scope.staffList;
                                    $scope.orgStaff[org.organizationcode] = org;
                                }
                            },function(){
                                G.loading(false, {$container: $('.org-staff-list')});
                            })
                        }
                    }

                    $scope.removeCC = function(item, e, index){
                        e.stopPropagation();
                        if(item.type==='more'){return;};
                        // $scope.displaySelectedList.splice(index, 1);
                        if(item.jobcard){
                            $scope.selectedUserList = $scope.selectedUserList.filter(function(user){
                                return item.jobcard!==user.jobcard;
                            });
                            if(item.userorgpros&&item.userorgpros.length>0&&$scope.orgStaff[item.userorgpros[0].organizationcode]){
                                let staffList = $scope.orgStaff[item.userorgpros[0].organizationcode].staffList;
                                staffList.map(function(user){
                                   if(item.jobcard===user.jobcard){
                                       user.isChecked = false;
                                   }
                                });
                            }
                        }else{
                            $scope.selectedOrgList = $scope.selectedOrgList.filter(function(org){
                                return item.organizationcode!==org.organizationcode;
                            });
                            $scope.orgStaff[item.organizationcode].isChecked = false;
                            $scope.orgStaff[item.organizationcode].staffList.map(function(user){
                                user.isChecked = false;
                            });
                        }
                        countTotal();
                        $scope.fillCCList();
                    };

                    function getOrg(){
                        service.getOrg().then(function(data){
                            $scope.orgList=data.data.data;
                            
                            $scope.orgList = G.toTree($scope.orgList,'0','parorganizationcode','organizationcode');
                            $scope.selectOrg = G.clone($scope.orgList);
                            $scope.orgList=new Array($scope.orgList);
                            getStaffList($scope.selectOrg);
                        });
                    }
                    
                    function init(){
                        $scope.formData = {
                            menuListFromList: G.clone(navsData.data)
                        };
                        $scope.totalCount = 0;
                        $scope.selectedOrgList = [];
                        $scope.selectedUserList = [];
                        $scope.selectedList = [];
                        $scope.selectOrg = {};
                        $scope.orgStaff = {};
                        $scope.displaySelectedList = [];
                        $scope.showOrgStaffList = false;
                        $scope.closeAllMenu();
                        getOrg();
                    }

                    $('#sendMsgDialog').on('show.bs.modal',function(){
                        init();
                    });

                }
            };
        }]);
    }
});
