import angular from 'angular';

export default function(app, elem, attrs, scope) {
  app.factory('activityLikesService', [
    '$rootScope',
    '$http',
    'G',
    function($rootScope, $http, G) {
      return {
        getPicture: function(options) {
          return $http({
            url: '/ehuodiScreen/annualMeeting/getPicture',
            method: 'post',
            data: options,
            test:
              'appPublishWeb/appPublishModules/activityLikes/activityLikes.json', //假数据api
          });
        },
        getactivityLikesUrl: '/pushServiceApi/pushManage/queryactivityLikes',

        checkPicture: function(options) {
          return $http({
            url: '/ehuodiScreen/annualMeeting/checkPicture',
            method: 'post',
            data: options,
            test: 'appPublishWeb/appPublishModules/header/success.json', //假数据api
          });
        },
        endActivity: function(options) {
          return $http({
            url: '/ehuodiScreen/annualMeeting/endActivity',
            method: 'post',
            data: options,
            test: 'appPublishWeb/appPublishModules/header/success.json', //假数据api
          });
        },
        getPictureCount: function(options) {
          return $http({
            url: '/ehuodiScreen/annualMeeting/getPictureCount',
            method: 'post',
            data: options,
            test: 'appPublishWeb/appPublishModules/header/success.json', //假数据api
          });
        },
        getPictureList: function(options) {
          return $http({
            url: '/ehuodiScreen/annualMeeting/getPictureList',
            method: 'post',
            data: options,
            test: 'appPublishWeb/appPublishModules/header/success.json', //假数据api
          });
        },
      };
    },
  ]);
}
