define([
    '../addNewPositionDialog/addNewPositionDialog.css',
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('addNewPositionDialogService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                getDemoPosName: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdpubdictionarycs/selectPubDictionaryListByParam',
                        method: 'post',
                        data: options,
                        test: 'basicConfigModules/basicAreaMgmt/areaType.json'//假数据api
                    });
                },

                addPosition: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacpostcs/insertRbacPost',
                        method: 'post',
                        data: options,
                        noconcurrent: true,
                        test: 'basicConfigModules/header/success.json'//假数据api
                    });
                },

                updatePosition: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacpostcs/updateRbacPost',
                        method: 'post',
                        data: options,
                        noconcurrent: true,
                        test: 'basicConfigModules/header/success.json'//假数据api
                    });
                },  
                getAllRole: function(opts) {
                     return $http({
                        url: '/ehuodiBedrockApi/ehdrbacrolecs/selectRbacRoleByParam',
                        method: 'post',
                        data:opts,
                        test: 'basicConfigModules/systemRoleMgmt/selectRbacRoleByParam.json'//假数据api
                    });  
                }
            };
        }]);
    }
});