define([
    'angular'
], function(
    angular
) {
    return function(app, elem, attrs, scope) {
        app.controller('systemErrorCodeCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'systemErrorCodeService', 
            function($rootScope, $scope, $state, $stateParams, $timeout, G, service) {
            
            $scope.apiUrl = service.queryErrorConfigList;
            $scope.setCurrent = function(item){
                $scope.currentItem = item;
            };

            $scope.toggleStatus=function($event,item) {
                var status = item.status==='1'?'9':'1';
                var msg = item.status==='1'?'确定关闭？':'确定开启？';
                var $alert = G.confirm({
                    $target: $($event.target),
                    msg: msg,
                    position: 'bottom',
                    submit: function() {    
                        service.modifyErrorConfigById({
                            errorconfigid:item.errorconfigid,
                            status:status,
                            username: G.userInfo.username,
                            jobcard: G.userInfo.jobcard
                        }).then(function(data) {
                            data = data.data;
                            if(data.result === 'success') {
                                $alert.hide();
                                G.alert('状态切换成功');
                                $scope.fetch();
                            }else {
                                G.alert(data.msg || '状态切换失败', {
                                    type: 'error'
                                });
                                $alert.hide();
                            }
                        },
                        function() {
                            G.alert('状态切换失败', {
                                type: 'error'
                            });
                            $alert.hide();
                        });
                    }    
                });
            };

            $scope.$on('refreshList', function(){
                $scope.fetch({currPage:1});
            })

        }]);
    }
});