define([
    '../listGenerator/listGeneratorCtrl',
    '../listGenerator/listGeneratorService',
    'scmsModules/table/table',
    '../listGenerator/listGenerator.html',
    '../listGenerator/listGenerator.css'
], function (
    ctrl,
    service,
    table,
    html) {
        return function (app, elem, attrs, scope) {
            ctrl(app, elem, attrs, scope);
            service(app, elem, attrs, scope);
            table(app, elem, attrs, scope);
            elem.append(html);
        }
    });