define([
    '../addNewUpgradeDialog/addNewUpgradeDialog.css',
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('addNewUpgradeDialogService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                addVersion: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/versionmanagecs/addVersion',
                        method: 'post',
                        data: options,
                        noconcurrent: true
                    });
                },

                modifyVersion: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/versionmanagecs/modifyVersionManage',
                        method: 'post',
                        data: options,
                        noconcurrent: true
                    });
                }
            };
        }]);
    }
});