define([
    'angular'
    ], function(angular) {
        return function(app, elem, attrs, scope) {
            app.factory('addBasicPreloadService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
                return {
                    addBasicPreload: function(opts) {
                        return $http({
                            url: '/goodstaxiAdmin/preloadurlcs/insertPreLoadUrl',
                            method: 'post',
                            data: opts,
                            test: 'basicConfigWeb/basicConfigModules/basicPreload/success.json'
                        });
                    },
                    updatePreload: function(opts) {
                        return $http({
                            url: '/goodstaxiAdmin/preloadurlcs/updatePreLoadUrl',
                            method: 'post',
                            data: opts,
                            test: 'basicConfigWeb/basicConfigModules/basicPreload/success.json'
                        });
                    },
                    detailPreload:function(opts){
                        return $http({
                            url:'/goodstaxiAdmin/preloadurlcs/selectPreLoadUrlByUrlKey',
                            method:'post',
                            data:opts,
                            test:'basicConfigWeb/basicConfigModules/basicPreload/basicPreload.json'
                        });
                    },
                };
            }]);
        }
    });