define([
    'angular'
    ], function(angular) {
        return function(app, elem, attrs, scope) {
            app.factory('addNewStructService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
                return {
                    insertRbacOrganization: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacorganizationcs/insertRbacOrganization',
                            method: 'post',
                            data:opts,
                            noconcurrent: true,
                        test: 'basicConfigModules/header/success.json'//假数据api
                    });
                    },  
                    updateRbacOrganization: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacorganizationcs/updateRbacOrganization',
                            method: 'post',
                            data:opts,
                            noconcurrent: true,
                        test: 'basicConfigModules/addNewMenu/addNewMenu.json'//假数据api
                    });
                    },
                    selectRbacOrganizationByCode:function(opts){
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacorganizationcs/selectRbacOrganizationByCode',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigModules/systemOrgMgmt/selectRbacOrganizationByCode.json'//假数据api
                    });                    
                    },
                    selectRbacUserByParam:function(opts){
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacuserscs/selectRbacUserByParam',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigModules/systemOrgMgmt/selectRbacUserByParam.json'//假数据api
                        });                    
                    }
                };
            }]);
        }
    });