define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('tfLyAddNewDataDirService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {       
                   insertPubDictionary: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/utmsdictionarycs/createUtmsDictionary',
                        method: 'post',
                        data:opts,
                        noconcurrent: true,
                    });
                },  
                updatePubDictionary: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/utmsdictionarycs/updateUtmsDictionary',
                        method: 'post',
                        data:opts,
                        noconcurrent: true,
                    });
                }
            };
        }]);
    }
});