define([
    'angular',
    'scmsModules/multiselect/multiselect',
    '../fileUploadDirective/fileUploadDirective',
    '../tfLyAddNewMenu/tfLyAddNewMenuService',
    '../tfLyAddNewMenu/tfLyAddNewMenu.html',
    '../tfLyAddNewMenu/tfLyAddNewMenu.css'
    ], function(
        angular,
        multiselect,
        fileUploadDirective,
        service,
        html,
        css) {
        return function(app, elem, attrs, scope) {
            multiselect(app, elem, attrs, scope);
            fileUploadDirective(app, elem, attrs, scope);
            service(app, elem, attrs, scope);
            app.directive('tfLyAddNewMenu', ['tfLyAddNewMenuService','G','$timeout',function(service,G,$timeout) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        currItem:'=',
                        opType:'=',
                        parentresourceid:'=',
                        parentversioncode:'=',
                        currentFlag:'='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude, $log, $http, G,$timeout) {
                        $scope.resourceTypes=[{name:'菜单',value:0},{name:'按钮',value:1}]
                        $scope.versions = 'P';
                        $('#tfLyAddNewMenu').on('show.bs.modal',function(){
                            $timeout(()=>{
                                $scope.$apply(function(){
                                    $scope.errors = {};
                                    $scope.formData={};
                                    $scope.formData.resourcetypeObj={};
                                    if($scope.opType==='edit') {
                                        $scope.formData = G.clone($scope.currItem);
                                        $scope.formData.title = '修改';
                                        angular.forEach($scope.resourceTypes, function(type){
                                            if(type.value===$scope.currItem.resourcetype){
                                                $scope.formData.resourcetypeObj = type;
                                            }
                                        });
                                    }else {
                                        $scope.formData.title = '新增';
                                    }
                                    $scope.errorResourcetype = false;
                                    $scope.initResourcetype = false;
                                })
                            })
                        });
                        $scope.$watch('formData.resourcetypeObj',function(newVal,oldVal){
                            $scope.errors = {};
                        },true);

                        $('#tfLyAddNewMenu').on('hide.bs.modal',function(){
                            G.clearAllError();
                        });
                        $scope.submit = function(){
                            if(!$scope.formData.resourcetypeObj.value&&$scope.formData.resourcetypeObj.value!==0&&$scope.formData.resourcetypeObj.value!==1){
                                $scope.errorResourcetype = true;
                                $scope.initResourcetype = true;
                                $scope.errors.type = '请选择标记类型';
                                return;
                            }

                            if($scope.opType==='edit'){
                                if($scope.formData.children&&$scope.formData.children.length>0&&$scope.formData.resourcetypeObj.value===1) {
                                    G.alert('该菜单下还有其他菜单或按钮，不能更改为按钮',{type:'error',$container:$element.find('.modal-content')});
                                    return;
                                }
                                const param = {
                                    parentresourceid: $scope.formData.parentresourceid,
                                    utmsresourceid:$scope.formData.utmsresourceid,
                                    resourcename: $scope.formData.resourcename,
                                    resourcetype: $scope.formData.resourcetypeObj.value,
                                    resourceurl: $scope.formData.resourceurl,
                                    resourceicon: $scope.formData.resourceicon,
                                    description: $scope.formData.description,
                                    operatekey: $scope.formData.operatekey,
                                }
                                service.updateRbacResource(param).then(function(data){
                                    if(data.data.result=='success'){
                                        $('#tfLyAddNewMenu').modal('hide');
                                        G.alert('修改资源成功!');
                                        $scope.currItem = G.clone($scope.formData);
                                        $timeout(function(){
                                            $scope.$emit('refreshMenu');
                                        },60)
                                    }else{
                                        G.alert(data.data.msg,{type:'error',$container:$element.find('.modal-content')});
                                    }
                                })
                            }else {
                                const param = {
                                    parentresourceid:$scope.parentresourceid,
                                    resourcename:$scope.formData.resourcename,
                                    resourcetype:$scope.formData.resourcetypeObj.value,
                                    resourceurl:$scope.formData.resourceurl,
                                    resourceicon: $scope.formData.resourceicon,
                                    description:$scope.formData.description,
                                    versionscodes: $scope.parentversioncode === 'P' ? 'P,Z' : $scope.parentversioncode,
                                    operatekey: $scope.formData.operatekey
                                }
                                service.insertRbacResource(param).then(function(data){
                                    if(data.data.result=='success'){
                                        $('#tfLyAddNewMenu').modal('hide');
                                        G.alert('新建资源成功!');
                                        $scope.$emit('refreshMenu');
                                    }else{
                                        G.alert(data.data.msg,{type:'error',$container:$element.find('.modal-content')});
                                    }
                                })
                            }
                        }
                    }
                };
            }]);
        }
    });