define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('ownStaffListService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                getStaffList: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacuserscs/selectRbacUserByParam',
                        method: 'post',
                        data: options,
                        test: 'basicConfigModules/basicStaffMgmt/basicStaffMgmt.json'//假数据api
                    });
                },

                removeStaff: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacuserscs/removupdateeODMPByRbacUser',
                        method: 'post',
                        data: options,
                        test: 'basicConfigModules/header/success.json'//假数据api
                    });
                }
            };
        }]);
    }
});