define([
  'angular'
], function (angular) {
  return function (app, elem, attrs, scope) {
    app.factory('addDiscoveryPreloadService', ['$rootScope', '$http', 'G', function ($rootScope, $http, G) {
      return {
        addPreload: function (opts) {
          return $http({
            url: '/goodstaxiAdmin/discoveryconfigcs/insertDiscoveryConfig',
            method: 'post',
            data: opts,
            test: 'basicConfigWeb/basicConfigModules/discoveryPreload/success.json'
          });
        },
        updatePreload: function (opts) {
          return $http({
            url: '/goodstaxiAdmin/discoveryconfigcs/updateDiscoveryConfig',
            method: 'post',
            data: opts,
            test: 'basicConfigWeb/basicConfigModules/discoveryPreload/success.json'
          });
        },
        detailPreload: function (opts) {
          return $http({
            url: '/goodstaxiAdmin/discoveryconfigcs/selectDiscoveryConfigById',
            method: 'post',
            data: opts,
            test: 'basicConfigWeb/basicConfigModules/discoveryPreload/discoveryPreload.json'
          });
        }
      };
    }]);
  }
});