define([
    'angular'
], function(
    angular
) {
    return function(app, elem, attrs, scope) {
        app.controller('msgDetailsCtrl', ['$rootScope', '$scope', '$timeout', 'G', '$stateParams', 'navsData', 'msgMgmtService', function($rootScope, $scope, $timeout, G, $stateParams, navsData, service) {

            $scope.switchType = function(key, type){
                $scope.searchParam[key] = type || '';
                $scope.fetchList();
            };

            $scope.fetchList = function(){
                $scope.fetch({currPage:1});
            };

            $scope.formatData = function(items){
                if(items){
                    $scope.displayUnreadCount = items.todocount>0?items.todocount:'';
                }
            };

            function getMsgDetails(){
                service.getMsgDetails({
                    workthingid: $stateParams.id
                }).then(function(data){
                    if(data.data&&data.data.data&&data.data.result==='success'){
                        $scope.msgDetails = data.data.data[0];
                        if($scope.msgDetails.url){
                            var urlArr = $scope.msgDetails.url.split('(');
                            $scope.pageName = urlArr[0];
                            $scope.pageUrl = urlArr[1].substring(0, urlArr[1].length-1);
                        }
                    }
                });
            }

            function init(){
                $scope.apiUrl = service.msgReceiverUrl;
                $scope.searchParam = {
                    workthingid: $stateParams.id
                };
                getMsgDetails();
            }

            init();
        }]);
    }
});