define([
    '../roleAuthority/roleAuthorityCtrl',
    '../roleAuthority/roleAuthorityService',
    'scmsModules/pagination/paginationDirective',
    'scmsModules/tooltip/tooltip',
    '../roleTreeView/roleTreeView',
    '../roleAuthority/roleAuthority.html'
], function(
    ctrl,
    service,
    paginationDirective,
    tooltip,
    roleTreeView,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        paginationDirective(app, elem, attrs, scope);
        tooltip(app, elem, attrs, scope);
        roleTreeView(app, elem, attrs, scope);
        elem.append(html);
    }
});