define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('addNewRoleService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                insertRbacRole: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacrolecs/insertRbacRole',
                        method: 'post',
                        data:options,
                        noconcurrent: true,
                        test:'basicConfigModules/header/success.json'
                    });
                },
                updateRbacRole: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacrolecs/updateRbacRole',
                        method: 'post',
                        data:options,
                        noconcurrent: true,
                        test:'basicConfigModules/header/success.json'
                    });
                }
            };
        }]);
    }
});