import ctrl from './questionBank/index';
import service from './common/service';
import tableDirective from 'scmsModules/table/table';
import questionDirective from './questionBank/questionEdit';
import html from './questionBank/index.html';

module.exports = (app, elem, attrs, scope) => {
  ctrl(app, elem, attrs, scope);
  service(app, elem, attrs, scope);
  tableDirective(app, elem, attrs, scope);
  questionDirective(app, elem, attrs, scope);
  elem.append(html);
}