define([
    '../basicDeptMgmt/basicDeptMgmtCtrl',
    '../basicDeptMgmt/basicDeptMgmtService',
    '../treeView/treeView',
    '../addNewDeptDialog/addNewDeptDialogDirective',
    'scmsModules/pagination/paginationDirective',
    'scmsModules/tooltip/tooltip',
    '../basicDeptMgmt/basicDeptMgmt.html',
    '../basicDeptMgmt/basicDeptMgmt.css',
], function(
    ctrl,
    service,
    treeView,
    addNewDeptDialogDirective,
    paginationDirective,
    tooltip,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        treeView(app, elem, attrs, scope);
        addNewDeptDialogDirective(app, elem, attrs, scope);
        paginationDirective(app, elem, attrs, scope);
        tooltip(app, elem, attrs, scope);
        elem.append(html);
    }
});