define([
    '../systemMonitor/systemMonitorCtrl',
    '../systemMonitor/systemMonitorService',
    'scmsModules/table/table',
    '../systemMonitor/systemMonitor.html',
    '../systemMonitor/systemMonitor.css'
], function (
    ctrl,
    service,
    table,
    html) {
        return function (app, elem, attrs, scope) {
            ctrl(app, elem, attrs, scope);
            service(app, elem, attrs, scope);
            table(app, elem, attrs, scope);
            elem.append(html);
        }
    });