define([
    'angular'
], function(
    angular
    ) {
    return function(app, elem, attrs, scope) {
        app.controller('basicAreaMgmtCtrl', ['$scope', '$state', '$stateParams', '$timeout', 'G', 'basicAreaMgmtService', 'navsData',
            function($scope, $state, $stateParams, $timeout, G, service, navsData) {
                var searchLock = false;
                $scope.introduction = '市场是指城市的业务区域，是货主的聚集地，一般按照物流市场，物流园区，工业园区等划分，总部等其他组织则不需要配置业务区域。支持新增，修改，查看，删除。';
                $scope.topPos='right';
                $scope.marketList = {
                   totalCount: 0
                };
                $scope.searchResult = false;
                // $scope.pageSize = G.getPageSize();

                function init(){
                    $scope.permission = navsData.permission;
                    getCityByUser();
                }

                function getCityByUser(){
                    service.getCityByUser({
                        organizationcode: G.userInfo.organizationcode,
                        returnformat: 3
                    }).then(function(data){
                        $scope.cityList = [];
                        if(data&&data.data&&data.data.result==='success'){
                            angular.forEach(data.data.data, function(item){
                                if(item.parorganizationcode==='0'){
                                    item.organizationname='全国';
                                }
                                $scope.cityList.push(item);
                                if(item.organizationcode===G.userInfo.organizationcode){
                                    $scope.selectedCity=item;
                                }
                            });
                        }
                    });
                }

                $scope.remove = function(item, $event){
                    var $alert = G.confirm({
                        $target: $($event.target),
                        msg: '确认删除？',
                        position: 'bottom',
                        submit: function() {
                            service.updateMarket({
                                ehdrbacmarketplaceid: item.ehdrbacmarketplaceid,
                                marketplacestatus: 9
                        }).then(function(data) {
                                data = data.data;
                                $alert.hide();
                                if(data.result === 'success') { 
                                    G.alert('删除成功');                                                         
                                    getMarketList($scope.currPage);
                                }else {
                                    G.alert(data.msg || '操作失败');
                                }                                        
                            }, function(data) {
                                G.alert(data.msg);
                            });    
                        }
                    });
                };

            $scope.getStaff = function(item){
               $state.go('ownStaffList.ownStaffList',{marketplaceid: item.ehdrbacmarketplaceid});
            };

            $scope.search = function(){
                $scope.searchResult=false;
                $scope.searchFlag=true;
                $scope.keywordsCopy=G.clone($scope.keywords);
                getMarketList(1);
                searchLock = true;
            };
            $scope.returnPre = function() {
                $scope.keywords = '';
                $scope.searchResult = false;
                getMarketList();
            }

            $scope.selectItem = function(item){
                $scope.selectedItem = item;
                $scope.isUpdate = true;
            };

            $scope.isAdd = function(){
                $scope.isUpdate = false;
            };

            $scope.$watch('selectedCity',function(newValue,oldValue){
                if(newValue&&newValue.organizationcode) {
                    getMarketList(1);
                }
            }, true)

            function getMarketList(currPage){
                $scope.currPage = currPage || 1;
                $scope.showAddGuide = false;
                $scope.searchResult=false;
                if(!$scope.selectedCity||!$scope.selectedCity.organizationcode) {
                    return;
                }
                G.loading(true);
                service.getMarketList({
                    marketplacenamelike: $scope.keywords,
                    organizationcode:$scope.selectedCity.organizationcode,
                    skipCount: ($scope.currPage - 1) * $scope.pageSize,
                    pageSize: $scope.pageSize
                }).then(function(data){
                    searchLock = false;
                    if(data&&data.data&&data.data.result==='success'){
                        $scope.marketList.data = data.data.data;
                        if($scope.searchFlag){
                            if($scope.marketList.data.length===0){
                                $scope.searchResult=true;
                            }
                        }
                        $scope.searchFlag = false;
                        $scope.marketList.totalCount = data.data.count;
                        angular.forEach($scope.marketList.data, function(market){
                            if(market.province){
                                market.address = market.province;
                            }
                            if(market.city){
                                market.address = market.address + ' | ' + market.city;
                            }
                            if(market.region){
                                market.address = market.address + ' | ' + market.region;
                            }
                        })
                        if($scope.marketList.totalCount===0 && ($scope.keywords===''||$scope.keywords==null)){
                            $scope.showAddGuide = true;
                        }
                    }else{
                        G.errorAlert('查询失败', data);
                    }
                    G.loading(false);
            },function(data){
                G.loading(false);
                searchLock = false;
                G.errorAlert('查询失败', data);    
            });
            }

            $scope.$watch('currPage + pageSize', function(newValue, oldValue) {
                if(newValue && oldValue && newValue !== oldValue && !searchLock) {
                    getMarketList($scope.currPage);
                }
                searchLock = false;
            });

            $scope.$on('addMarketSuccess', function(){
                getMarketList();
            });

            $scope.$on('updateOrg',function(){
                init();
            });

            init();
        }]);

        }
    });