define([
    'angular'
], function (angular) {
    return function (app, elem, attrs, scope) {
        app.factory('systemMonitorDetailService', ['$rootScope', '$http', 'G', function ($rootScope, $http, G) {
            return {
                getList: function (opts) {
                    return $http({
                        url: '/ehuodiGateway/ehuodiBdCrmApi/monitorcs/selectDetailListByCondition',
                        method: 'post',
                        data: opts,
                        // isDebug:true,
                        test: '/basicConfigWeb/basicConfigModules/systemMonitorDetail/systemMonitorDetail.json'
                    });
                },
                getOrg: function (opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacorganizationcs/selectCascadeRbacOrganizationByCode',
                        method: 'post',
                        data: opts
                    });
                },
                getDetail: function (opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/dataserviceswebcs/obtainData',
                        method: 'post',
                        data: opts,
                    });
                }
            };
        }]);
    }
});