define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('systemSigninStatisticsService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                getSigninReportList: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/worksigncs/queryWorkSignList',
                        method: 'post',
                        data: opts
                    });
                },
                getSigninReportListUrl: '/ehuodiBedrockApi/worksigncs/queryWorkSignList',
            };
        }]);
    }
});