define([
    '../tfLySysMenuMgmt/tfLySysMenuMgmtCtrl',
    '../tfLySysMenuMgmt/tfLySysMenuMgmtService',
    '../treeView/treeView',
    '../nestableTree/nestableTree',
    '../tfLyAddNewMenu/tfLyAddNewMenu',
    'scmsModules/tooltip/tooltip',
    '../confirmDialog/confirmDialog',
    '../tfLySysMenuMgmt/tfLySysMenuMgmt.html',
    '../tfLySysMenuMgmt/tfLySysMenuMgmt.css'
], function(
    ctrl,
    service,
    treeView,
    nestableTree,
    addNewMenu,
    tooltip,
    confirmDialog,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        treeView(app, elem, attrs, scope);
        nestableTree(app, elem, attrs, scope);
        addNewMenu(app, elem, attrs, scope);
        tooltip(app, elem, attrs, scope);
        confirmDialog(app, elem, attrs, scope);
        elem.append(html);
    }
});