define([
    '../systemLogStatistics/systemLogStatisticsCtrl',
    'scmsModules/datePicker/datePickerDirective',
    'scmsModules/table/table',
    'scmsModules/businessCitySelect',
    '../systemLogStatistics/logDetailsDialog',
    '../systemLogStatistics/systemLogStatisticsService',
    '../systemLogStatistics/systemLogStatistics.html',
    '../systemLogStatistics/systemLogStatistics.css'
], function(
    ctrl,
    datePickerDirective,
    table,
    businessCitySelect,
    logDetailsDialog,
    service,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        datePickerDirective(app, elem, attrs, scope);
        table(app, elem, attrs, scope);
        businessCitySelect(app, elem, attrs, scope);
        logDetailsDialog(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        elem.append(html);
    }
});