define([
    'angular'
    ], function(angular) {
        return function(app, elem, attrs, scope) {
            app.factory('systemMenuMgmtService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
                return {    
                    selectRbacResourceByParam: function(opts) {
                        return $http({
                          url: '/ehuodiBedrockApi/ehdrbacresourcecs/selectRbacResourceByParam',
                          method: 'post',
                          data:opts,
                          test: 'basicConfigModules/systemMenuMgmt/systemMenuMgmt.json'//假数据api
                        });
                    }, 
                    updateRbacResource: function(opts) {
                        return $http({
                          url: '/ehuodiBedrockApi/ehdrbacresourcecs/updateRbacResource',
                          method: 'post',
                          data:opts,
                        test: 'basicConfigModules/systemMenuMgmt/updateRbacResource.json'//假数据api
                     });
                    }
                };
            }]);
        }
    });