define([
  'angular'
], function (angular) {
  return function (app, elem, attrs, scope) {
    app.controller('basicPreloadCtrl',['$scope', 'G', 'basicPreloadService','$state', 'navsData',function ($scope, G, service,$state, navsData) {
          $scope.permission = navsData.permission || {};
          
          $scope.types = [
            {'text': '全部', 'value': ''},
            { 'text': '司机端', 'value': '0' },
            { 'text': '货主端', 'value': '1' },
            {'text':'网站web端','value':'2'}
          ];
          $scope.type = $scope.types[0];
          $scope.apiUrl = service.listPreload;

          $scope.typeFilter = function(type){
            var obj = {0:'司机端',1:'货主端',2:'网站Web端'};
            return obj[type]||'';
          }

          $scope.search = function(){
             $scope.fetch({ currPage: 1 });
          };
          $scope.add = function(){
            $state.go('addBasicPreload.addBasicPreload');
          };
          $scope.modify = function(urlkey){
            $state.go('addBasicPreload.addBasicPreload',{urlkey:urlkey});
          }

          // 删除
          $scope.remove = function (urlkey) {
            $.alert('此操作将永久删除该条预加载，是否继续？', {
              type: 'confirm',
              title: '预加载删除',
              callback: function () {
                var isSend = false;//防止多次提交
                if(!isSend){
                  isSend = true;
                  service.deletePreload({
                  urlkey: urlkey
                }).then(function (data) {
                    isSend = false;
                  if (data && data.data && data.data.result === 'success') {
                    G.alert(data.data.msg || '删除成功');
                    $scope.fetch({ currPage: $scope.currPage });
                  } else {
                    G.alert(data.data.msg || '删除失败', {type: 'error'});
                  }
                }, function (data) {
                  isSend = false;
                  G.alert(data.data.msg || '删除失败', {type: 'error'});
                });
                }
                
              }
            });
          };

          $scope.$watch('type.text',function(newValue,oldValue){
                if(newValue!=oldValue){
                    $scope.params.type = $scope.type.value;
                    $scope.fetch({currPage:1});
                }
            });
          $scope.$watch('params.type',function(newValue,oldValue){
              angular.forEach($scope.types,function(item){
                  if(item.value===newValue){
                      $scope.type = item;
                  }
              });
          });

          $scope.select = function(key){
            key && ($scope.urlkey = key);
          }

          $scope.$on('updateList', function () {
            $scope.fetch({ currPage: 1 });
          });

        }])
  };
});