define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('siteMapService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                selectRbacResourceByParam: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacresourcecs/selectRbacResourceByParam',
                        method: 'post',
                        data: opts
                    });
                },

                selectPubDictionaryListByParam: function(options) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdpubdictionarycs/selectPubDictionaryListByParam',
                        method: 'post',
                        data: options
                    });
                }
            };
        }]);
    }
});