define([
    'angular',
    '../selectCity/selectCityDirective',
    'scmsModules/datePicker/datePickerDirective',
    'scmsModules/formRadio/formRadio',
    '../directiveFormInput/bcFormSelectMulti',
    '../addNewCity/addNewCityDirective.html',
    '../addNewCity/addNewCityDialog.css',
    ], function(
        angular,
        selectCityDirective,
        datePickerDirective,
        formRadio,
        bcFormSelectMulti,
        html) {
        return function(app, elem, attrs, scope) {
            selectCityDirective(app, elem, attrs, scope);
            datePickerDirective(app, elem, attrs, scope);
            formRadio(app, elem, attrs, scope);
            bcFormSelectMulti(app, elem, attrs, scope);
            app.directive('addNewCityDirective', ['systemOrgMgmtService','G', '$q',function(service,G,$q) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        parorganizationcode:'=',
                        selectedItem:'=',
                        opType:'=',
                        orgType:'='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude,$timeout, $log, $http, G) {
                        getcityStatus();
                        function init(){
                            $scope.radioList=[{
                                label: '直营城市',
                                value: '直营',
                            },
                            {
                                label: '合伙人城市',
                                value: '合伙人',
                            }];
                            $scope.formData={
                                selectedArea:{}
                            };
                            $scope.error={};
                            $scope.initAddress=false;
                            $scope.initWorkAddress=false;
                            $scope.initCity=false;
                            $scope.initTime=false;
                            $scope.initStatus=false;
                            getAreaList();
                            var promise = getOrgList();
                            return promise;
                        }

                        function getAreaList(){
                            service.selectAreaCodeName().then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    $scope.areaList = data.data.data;
                                }
                            });
                        }

                        function getOrgList(){
                            var deferred = $q.defer(),
                                promise = deferred.promise;
                            service.selectDictionaryList({
                                parentdictionarycode: 'businessType'
                            }).then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    $scope.businessTypeList = data.data.data;
                                    deferred.resolve();
                                }
                            });
                            return promise;
                        }

                        $scope.updateData = +(new Date());

                        $('#addNewCity').on('show.bs.modal', function (e) {
                            $scope.$apply(function(){
                                init().then(function(){
                                    if($scope.opType==='edit') {
                                        $scope.formData.title='修改';
                                        var data=G.clone($scope.selectedItem);
                                        angular.forEach($scope.radioList, function(item,index){
                                            if(item.value===data.organizationmodel){    
                                                item.checked=true;
                                                $scope.formData.radioModel=data.organizationmodel;
                                            }else {
                                                item.checked=false;
                                            }
                                        });
                                        $scope.formData.address={};
                                        $scope.formData.workAddress={};
                                        $scope.formData.selectedArea.areacode=data.organizationcode;
                                        $scope.formData.address.province=data.province;
                                        $scope.formData.address.city=data.city;    
                                        $scope.formData.organizationname=data.organizationname; 
                                        $scope.formData.workAddress.province=data.managerprovince;
                                        $scope.formData.workAddress.city=data.managercity;
                                        $scope.formData.workAddress.region=data.managerregion;
                                        $scope.formData.managerjobcard=data.managerjobcard;
                                        $scope.formData.activatedate=data.activatedate.slice(0,10);
                                        if(data.managermobilenumber) {
                                            $scope.formData.managername=data.managername+'/'+data.managermobilenumber;
                                        }else {
                                            $scope.formData.managername=data.managername;
                                        }
                                        $scope.formData.workstreet=data.managerstreet;
                                        $scope.formData.isactivated=matchStatus(data.isactivated);                        
                                        $scope.formData.description=data.description;                                                                        
                                        $scope.updateData = +(new Date());
                                        $scope.formData.businessType = getMatchArray(data.opencitytype,'dictionarycode',$scope.businessTypeList);
                                    }else{
                                        $scope.formData.title='新增';
                                        $scope.formData.address={};
                                        $scope.formData.workAddress={};
                                        $scope.updateData = +(new Date());
                                    }    
                                });
                                
                            })
                        })
                        
                        $('#addNewCity').on('hide.bs.modal',function(e){
                            $scope.managernames=[];
                            G.clearAllError();
                        });

                        function getMatchArray(str,field,lists){
                            if(!str){
                                return '';
                            }
                            var arr=str.split(','),
                            result=[];
                            angular.forEach(arr, function(item){
                                angular.forEach(lists,function(checkedItem){
                                    if(checkedItem[field]==item){
                                        result.push(checkedItem);
                                    }
                                })
                            })
                            return result;
                        }

                        $scope.changeArea = function(){
                            $scope.formData.organizationname = $scope.formData.selectedArea.areaname;
                        };

                        $scope.$watch('formData.address',function(newVal,oldVal){
                            if(newVal){
                                if(!newVal.province||!newVal.city){
                                    $scope.errorAddress=true;
                                }else{
                                    $scope.errorAddress=false;
                                }
                            }
                        },true);
                        $scope.$watch('formData.workAddress',function(newVal,oldVal){
                            if(newVal){
                                if(!newVal.province||!newVal.city||!newVal.region){
                                    $scope.errorWorkAddress=true;
                                }else{
                                    $scope.errorWorkAddress=false;
                                }
                            }
                        },true);
                        $scope.$watch('formData.radioModel',function(newVal,oldVal){
                            if(!newVal){
                                $scope.error && ($scope.error.cityType='请选择城市类型');
                            }else{
                                $scope.error && ($scope.error.cityType='');
                            }
                        },true);
                        $scope.$watch('formData.activatedate',function(newVal,oldVal){
                            if(!newVal){
                                $scope.itemError=true;
                            }else{
                                $scope.timeError=false;
                            }
                        },true);                        
                        $scope.$watch('formData.isactivated',function(newVal,oldVal){
                            if(!newVal||!newVal.dictionaryname){
                                $scope.statusError=true;
                            }else{
                                $scope.statusError=false;
                            }

                        },true);

                        function matchStatus(status){
                            var ret;
                            angular.forEach($scope.cityStatuses, function(item,index){
                                if(item.dictionaryname===status) {
                                    ret=item;
                                }
                            })
                            return ret;
                        }

                        function getcityStatus(){
                            service.selectDictionaryList({
                                parentdictionarycode:'cityState'
                            }).then(function(data){
                                $scope.cityStatuses=data.data.data;
                            })
                        }

                        $scope.search=function(){
                            $scope.error = {};
                            service.selectRbacUserByParam({
                                keywords:$scope.formData.managername
                            }).then(function(data){
                                $scope.managernames=[];
                                if(data.data.data.length){
                                    angular.forEach(data.data.data, function(item,index){
                                        $scope.managernames.push({
                                            name: item.username+'/'+item.usermobilenumber,
                                            jobcard: item.jobcard
                                        })
                                    });
                                }else{
                                    $scope.error.managername = '没有该联系人';
                                }
                            })
                        };

                        $scope.reset=()=>{
                            $scope.displayManager = '';
                            $scope.formData.managername = '';
                            $scope.formData.managerjobcard = '';
                        };

                        $scope.managerSelect=function(item){
                            $scope.managernames=[];
                            $scope.formData.managername=item.name;
                            $scope.formData.managerjobcard = item.jobcard;
                            $('.manager .error-msg').html(' ');
                        }

                        $scope.clickOnblur=function(){
                            $scope.managernames=[];
                        };

                        $scope.submit=function(){
                            $scope.error = {};
                            service.selectRbacUserByParam({
                                keywords:$scope.formData.managerjobcard?$scope.formData.managerjobcard:$scope.formData.managername.split('/')[0]
                            }).then(function(data){
                                $scope.managernames=[];
                                if(data&&data.data&&data.data.result==='success'&&data.data.data.length){
                                    if(data.data.data.length>1){
                                        $scope.error.managername = '请搜索选择联系人';
                                    }else{
                                        let result = data.data.data[0];
                                        $scope.formData.managername = result.username + '/' + result.usermobilenumber;
                                        $scope.formData.managerjobcard = result.jobcard;
                                        submitFrom();
                                    }
                                }else{
                                    $scope.error.managername = '没有该联系人';
                                }
                            })
                        }

                        function arrayToString(arr,feild) {
                            if(!arr.length){
                                return;
                            }
                            var ret='';
                            for(var i=0,len=arr.length;i<len;i++) {
                                if(arr[i][feild]){
                                    ret=ret+','+arr[i][feild];    
                                }
                            }
                            return ret.slice(1);
                        }

                        function submitFrom(){
                            if(!$scope.formData.address.province||!$scope.formData.address.city){
                                $scope.errorAddress=true;
                                $scope.initAddress=true;
                                return;
                            }
                            if(!$scope.formData.radioModel) {
                                $scope.error.cityType='请选择城市类型';
                                return;
                            }
                            if(!$scope.formData.businessType || !$scope.formData.businessType.length) {
                                $scope.error.businessType='请选择城市业务';
                                return;
                            }
                            if(!$scope.formData.activatedate) {
                                $scope.timeError=true;
                                $scope.initTime=true;
                                return;
                            }

                            if(!$scope.formData.workAddress.province||!$scope.formData.workAddress.city||!$scope.formData.workAddress.region){
                                $scope.errorWorkAddress=true;
                                $scope.initWorkAddress=true;
                                return;
                            }
                            if(!$scope.formData.isactivated||!$scope.formData.isactivated.dictionaryname) {
                                $scope.statusError=true;
                                $scope.initStatus=true;
                                return;
                            }

                            if($scope.opType==='edit'){
                                service.updateRbacOrganization({
                                    organizationcode:$scope.formData.selectedArea.areacode,
                                    ehdrbacorganizationid:$scope.selectedItem.ehdrbacorganizationid,
                                    province:$scope.formData.address.province,
                                    city:$scope.formData.address.city,   
                                    organizationname:$scope.formData.organizationname,
                                    organizationmodel:$scope.formData.radioModel,
                                    activatedate:$scope.formData.activatedate+' 00:00:01',
                                    managername:$scope.formData.managername.split('/')[0],
                                    managermobilenumber:$scope.formData.managername.split('/')[1],
                                    managerjobcard: $scope.formData.managerjobcard,
                                    managerprovince:$scope.formData.workAddress.province,
                                    managercity:$scope.formData.workAddress.city,
                                    managerregion:$scope.formData.workAddress.region,
                                    managerstreet:$scope.formData.workstreet,
                                    isactivated:$scope.formData.isactivated.dictionaryname,
                                    description:$scope.formData.description,
                                    opencitytype:arrayToString($scope.formData.businessType,'dictionarycode')
                                }).then(function(data){
                                    if(data.data.result=='success'){
                                        $('#addNewCity').modal('hide');
                                        G.alert('修改城市成功!');
                                        $scope.$emit('refresh');
                                    }else{
                                        G.alert(data.data.msg,{type:'error',$container:$element.find('.modal-content')});
                                    }
                                })
                            }else{
                                service.insertRbacOrganization({
                                    parorganizationcode:$scope.parorganizationcode,
                                    organizationcode:$scope.formData.selectedArea.areacode,
                                    organizationtype:$scope.orgType.dictionaryname,
                                    country:$scope.formData.address.country,
                                    province:$scope.formData.address.province,
                                    city:$scope.formData.address.city,
                                    organizationname:$scope.formData.organizationname,
                                    organizationmodel:$scope.formData.radioModel,
                                    activatedate:$scope.formData.activatedate+' 00:00:01',
                                    managername:$scope.formData.managername.split('/')[0],
                                    managermobilenumber:$scope.formData.managername.split('/')[1],
                                    managerjobcard: $scope.formData.managerjobcard,
                                    managerprovince:$scope.formData.workAddress.province,
                                    managercity:$scope.formData.workAddress.city,
                                    managerregion:$scope.formData.workAddress.region,
                                    managerstreet:$scope.formData.workstreet,
                                    isactivated:$scope.formData.isactivated.dictionaryname,
                                    description:$scope.formData.description,
                                    opencitytype:arrayToString($scope.formData.businessType,'dictionarycode')
                                }).then(function(data){
                                    if(data.data.result=='success'){
                                        $('#addNewCity').modal('hide');
                                        G.alert('新建城市成功!');
                                        $scope.$emit('refresh');
                                    }else{
                                        G.alert(data.data.msg,{type:'error',$container:$element.find('.modal-content')});
                                    }
                                })
                            }
                        }


                    }
                };
            }]);
}
});