define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('basicOrgChartService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                getOrgChart: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacorganizationcs/selectRbacOrganizationByParam',
                        method: 'post',
                        data: opts,
                        test: 'basicConfigModules/basicOrgChart/basicOrgChart.json'//假数据api
                    });
                }
            };
        }]);
    }
});