define([
    '../systemSigninStatistics/systemSigninStatisticsCtrl',
    'scmsModules/datePicker/datePickerDirective',
    'scmsModules/table/table',
    'scmsModules/businessCitySelect',
    '../systemSigninStatistics/signinDetailsDialog',
    '../systemSigninStatistics/systemSigninStatisticsService',
    '../systemSigninStatistics/systemSigninStatistics.html',
    '../systemSigninStatistics/systemSigninStatistics.css'
], function(
    ctrl,
    datePickerDirective,
    table,
    businessCitySelect,
    signinDetailsDialog,
    service,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        datePickerDirective(app, elem, attrs, scope);
        businessCitySelect(app, elem, attrs, scope);
        table(app, elem, attrs, scope);
        signinDetailsDialog(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        elem.append(html);
    }
});