define([
    '../systemRoleMgmt/systemRoleMgmtCtrl',
    '../systemRoleMgmt/systemRoleMgmtService',
    '../addNewRole/addNewRoleDirective',
    'scmsModules/table/table',
    '../systemRoleMgmt/systemRoleMgmt.html',
    '../systemRoleMgmt/systemRoleMgmt.css'
], function(
    ctrl,
    service,
    addNewRoleDirective,
    table,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        addNewRoleDirective(app, elem, attrs, scope);
        table(app, elem, attrs, scope);
        elem.append(html);
    }
});