define([
    'angular',
    '../chooseOrg/chooseOrgService',
    '../chooseOrg/chooseOrgDirective.html',
    '../chooseOrg/chooseOrgDialog.css',
    ], function(
        angular,
        chooseOrgService,
        html) {
        return function(app, elem, attrs, scope) {
            chooseOrgService(app, elem, attrs, scope);
            app.directive('chooseOrgDirective', ['G','chooseOrgService',function(G,service) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        orgType:'='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude, $log, $http, G) {
                        service.selectDictionaryList({
                            parentdictionarycode:'orgType'
                        }).then(function(data){
                            $scope.orgTypes=data.data.data;
                            $scope.orgType=$scope.orgTypes[0];
                        })

                        $('#chooseOrg').on('show.modal.bs',function(){
                            $scope.$apply(function(){
                                $scope.orgType=$scope.orgTypes[0];
                            })
                        })

                    }

                };
            }]);
        }
    });