define([
    'angular',
    '../versionDetailsDialog/versionDetailsDialog.html'
    ], function(
        angular,
        html) {
        return function(app, elem, attrs, scope) {
            app.directive('versionDetailsDialogDirective', ['G', 'FileUploader', 'navsData',function(G, FileUploader, navsData) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        currentItem:'='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude,$timeout, $log, $http, G) {

                        $('#versionDetailsDialog').on('show.bs.modal',function(e){
                            $timeout(function(){
                                $scope.formData = G.clone($scope.currentItem);
                                if($scope.formData.navigations){
                                    angular.forEach(G.clone(navsData.navsList), function(item){
                                        if(item.resourceurl.indexOf($scope.formData.navigations)>-1){
                                            $scope.formData.displayNav = item.resourcename + '(' + $scope.formData.navigations + ')';
                                        }
                                    });
                                    if(!$scope.formData.displayNav){
                                        $scope.formData.displayNav = $scope.formData.navigations;
                                    }
                                }
                            });
                        });
                    }
                };
            }]);
}
});