define([
    'angular',
    '../staffDataPermission/staffDataPermissionService',
    '../staffDataPermission/staffDataPermission.html',
    '../staffDataPermission/staffDataPermission.css'
    ], function(
        angular,
        staffDataPermissionService,
        html) {
        return function(app, elem, attrs, scope) {
            staffDataPermissionService(app, elem, attrs, scope);
            app.directive('staffDataPermissionDirective', ['staffDataPermissionService','G', '$q', '$timeout', function(service,G,$q,$timeout) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        currentItem:'='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude,$timeout, $log, $http, G) {

                        function selectRbacOrganizationByParam(){
                            var orgArray = [];
                            if($scope.currentItem.dataauthorityorg){
                                orgArray = $scope.currentItem.dataauthorityorg.split(',');
                            }
                            service.selectRbacOrganizationByParam().then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    var orgList = data.data.data;
                                    angular.forEach(orgList, function(item){
                                        item.$$isExpend = true;
                                        angular.forEach(orgArray, function(org){
                                            if(item.organizationcode===org){
                                                item.dataAccessible = 1;
                                            }
                                        });
                                    });
                                    $scope.orgList = G.toTree(orgList,'0','parorganizationcode','organizationcode', null, true);
                                    $scope.orgList = new Array($scope.orgList);
                                }
                            });
                        }

                        function init(){
                            selectRbacOrganizationByParam();
                        }
                        
                        $scope.itemExpended = function(item, $event){
                            item.$$isExpend = !item.$$isExpend;
                            $event.stopPropagation();
                        };

                        $scope.checked = function(item){
                            var parent = {},
                                self = item;
                            self.dataAccessible = self.dataAccessible===1?0:1;
                            if(self.dataAccessible){
                                while(self.parentObj){
                                    parent = self.parentObj;
                                    parent.dataAccessible = 1;
                                    self = parent;
                                }
                            }
                            checkChildren(item);
                        };

                        function checkChildren(item){
                            angular.forEach(item.children,function(child){
                                child.dataAccessible = item.dataAccessible;
                                checkChildren(child);
                            });
                        }

                        $scope.getItemIcon = function(item){
                            checkIsLeaf(item);
                            if(item.isLeaf){
                                return '';
                            }
                            return item.$$isExpend ? 'fa fa-minus': 'fa fa-plus';
                        };

                        function checkIsLeaf(item){
                            item.isLeaf = false;
                            if(!item.children || !item.children.length){
                                item.isLeaf = true;
                            }
                        }

                        function expandBaner(tree,value,field,addfield,addValue) {
                            if(tree[field]===value){
                                tree[addfield]=addValue;
                                tree.active = true;
                            }
                            if(tree.children && tree.children.length>0){
                                for(var i=0,len=tree.children.length;i<len;i++){
                                    expandBaner(tree.children[i],value,field,addfield,addValue);
                                    if(tree.children[i][addfield]===addValue){
                                        tree[addfield]=addValue;
                                    }
                                }
                            }
                        }

                        $timeout(function(){
                            addActive();
                        },60);

                        $scope.$watch('defaultItem',function(){
                            $timeout(function(){
                                addActive();
                            },60);
                        })

                        function addActive() {
                            if($scope.defaultItem){
                                angular.forEach($scope.orgList, function(item,index){
                                    expandBaner(item,$scope.defaultItem[$scope.organizationcode],$scope.organizationcode,'$$isExpend',true);
                                })
                            }else{
                                angular.element('.tree-view span:eq(0)').addClass('active');
                            }
                        }

                        function orgToArray(tree, orgList){
                            orgList = orgList||[];
                            if(tree.organizationcode&&tree.dataAccessible){
                                orgList.push(tree.organizationcode);
                            }
                            angular.forEach(tree.children,function(child){
                                orgToArray(child, orgList);
                            });
                            return orgList;
                        }

                        $scope.submit = function(){
                            service.updateRbacUser({
                                ehdrbacusersid: $scope.currentItem.ehdrbacusersid,
                                dataauthorityorg: orgToArray($scope.orgList[0]).join(',')
                            }).then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    $('#staffDataPermission').modal('hide');
                                    $scope.$emit('refreshList');
                                    G.alert('数据权限修改成功!');
                                }
                            });
                        };

                        $('#staffDataPermission').on('show.bs.modal', function(){
                            init();
                        });

                        $('#staffDataPermission').on('hide.bs.modal', function(){
                            $('.org-tree-view').scrollTop(0);
                        });
                    }
                };
            }]);
    }
});