define([
    'angular'
    ], function(
        angular
        ) {
        return function(app, elem, attrs, scope) {
            app.controller('systemRoleMgmtCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'systemRoleMgmtService', 'navsData', 
                function($rootScope, $scope, $state, $stateParams, $timeout, G, service, navsData) {

                function init(){
                    $scope.permission = navsData.permission;
                    $scope.showGuide=false;
                    $scope.apiUrl = service.selectRbacRoleByParamUrl;          
                }

                $scope.remove=function($event, item) {
                    var $alert = G.confirm({
                        $target: $($event.target),
                        msg: '您确定删除该角色？',
                        position: 'bottom',
                        submit: function() {    
                            service.updateRbacRole({
                                ehdrbacroleid:item.ehdrbacroleid,
                                rolestatus:9
                            }).then(function(data) {
                                data = data.data;
                                if(data.result === 'success') {
                                    $alert.hide();
                                    G.alert('角色删除成功');
                                    $scope.fetch($scope.currPage);
                                }else {
                                    G.alert(data.msg || '角色删除失败', {
                                        type: 'error'
                                    });
                                    $alert.hide();
                                }
                            },
                            function() {
                                G.alert('角色删除失败', {
                                    type: 'error'
                                });
                                $alert.hide();
                            });
                        }    
                    });
                }
                
                $scope.selectCurrent = function(isEdit, item){
                    $scope.selectedRole = item;
                    $scope.editFlag = isEdit;
                };

                $scope.formatData = function(data, count){
                    $scope.totalCount = count;
                };

                $scope.$on('refresh',function(){
                    $scope.fetch($scope.currPage);
                });

                init();
            }]);

}
});