define([
    'angular'
    ], function(angular) {
        return function(app, elem, attrs, scope) {
            app.factory('addNewStaffService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
                return {   
                    getPositionList: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacpostcs/selectRbacPostByParam',
                            method: 'post',
                            data: opts,
                            test: 'basicConfigModules/basicPositionMgmt/basicPositionMgmt.json'//假数据api
                        });
                    },
                    getAllDept: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacdepartmentcs/selectRbacDepartmentByParam',
                            data:opts,
                            method: 'post',
                            test: 'basicConfigModules/basicDeptMgmt/basicDeptMgmt.json'//假数据api
                        });
                    },
                    getMarketList: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacmarketplacecs/selectRbacMarketPlaceByParam',
                            data:opts,
                            method: 'post',
                            test: 'basicConfigModules/basicAreaMgmt/basicAreaMgmt.json'//假数据api
                        });
                    },
                    insertUser: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacuserscs/insertRbacUser',
                            method: 'post',
                            data:opts,
                            noconcurrent: true,
                            test: 'basicConfigModules/header/success.json'//假数据api
                        });
                    },
                    updateUser: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacuserscs/updateRbacUser',
                            method: 'post',
                            data:opts,
                            noconcurrent: true,
                            test: 'basicConfigModules/header/success.json'//假数据api
                        });
                    },
                    selectUser: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacuserscs/selectRbacUserByParam',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigModules/basicStaffMgmt/basicStaffMgmt.json'//假数据api
                        });
                    },
                    selectDictionaryList: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdpubdictionarycs/selectPubDictionaryListByParam',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigModules/addNewStaff/addNewStaffDictionary.json'//假数据api
                        });
                    },
                    getAllOrg: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacorganizationcs/selectCascadeRbacOrganizationByCode',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigModules/addNewStaff/addNewStaffDictionary.json'//假数据api
                        });
                    },
                    getRoleList: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacrolecs/selectRbacRoleByParam',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigModules/addNewStaff/addNewStaffDictionary.json'//假数据api
                        });
                    },
                    getOrganization: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/ehdrbacorganizationcs/selectRbacOrganizationByParam',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigModules/addNewStaff/addNewStaffDictionary.json'//假数据api
                        });
                    }
                };
            }]);
        }
    });