import html from './addQuestion.html';
export default async function(app, elem, attrs, scope) {
  app.directive('addQuestionDialogDirective', [
    '$cookies',
    '$timeout',
    'annualPartyService',
    function($cookies, $timeout, service) {
      return {
        template: html,
        restrict: 'EA',
        replace: true,
        scope: {
          items: '='
        },
        link: function postLink($scope, $element, $attrs) {},
        controller: function(
          $scope,
          $element,
          $attrs,
          $transclude,
          G
        ) {
          var addQuestionDialog = $('#addQuestionDialog');

          function parse(json, defaultValue) {
            if (json === null || json === undefined) {
              return defaultValue;
            }
            try {
              return JSON.parse(json, function(key, value) {
                if (key === '$$hashKey') {
                  return undefined;
                }

                return value;
              });
            } catch (e) {
              return defaultValue;
            }
          }

          function stringify(json, defaultValue) {
            if (json === null || json === undefined) {
              return defaultValue;
            }
            try {
              return JSON.stringify(json, function(key, value) {
                if (key === '$$hashKey') {
                  return undefined;
                }

                return value;
              });
            } catch (e) {
              return defaultValue;
            }
          }

          function fetchQuestionList(lv){
            service.question.get({
              isRelated :2,
              levelType:lv,
              skipCount:0,
              pageSize:1000
            }).then(res=>{
              res = res.data;
              if(res.result=='success'){
                $scope.questions = res.data;
              }else{
                G.alert(res.msg || '获取试题失败', { type: 'error' });
              }
            },res=>{
              G.alert(res.data.msg || '获取试题失败', { type: 'error' });
            });
          }



          $scope.submit = function() {
            const {question} = $scope;

            $timeout(()=>{
              $scope.items.push(angular.fromJson(question));
              addQuestionDialog.modal('hide');
            },10);
          };

          $scope.$watch('levelType',(lv,oldlv)=>{
            // console.log(lv,oldlv);
            if(oldlv!==lv){
              $scope.question='';
              fetchQuestionList(lv);
            }
          });

          $('#addQuestionDialog').on('show.bs.modal', function() {
            $timeout(()=>{
              $scope.question='';
              $scope.levelType='';
            },10);

            // fetchQuestionList();

          });
          // $('#addQuestionDialog').on('hide.bs.modal', function() {

          // });
        }
      };
    }
  ]);
}
