define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('systemUpgradeMgmtService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                selectRbacResourceByParam: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/ehdrbacresourcecs/selectRbacResourceByParam',
                        method: 'post',
                        data: opts
                    });
                },
                queryVersionManageListUrl: '/ehuodiBedrockApi/versionmanagecs/queryVersionManageList',

                modifyVersionManage: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/versionmanagecs/modifyVersionManage',
                        method: 'post',
                        data: opts
                    });
                }
            };
        }]);
    }
});