define([
  'angular',
  '../basicPreload/preloadDetail.html',
], function (
  angular,
  html) {
    return function (app, elem, attrs, scope) {
      app.directive('preloadDetailDirective', ['basicPreloadService', 'G', function (service, G) {
        return {
          template: html,
          restrict: 'EA',
          scope: {
            urlkey: '='
          },
          link: function postLink($scope, $element, $attrs) {
          
            $scope.fetchData = function () {
              service.detailPreload({
                urlkey: $scope.urlkey
              }).then(function (data) {
                if (data && data.data && data.data.result === 'success') {
                  var obj = {0:'司机端',1:'货主端',2:'网站Web端'};
                  data.data.data.type=obj[data.data.data.type];
                  $scope.preloadData = data.data.data;
                  
                } else {
                  G.alert(data.data.msg || '查询详情失败', {
                    type: 'error'
                  });
                }
              }, function (data) {
                G.alert(data.data.msg || '查询详情失败', {
                  type: 'error'
                });
              });
            }


            $('#details').on('show.bs.modal', function (e) {
              $scope.preloadData = '';
              $scope.fetchData();
            });

          }
        };
      }]);
    }
  });