define([
    'angular',
    '../systemLogStatistics/logDetailsDialog.html',
    '../systemLogStatistics/logDetailsDialog.css'
], function(
    angular,
    html,css) {
    return function(app, elem, attrs, scope) {
        app.directive('logDetailsDialog', ['$timeout', 'systemLogStatisticsService', function($timeout, service) {
            return {
                template: html,
                restrict: 'EA',
                replace: true,
                scope: {
                    currentItem: '='
                },
                link: function postLink($scope, $element, $attrs) {

                },

                controller: function($scope,$element,$attrs,$transclude,$log,$http,G){

                    function init(){
                        service.queryWorkReport({
                            workreportid: $scope.currentItem.workreportid
                        }).then(function(data){
                            if(data.data&&data.data.result==='success'){
                                var reviewers = [],comments=[];
                                $scope.reportDetails = data.data.data;

                                comments = $scope.reportDetails.leafworkreport;
                                $scope.reportDetails.displayComments = '';
                                angular.forEach(comments, function(comment){
                                    if(comment.createuser){
                                        let user = comment.createuser.username + '/' + comment.createuser.jobcard;
                                        $scope.reportDetails.displayComments += user + ':';
                                        if(reviewers.indexOf(user)<0){
                                            reviewers.push(user);
                                        }
                                    }
                                    $scope.reportDetails.displayComments += comment.context + '<br>';
                                });
                                $scope.reportDetails.displayReviewers = reviewers.join(',');
                            }
                        });
                    }

                    $('#logDetailsDialog').on('show.bs.modal',function(){
                        init();
                    });

                }
            };
        }]);
    }
});
