define('basicConfigModules/basicPreload/basicPreload',[
  './basicPreloadCtrl',
  './basicPreloadService',
  'scmsModules/table/table',
  './preloadDetailDriective',
  './basicPreload.html',
  './basicPreload.css'
], function(ctrl, service,preloadDetailDriective,table, html) {
  return function(app, elem, attrs, scope) {
    ctrl(app, elem, attrs, scope);
    service(app, elem, attrs, scope);
    preloadDetailDriective(app, elem, attrs, scope);
    table(app,elem,attrs, scope);
    elem.append(html);
  };
});