define([
    '../basicPositionMgmt/basicPositionMgmtCtrl',
    '../basicPositionMgmt/basicPositionMgmtService',
    'scmsModules/pagination/paginationDirective',
    '../addNewPositionDialog/addNewPositionDialogDirective',
    'scmsModules/tooltip/tooltip',
    '../basicPositionMgmt/basicPositionMgmt.html',
    '../basicPositionMgmt/basicPositionMgmt.css'
], function(
    ctrl,
    service,
    paginationDirective,
    addNewPositionDialogDirective,
    tooltip,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        paginationDirective(app, elem, attrs, scope);
        addNewPositionDialogDirective(app, elem, attrs, scope);
        tooltip(app, elem, attrs, scope);
        elem.append(html);
    }
});