define([
    'angular',
    '../confirmDialog/confirmDialog.html',
    '../confirmDialog/confirmDialog.css'
    ], function(
        angular,
        html) {
        return function(app, elem, attrs, scope) {
            app.directive('confirmDialog', ['G', '$timeout',function(G, $timeout) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        confirmMsg:'='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude,$timeout, $log, $http, G) {
                        $scope.submit = function(){
                            $scope.$emit('confirmChange');
                        };

                        $scope.cancel = function(){
                            $scope.$emit('cancelChange');
                        };

                    }
                };
            }]);
        }
    });