define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('systemLogConfigService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                getLogListUrl: '/ehuodiBedrockApi/logmonitorconfigcs/queryLogMonitorConfigcList',

                addLogConfig:function(opts){
                    return $http({
                        url: '/ehuodiBedrockApi/logmonitorconfigcs/addLogMonitorConfig',
                        method: 'post',
                        data:opts,
                        test: 'scmsModules/header/success.json'//假数据api
                    });                    
                },

                updateLogConfig:function(opts){
                    return $http({
                        url: '/ehuodiBedrockApi/logmonitorconfigcs/updateLogMonitorConfig',
                        method: 'post',
                        data:opts,
                        test: 'scmsModules/header/success.json'//假数据api
                    });                    
                },

                selectRbacResourceByParam: function(opts) {
                    return $http({
                      url: '/ehuodiBedrockApi/ehdrbacresourcecs/selectRbacResourceByParam',
                      method: 'post',
                      data:opts,
                      test: 'basicConfigModules/systemMenuMgmt/systemMenuMgmt.json'//假数据api
                    });
                },

                selectDictionaryList: function(opts) {
                    return $http({
                      url: '/ehuodiBedrockApi/ehdpubdictionarycs/selectPubDictionaryListByParam',
                      method: 'post',
                      data:opts,
                      test: 'basicConfigModules/systemMenuMgmt/systemMenuMgmt.json'//假数据api
                    });
                }
            };
        }]);
    }
});