define([
    'angular'
], function (angular) {
    return function (app, elem, attrs, scope) {
        app.factory('systemMonitorService', ['$rootScope', '$http', 'G', function ($rootScope, $http, G) {
            return {
                getFetchUrl: function () {
                    return '/ehuodiBedrockApi/dataserviceswebcs/obtainData';
                },
                // getFetchUrl: function () {
                //     return '/ehuodiGateway/ehuodiBdCrmApi/monitorcs/selectListByCondition';
                // },
                getCount: function (opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/dataserviceswebcs/obtainData',
                        method: 'post',
                        data: opts,
                    });
                }
            };
        }]);
    }
});