define([
    'angular'
    ], function(angular) {
        return function(app, elem, attrs, scope) {
            app.factory('errorCodeDialogService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
                return {   
                    addErrorConfig:function(opts){
                        return $http({
                            url: '/ehuodiBedrockApi/errorconfigcs/addErrorConfig',
                            method: 'post',
                            data:opts
                        });
                    },

                    modifyErrorConfigById:function(opts){
                        return $http({
                            url: '/ehuodiBedrockApi/errorconfigcs/modifyErrorConfigById',
                            method: 'post',
                            data:opts
                        });
                    }
                };
            }]);
        }
    });