define([
    'angular',
    '../addNewStruct/addNewStructService',
    '../addNewStruct/addNewStructDirective.html',
    '../addNewStruct/addNewStructDialog.css',
    ], function(
        angular,
        service,
        html) {
        return function(app, elem, attrs, scope) {
            service(app, elem, attrs, scope);
            app.directive('addNewStructDirective', ['addNewStructService','G',function(service,G) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        parorganizationcode:'=',
                        selectedItem:'=',
                        opType:'=',
                        orgType:'='
                    },          
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude,$timeout, $log, $http, G) {
                        $scope.formData={}
                        $scope.updateData=+(new Date());
                        $('#addNewStruct').on('show.bs.modal',function(){
                            $scope.$apply(function(){
                                $scope.initAddress=false;
                                $scope.formData={};
                                $scope.errors = {};
                                if($scope.opType==='edit') {
                                    $scope.formData.title='修改';
                                    var data=$scope.selectedItem;
                                    $scope.formData.address={};
                                    $scope.formData.organizationname=data.organizationname;
                                    $scope.formData.organizationcode=data.organizationcode;
                                    $scope.formData.activatedate=data.activatedate;
                                    $scope.formData.address.province=data.managerprovince;
                                    $scope.formData.address.city=data.managercity;
                                    $scope.formData.address.region=data.managerregion;
                                    $scope.formData.managerjobcard=data.managerjobcard;
                                    if(data.managermobilenumber) {
                                        $scope.formData.managername=data.managername+'/'+data.managermobilenumber;
                                    }else {
                                        $scope.formData.managername=data.managername;
                                    }
                                    $scope.formData.managerstreet=data.managerstreet;
                                    $scope.updateData=+(new Date());                                

                                }else {
                                    $scope.formData.title='新增';
                                    $scope.updateData=+(new Date());
                                }
                            })

                        });

                        $('#addNewStruct').on('hide.bs.modal',function(e){
                            $scope.managernames=[];
                            G.clearAllError();
                        });

                        $scope.clickOnblur=function(){
                            $scope.managernames=[];
                        };

                        $scope.search=function(){
                            $scope.errors = {};
                            service.selectRbacUserByParam({
                                keywords:$scope.formData.managername
                            }).then(function(data){
                                $scope.managernames=[];
                                if(data&&data.data&&data.data.result==='success'&&data.data.data.length){
                                    angular.forEach(data.data.data, function(item,index){
                                        $scope.managernames.push({
                                            name: item.username+'/'+item.usermobilenumber,
                                            jobcard: item.jobcard
                                        })
                                    })
                                }else{
                                    $scope.errors.managername = '没有该联系人';
                                }
                            })
                        };

                        $scope.reset=()=>{
                            $scope.displayManager = '';
                            $scope.formData.managername = '';
                            $scope.formData.managerjobcard = '';
                        };

                        $scope.managerSelect=function(item){
                            $scope.managernames=[];
                            $scope.formData.managername=item.name;
                            $scope.formData.managerjobcard = item.jobcard;
                            $('.manager .error-msg').html(' ');
                        }

                        $scope.$watch('formData.address',function(newVal,oldVal){
                            if(newVal){
                                if(!newVal.province||!newVal.city||!newVal.region){
                                    $scope.errorAddress=true;
                                }else{
                                    $scope.errorAddress=false;
                                }
                            }
                        },true);

                        $scope.submit = function(){
                            $scope.errors = {};
                            service.selectRbacUserByParam({
                                keywords:$scope.formData.managerjobcard?$scope.formData.managerjobcard:$scope.formData.managername.split('/')[0]
                            }).then(function(data){
                                $scope.managernames=[];
                                if(data&&data.data&&data.data.result==='success'&&data.data.data.length){
                                    if(data.data.data.length>1){
                                        $scope.errors.managername = '请搜索选择联系人';
                                    }else{
                                        let result = data.data.data[0];
                                        $scope.formData.managername = result.username + '/' + result.usermobilenumber;
                                        $scope.formData.managerjobcard = result.jobcard;
                                        validate();
                                    }
                                }else{
                                    $scope.errors.managername = '没有该联系人';
                                }
                            })
                        }

                        function validate(){
                            if($scope.formData.organizationcode==0){
                                $scope.errors.msg = '组织编码不能为0!';
                                G.alert('组织编码不能为0!',{type:'error',$container:$element.find('.modal-content')});
                                return;
                            }
                            if(!$scope.formData.address.province||!$scope.formData.address.city||!$scope.formData.address.region){
                                $scope.errorAddress=true;
                                $scope.initAddress=true;
                                return;
                            }

                            if($scope.formData.managername.indexOf('/')===-1){
                                $scope.formData.managername+='/';
                            }

                            if($scope.opType==='edit'){
                                service.updateRbacOrganization({
                                    ehdrbacorganizationid:$scope.selectedItem.ehdrbacorganizationid,
                                    organizationname:$scope.formData.organizationname,
                                    managername:$scope.formData.managername.split('/')[0],
                                    managermobilenumber:$scope.formData.managername.split('/')[1],
                                    managerjobcard: $scope.formData.managerjobcard,
                                    managerprovince:$scope.formData.address.province,
                                    managercity:$scope.formData.address.city,
                                    managerregion:$scope.formData.address.region,
                                    managerstreet:$scope.formData.managerstreet
                                }).then(function(data){
                                    if(data.data.result=='success'){
                                        $('#addNewStruct').modal('hide');
                                        G.alert('修改组织成功!');
                                        $scope.$emit('refresh')
                                    }else{
                                        $scope.errors.msg = data.data.msg;
                                        G.alert(data.data.msg,{type:'error',$container:$element.find('.modal-content')})
                                    }
                                });

                            }else{
                                service.insertRbacOrganization({
                                    parorganizationcode:$scope.parorganizationcode||'0',
                                    organizationname:$scope.formData.organizationname,
                                    organizationcode:$scope.formData.organizationcode,
                                    organizationtype:$scope.orgType.dictionaryname,
                                    managername:$scope.formData.managername.split('/')[0],
                                    managermobilenumber:$scope.formData.managername.split('/')[1],
                                    managerjobcard: $scope.formData.managerjobcard,
                                    managerprovince:$scope.formData.address.province,
                                    managercity:$scope.formData.address.city,
                                    managerregion:$scope.formData.address.region,
                                    managerstreet:$scope.formData.managerstreet
                                }).then(function(data){
                                    if(data.data.result=='success'){
                                        $('#addNewStruct').modal('hide');
                                        G.alert('新建组织成功!');
                                        $scope.$emit('refresh');
                                    }else{
                                        $scope.errors.msg = data.data.msg;
                                        G.alert(data.data.msg,{type:'error',$container:$element.find('.modal-content')})
                                    }
                                })
                            }

                        }
                    }
                };
            }]);
}
});