define([
    'angular',
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.directive('mobileAndTel',function($timeout){
            return {
                restrict: 'EA',
                require: 'ngModel',
                link: function($scope,$element,$attrs,ngModelController){
                    const reg = /^((\d{0,4}-?\d{7,8})|(\d{11}))$/;
                    var parsers = function(value){
                        if(!value || value.match(reg)){
                            ngModelController.$setValidity('mobileAndTel',true);
                        }else{
                            ngModelController.$setValidity('mobileAndTel',false);
                        }
                        return value;
                    }
                    var formatters=function(value){
                        if(value!==null&&value!==undefined){
                            //timeout解决弹框的动效导致执行顺序的问题
                            $timeout(()=>{
                                parsers(value.toString());
                            },200);
                        }
                        return value;
                    }
                    ngModelController.$parsers.push(parsers);
                    ngModelController.$formatters.push(formatters);
                }
            }
        });
        app.controller('tfLyAddAccountCtrl', ['$scope','$state','$stateParams', 'G','tfLyAddAccountService','defaultErrorMessageResolver', function($scope,$state,$stateParams,G,service,defaultErrorMessageResolver) {
            $scope.paramType = $stateParams.type;
            $scope.formData={};
            $scope.addrObj='';
            $scope.pageSize=10;
            $scope.minDate=G.formatDate(new Date(),'YYYY-MM-DD hh:mm:ss');
            $scope.imgUploadType='jpg,png,jpeg,gif';
            $scope.$watch('formData.buyenddate',(newVal,oldVal)=>{
                console.log(newVal,oldVal);
                if(newVal&&newVal!=oldVal){
                    $scope.formData.buyenddate=newVal.split(' ')[0]+' 23:59:59';
                }
            });
            if($scope.paramType && $scope.paramType === 'edit'){
                const data = angular.fromJson(sessionStorage.getItem('tfLyAccountMgmt'));
                $scope.addrObj = {
                    province: data.province,
                    city: data.city,
                    region: data.region,
                }
                $scope.minDate = data.buycreatedate;
                if(data.loginlogoimg){
                    $scope.loginLogoStatus=true;
                }
                if(data.indexlogoimg){
                    $scope.indexLogoStatus=true;
                }
                if(data.style){
                    $scope.styleStatus=true;
                }
                if(data.clientdirectorusername && data.clientdirectormobile){
                    $scope.clientDirector=`${data.clientdirectorusername}/${data.clientdirectormobile}`;
                }
                $scope.formData = { ...data };
                $scope.formData.buyenddate=$scope.formData.buyenddate?$scope.formData.buyenddate.split(' ')[0]+' 23:59:59':$scope.formData.buyenddate;
                $scope.formData.versionscode = $scope.formData.versionscode.toLocaleUpperCase();
            }
            $scope.chooseUser=(item)=>{
                $scope.isShowDropdown=false;
                $scope.userList=[];
                $scope.formData.clientdirectorusername=item.username;
                $scope.formData.clientdirectormobile=item.usermobilenumber;
                $scope.clientDirector=`${item.username}/${item.usermobilenumber}`;
            }
            $scope.isShowDropdown=false;
            //查询客户经理
            $scope.searchUsers=(opts,bool=false)=>{
                console.log(bool);
                if((!bool && $scope.isShowDropdown)||bool){
                    service.selectRbacUserByParam({
                        keywords:$scope.clientDirector,
                        pageSize:$scope.pageSize,
                        skipCount:(opts.currentPage - 1) * $scope.pageSize
                    }).then(res=>{
                        if(res.data.result === 'success'){
                            $scope.totalCount=res.data.count;
                            $scope.userList=res.data.data;
                            if(res.data.data.length==0){
                                G.alert('查无此人',{type: 'error'});
                                return $scope.isShowDropdown=false;
                            }
                            $scope.isShowDropdown=true;
                        }
                    });
                }
            }
            $scope.cancel=()=>{
                $state.go('tfLyAccountMgmt.tfLyAccountMgmt');
            }
            $scope.$watch('formData.buyenddate',(newVal,oldVal)=>{
                if(newVal){
                    $scope.buyenddateError=''
                }
            });
            $scope.submit=()=>{
                if(!$scope.formData.buyenddate){
                    return $scope.buyenddateError='请选择有效截止期';
                }
                if($scope.loginLogoStatus&&!$scope.formData.loginlogoimg){
                    return G.alert("请上传登录页自定义LOGO", {type: "error"});
                }
                if($scope.indexLogoStatus&&!$scope.formData.indexlogoimg){
                    return G.alert("请上传首页自定义LOGO", {type: "error"});
                }
                if($scope.styleStatus&&!$scope.formData.style){
                    return G.alert("请选择系统主VI风格", {type: "error"});
                }
                if($scope.clientDirector&&$scope.clientDirector!=`${$scope.formData.clientdirectorusername}/${$scope.formData.clientdirectormobile}`){
                    return G.alert("请搜索选择正确的客户经理", {type: "error"});
                }
                const {
                    clientname,linkmobilenumber,linkmanname,linkposition,versionscode,buyenddate,street,
                    orgscale,userscale,loginlogoimg,indexlogoimg,style,domainname,clientdirectormobile,
                    clientdirectorusername,province,city,region
                } = {...$scope.formData,...$scope.addrObj};
                const param = {
                    clientname,linkmobilenumber,linkmanname,linkposition,versionscode,buyenddate,street,
                    orgscale,userscale,loginlogoimg,indexlogoimg,style,domainname,clientdirectormobile,
                    clientdirectorusername,province,city,region
                };
                if(!$scope.styleStatus){
                    param.style = '';
                }
                if(!$scope.loginLogoStatus){
                    param.loginlogoimg = '';
                }
                if(!$scope.indexLogoStatus){
                    param.indexlogoimg = '';
                }
                let method =  'createUtmsClient';
                if($scope.paramType && $scope.paramType === 'edit'){
                    method =  'updateUtmsClient'
                    param.utmsclientid = $scope.formData.utmsclientid;
                }
                service[method](param).then(res=>{
                    G.alert(res.data.msg,{type:res.data.result});
                    return $state.go('tfLyAccountMgmt.tfLyAccountMgmt');
                }).catch(()=>{
                    return G.alert('请稍后重试',{type:'error'});
                })
            }
            function selectUtmsDictionaryList(){
                service.selectUtmsDictionaryList({
                    parentdictionarycode:'contactPosition',
                }).then(res=>{
                    if(res.data.result==='success'){
                        $scope.linkpositionList = res.data.data.reduce((total,item)=>{
                            total.push({
                                name:item.dictionaryname,
                                code:item.dictionarycode
                            })
                            return total;
                        },[]);
                    }
                });
            }
            selectUtmsDictionaryList();
            defaultErrorMessageResolver
                .getErrorMessages()
                .then(function (errorMessages) {
                    errorMessages['mobileAndTel'] = '请填写正确的手机号或座机号';
                });
        }]);
    }
});