define([
    'angular'
    ], function(angular) {
        return function(app, elem, attrs, scope) {
            app.factory('tfLySysMenuMgmtService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
                return {    
                    selectRbacResourceByParam: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/utmsresourcecs/selectUtmsResourceList',
                            method: 'post',
                            data:opts,
                        });
                    }, 
                    updateRbacResource: function(opts) {
                        return $http({
                            url: '/ehuodiBedrockApi/utmsresourcecs/updateUtmsResource',
                            method: 'post',
                            data:opts,
                            test: 'basicConfigModules/systemMenuMgmt/updateRbacResource.json'//假数据api
                        });
                    },
                    deleteUtmsResource: function(opts) {
                        return $http({
                            url:'/ehuodiBedrockApi/utmsresourcecs/deleteUtmsResource',
                            method: 'post',
                            data:opts,
                        })
                    },
                    updateUtmsResourceOrder: function(opts) {
                        return $http({
                            url:'/ehuodiBedrockApi/utmsresourcecs/updateUtmsResourceOrder',
                            method: 'post',
                            data:opts,
                        })
                    }
                };
            }]);
        }
    });