define([
    'angular'
], function (
    angular
) {
        return function (app, elem, attrs, scope) {
            app.controller('systemMonitorDetailCtrl', ['$rootScope', '$scope', '$state', '$stateParams', '$timeout', 'G', 'systemMonitorDetailService', function ($rootScope, $scope, $state, $stateParams, $timeout, G, service) {
                if(!$stateParams.resourceid){
                    $state.go('systemMonitor.systemMonitor');
                }
                var totalList;
                var resourceid = $stateParams.resourceid;
                function getList(){
                    G.loading(true);
                    service.getDetail(getParams()).then(function(data){
                        G.loading(false);
                        if(data.data.result === 'success'){
                            totalList = data.data.data;
                            $scope.totalCount = totalList&&totalList.length||0;
                            var start = ($scope.currentPage-1)*$scope.pageSize;
                            var end = start + $scope.pageSize;
                            $scope.lists = totalList&&totalList.slice(start, end)||[];   
                        }
                    })
                }
                $scope.maxDate = moment({hour: 23, minute: 59, seconds: 59}).add(-1,'day').format('YYYY-MM-DD HH:mm:ss');
                $scope.enddate = moment({hour: 23, minute: 59, seconds: 59}).add(-1,'day').format('YYYY-MM-DD HH:mm:ss');
                $scope.startdate = moment({hour: 0, minute: 0, seconds: 0}).add(-1,'month').format('YYYY-MM-DD HH:mm:ss');
                function getParams(){
                    let param = {
                        sqlKey: 'ehuodiCrmMonitorDetailList',
                        resourceid: resourceid,
                        keyword: $scope.keyword,
                        startdate: $scope.startdate,
                        enddate: $scope.enddate,
                        organizationcode: $scope.organization.organizationcode,
                    };
                    for(let key in param){
                        if(!param[key]){
                            delete param[key];
                        }
                    }
                     return param;
                }


                $scope.$watch('organization', function(newVal){
                    console.log(newVal)
                },true)

                $scope.search = function () {
                    $scope.currentPage = 1;
                    getList();
                }
                $scope.totalCount = 2000;

                $scope.pageChanged = function(opts) {
                    $scope.currentPage = opts.currentPage;
                    $scope.pageSize = opts.pageSize;
                    if(totalList){ 
                        var start = ($scope.currentPage-1)*$scope.pageSize;
                        var end = start + $scope.pageSize;
                        $scope.lists = totalList.slice(start, end);   
                    }
                }

                // $scope.export = function() {
                //     G.download('/ehuodiBDCrmApi/monitorcs/exportDetailsByCondition',getParams());
                // }

                function setCityCode(orgCode, cb) {
                    service.getOrg({
                      organizationcode: orgCode,
                      returnformat: 2,
                    }).then(function (data) {
                      if (data.data.result === 'success') {
                        if(orgCode=== '88888888') {
                            data.data.data[0].organizationname = '全国';
                            data.data.data[0].organizationcode = '';
                        }
                        $scope.cityList = data.data.data;
                        $scope.organization = $scope.cityList[0];
                        cb && cb();
                      }
                    })
          
                }
                var orgChangeListener = $rootScope.$on('updateOrg', function () {
                    setCityCode(G.userInfo.organizationcode, function () {
                      $scope.currPage = 1;
                      $timeout(getList);
                    });
                });

                setCityCode(G.userInfo.organizationcode, function () {
                    $timeout(getList);
                });
                $scope.$on('$destroy', orgChangeListener);
            }]);
        }
    });
