define([
    'angular',
    '../addNewDatadir/addNewDatadirService',
    '../addNewDatadir/addNewDatadir.html',
    '../addNewDatadir/addNewDatadir.css',
    ], function(
        angular,
        service,
        html) {
        return function(app, elem, attrs, scope) {
            service(app, elem, attrs, scope);
            app.directive('addNewDatadir', ['addNewDatadirService','G','$timeout',function(service,G,$timeout) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        currItem:'=',
                        opType:'=',
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude, $log, $http, G) {


                        $('#addNewDatadir').on('show.bs.modal',function(){
                            $scope.$apply(function(){
                                $scope.formData={};
                                if($scope.opType==='edit') {
                                    $scope.formData.title='修改';
                                    $scope.formData=G.clone($scope.currItem);
                                }else {
                                    $scope.formData.title='添加';
                                }
                            })

                        });

                        $('#addNewDatadir').on('hide.bs.modal',function(){
                            G.clearAllError();
                        });

                        $scope.submit=function(){
                            if($scope.opType==='edit'){
                                service.updatePubDictionary({
                                    ehdpubdictionaryid:$scope.formData.ehdpubdictionaryid,
                                    dictionarycode:$scope.formData.dictionarycode,
                                    dictionaryname:$scope.formData.dictionaryname,
                                    description:$scope.formData.description
                                }).then(function(data){
                                    if(data.data.result=='success'){
                                        $('#addNewDatadir').modal('hide');
                                        G.alert('修改数据字典成功!');
                                        $scope.currItem=G.clone($scope.formData);
                                        $timeout(function(){
                                            $scope.$emit('refresh');
                                        },60)
                                    }else{
                                        G.alert(data.data.msg,{type:'error'})
                                    }
                                })
                            }else {
                                service.insertPubDictionary({
                                    parentdictionarycode:$scope.currItem.dictionarycode,
                                    parentdictionaryname:$scope.currItem.dictionaryname,
                                    dictionarycode:$scope.formData.dictionarycode,
                                    dictionaryname:$scope.formData.dictionaryname,
                                    ordernumber:parseInt((+(new Date()))/1000),
                                    description:$scope.formData.description                                    
                                }).then(function(data){
                                    if(data.data.result=='success'){
                                        $('#addNewDatadir').modal('hide');
                                        G.alert('新建数据字典成功!');
                                        $timeout(function(){
                                            $scope.$emit('add');
                                        },60);
                                    }else{
                                        G.alert(data.data.msg,{type:'error'})
                                    }
                                })
                            }
                            
                        }

                    }
                };
            }]);
        }
    });