define([
    'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('tfLyAccountMgmtService', ['$http', function($http) {
            return {
                statisticsUtmsClient:(opts)=>{
                    return $http({
                        url:'/ehuodiBedrockApi/utmsclientcs/statisticsUtmsClient',
                        data:opts,
                        method:'post'
                    })
                },
                getApiUrl:()=>{
                    return '/ehuodiBedrockApi/utmsclientcs/selectUtmsClientList'
                }
            }
        }]);
    }
});