define('basicConfigModules/systemErrorCode/systemErrorCode', [
    '../systemErrorCode/systemErrorCodeCtrl',
    'scmsModules/table/table',
    'scmsModules/datePicker/datePickerDirective',
    '../errorCodeDialog/errorCodeDialog',
    '../systemErrorCode/systemErrorCodeService',
    '../systemErrorCode/systemErrorCode.css',
    '../systemErrorCode/systemErrorCode.html'
], function(
    ctrl,
    table,
    datePickerDirective,
    errorCodeDialog,
    service,
    css,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        table(app, elem, attrs, scope);
        datePickerDirective(app, elem, attrs, scope);
        errorCodeDialog(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        elem.append(html);
    }
});