define([
    'angular',
    '../updateSystemLog/updateSystemLogService',
    '../updateSystemLog/updateSystemLog.html',
    '../updateSystemLog/updateSystemLog.css'
    ], function(
        angular,
        updateSystemLogService,
        html) {
        return function(app, elem, attrs, scope) {
            updateSystemLogService(app, elem, attrs, scope);
            app.directive('updateSystemLogDirective', ['updateSystemLogService','G', '$q', '$timeout', function(service,G,$q,$timeout) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        modules:'=',
                        currentItem: '=',
                        params: '='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude,$timeout, $log, $http, G) {
                        $scope.changeModules = function(){
                            $scope.pages = [];
                            $scope.selectedPage = '';
                            $scope.actions = [];
                            $scope.selectedAction = '';
                            if($scope.selectedModule){
                                getAllPage($scope.selectedModule.children);
                            }
                        }

                        function getAllPage(modules){
                            angular.forEach(modules, function(module){
                                if(module.resourcetype===0&&module.resourceurl!==';'){
                                    $scope.pages.push(module);
                                }
                                if(module.children){
                                    getAllPage(module.children);
                                }
                            });
                        }

                        $scope.changePage = function(){
                            $scope.actions = [];
                            $scope.selectedAction = '';
                            if($scope.selectedPage){
                                getAllActions($scope.selectedPage.children);
                            }
                        };

                        $scope.submit = function(){
                            service.updateLogConfig({
                                logmonitorconfigid: $scope.currentItem.logmonitorconfigid,
                                module: $scope.selectedModule.resourcename,
                                page: $scope.selectedPage.resourcename,
                                action: $scope.selectedAction.resourcename,
                                linkurl: $scope.requestUrl,
                                coredatacfgs: $scope.selectedParam&&$scope.selectedParam.dictionarycode,
                                username: G.userInfo.username,
                                jobcard: G.userInfo.jobcard
                            }).then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    G.alert('配置修改成功');
                                    $('#updateSystemLog').modal('hide');
                                    $scope.$emit('refreshList');
                                }else{
                                    G.errorAlert('配置修改失败',data);
                                }
                            });
                        };
                        
                        function getAllActions(pages){
                            angular.forEach(pages, function(resource){
                                if(resource.resourcetype===1){
                                    $scope.actions.push(resource);
                                }
                                if(resource.children){
                                    getAllActions(resource.children);
                                }
                            });
                        }

                        function init(){
                            $timeout(function(){
                                var pages = [];
                                $scope.selectedModule = '';
                                $scope.selectedPage = '';
                                $scope.selectedAction = '';
                                $scope.selectedParam = '';
                                angular.forEach($scope.modules, function(module){
                                    if(module.resourcename===$scope.currentItem.module){
                                        $scope.selectedModule = module;
                                    }
                                })
                                $scope.changeModules();
                                angular.forEach($scope.pages, function(page){
                                    if(page.resourcename===$scope.currentItem.page){
                                        pages.push(page);
                                    }
                                });
                                for(var i=0;i < pages.length; i++){
                                    $scope.actions = [];
                                    getAllActions(pages[i].children);
                                    angular.forEach($scope.actions, function(action){
                                        if(action.resourcename===$scope.currentItem.action){
                                            $scope.selectedAction = action;
                                        }
                                    });
                                    if($scope.selectedAction){
                                        $scope.selectedPage = pages[i];
                                        break;
                                    }
                                }
                                angular.forEach($scope.params, function(param){
                                    if(param.dictionarycode===$scope.currentItem.coredatacfgs){
                                        $scope.selectedParam = param;
                                    }
                                });
                                $scope.requestUrl = $scope.currentItem.linkurl;
                                G.clearAllError();
                            });
                        }

                        $('#updateSystemLog').on('show.bs.modal', function(){
                            init();
                        })
                    }
                };
            }]);
    }
});