define([
    '../systemUpgradeMgmt/systemUpgradeMgmtCtrl',
    '../systemUpgradeMgmt/systemUpgradeMgmtService',
    'scmsModules/datePicker/datePickerDirective',
    'scmsModules/table/table',
    '../versionDetailsDialog/versionDetailsDialogDirective',
    '../addNewUpgradeDialog/addNewUpgradeDialogDirective',
    '../systemUpgradeMgmt/systemUpgradeMgmt.html',
    '../systemUpgradeMgmt/systemUpgradeMgmt.css'
], function(
    ctrl,
    service,
    datePickerDirective,
    table,
    versionDetailsDialogDirective,
    addNewUpgradeDialogDirective,
    html) {

    return function(app, elem, attrs, scope) {
        ctrl(app, elem, attrs, scope);
        service(app, elem, attrs, scope);
        datePickerDirective(app, elem, attrs, scope);
        table(app, elem, attrs, scope);
        versionDetailsDialogDirective(app, elem, attrs, scope);
        addNewUpgradeDialogDirective(app, elem, attrs, scope);
        elem.append(html);
    }
});