import './index.less'
export default (app, elem, attrs, scope) => {
  app.controller('questionBankCtrl', [
    '$scope',
    'annualPartyService',
    'G',
    ($scope, service, G) => {
      $scope.apiUrl = service.question.apiUrl;
      $scope.getLevel = lv => {
        const levelMap = {
          0: '简单',
          1: '中等',
          2: '复杂'
        };
        return levelMap[lv];
      };

      $scope.readonly = false

      $scope.fetchList = ()=>{
        $scope.fetch({currPage:1});
      };

      $scope.onfetch = function (argument) {
        $scope.fetch({currPage:1});
      };


      $scope.detail =item=>{
        $scope.currentItem = item;
        $scope.readonly = true;
      }

      $scope.edit = item=>{
        $scope.currentItem = item;
        $scope.readonly = false;
      }

      $scope.add = ()=>{
        $scope.currentItem = {};
        $scope.readonly = false;
      }


      $scope.delete = function($event, id) {
        var $alert = G.confirm({
          $target: $($event.target),
          msg: '确认删除该题目吗？',
          position: 'bottom',
          submit: function() {
            G.showWait();
            service.question
              .delete({
                gameQuestionId: id
              })
              .then(
                function(data) {
                  G.hideWait();
                  var data = data.data;
                  $alert.hide();
                  if (data.result === 'success') {
                    G.alert('删除成功');
                    $scope.fetch();
                  } else {
                    G.alert(data.msg || '操作失败', { type: 'error' });
                  }
                },
                function() {
                  G.hideWait();
                  $alert.hide();
                  G.alert('操作失败', { type: 'error' });
                }
              );
          }
        });
      };
    }
  ]);
};
