define([
    'angular',
    '../addNewDeptDialog/addNewDeptDialogService',
    '../addNewDeptDialog/addNewDeptDialog.html'
    ], function(
        angular,
        addNewDeptDialogService,
        html) {
        return function(app, elem, attrs, scope) {
            addNewDeptDialogService(app, elem, attrs, scope);
            app.directive('addNewDeptDialogDirective', ['G', 'addNewDeptDialogService', '$timeout',function(G, service, $timeout) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        selectedDept: '=',
                        isUpdate: '='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude,$timeout, $log, $http, G) {

                        $scope.submit = function(){
                            var msg = $scope.isUpdate?'更新':'添加';
                            var opt = {
                                departmentname: $scope.deptName,
                                organizationcode: $scope.selectedDept.organizationcode,
                                organizationname: $scope.selectedDept.organizationname,
                                description: $scope.description
                            }
                            if($scope.isUpdate){
                                opt.ehdrbacdepartmentid = $scope.selectedDept.ehdrbacdepartmentid;
                                opt.pardepartmentid = $scope.selectedDept.pardepartmentid
                                upDateDept(opt,msg);
                            }else {
                                opt.pardepartmentid = $scope.selectedDept.ehdrbacdepartmentid;
                                addDept(opt,msg);
                            }
                        };

                        function addDept(opts,msg){
                              service.addDept(opts).then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    G.alert(msg + '成功');
                                    $element.modal('hide');
                                    $scope.$emit('addMarketSuccess');
                                }else{
                                    G.errorAlert(msg + '失败', data);
                                }
                            },function(data){
                                G.errorAlert(msg + '失败', data);
                            });
                        }


                        function upDateDept(opts,msg) {
                            service.updateDept(opts).then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    G.alert(msg + '成功');
                                    $element.modal('hide');
                                    $scope.$emit('addMarketSuccess');
                                }else{
                                    G.errorAlert(msg + '失败', data);
                                }
                            },function(data){
                                G.errorAlert(msg + '失败', data);
                            });
                        }



                        $scope.onItemClick = function(item){
                            $scope.deptName = item.text;
                        };

                        function formateList(list){
                            var distList = [];
                            angular.forEach(list, function(item){
                                distList.push({
                                    text: item.dictionaryname
                                });
                            });
                            return distList;
                        }

                        function getDemoDeptName(){
                            service.getDemoDeptName({
                                parentdictionarycode: 'department'
                            }).then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    $scope.demoDeptName = formateList(data.data.data);
                                }
                            });
                        }

                        function initForm(){
                            G.clearAllError();
                            if($scope.isUpdate){
                                $scope.deptName = $scope.selectedDept.departmentname;
                                $scope.description = $scope.selectedDept.description;
                            }else{
                                $scope.deptName = '';
                                $scope.description = '';
                            }
                        }

                        $('#addNewDept').on('show.bs.modal',function(e){
                            initForm();
                            getDemoDeptName(); 
                        });

                    }
                };
            }]);
}
});