define([
    'angular',
    'scmsModules/multiselect/multiselect',
    '../addNewMenu/addNewMenuService',
    '../addNewMenu/addNewMenu.html'
    ], function(
        angular,
        multiselect,
        service,
        html) {
        return function(app, elem, attrs, scope) {
            multiselect(app, elem, attrs, scope);
            service(app, elem, attrs, scope);
            app.directive('addNewMenu', ['addNewMenuService','G','$timeout',function(service,G,$timeout) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        currItem:'=',
                        opType:'=',
                        parentresourceid:'=',
                        currentFlag:'='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude, $log, $http, G) {

                        $scope.resourceTypes=[{name:'菜单',value:0},{name:'按钮',value:1}]

                        $('#addNewMenu').on('show.bs.modal',function(){
                            $scope.errors = {};
                            $scope.selectedRole = [];
                            $scope.selectedModuleType = [];
                            $scope.$apply(function(){
                                getRoleList();
                                $scope.formData = {};
                                $scope.formData.resourcetypeObj={};
                                if($scope.opType==='edit') {
                                    $scope.formData = G.clone($scope.currItem);
                                    $scope.formData.title = '修改';
                                    angular.forEach($scope.resourceTypes, function(type){
                                        if(type.value===$scope.currItem.resourcetype){
                                            $scope.formData.resourcetypeObj = type;
                                        }
                                    });
                                }else {
                                    $scope.formData.title = '新增';
                                }
                                $scope.errorResourcetype = false;
                                $scope.initResourcetype = false;
                            })
                        });
 
                        $scope.$watch('formData.resourcetypeObj',function(newVal,oldVal){
                            $scope.errors = {};
                        },true);

                        $('#addNewMenu').on('hide.bs.modal',function(){
                            G.clearAllError();
                        });

                        $scope.submit = function(){
                            if(!$scope.formData.resourcetypeObj.value&&$scope.formData.resourcetypeObj.value!==0&&$scope.formData.resourcetypeObj.value!==1){
                                $scope.errorResourcetype = true;
                                $scope.initResourcetype = true;
                                $scope.errors.type = '请选择标记类型';
                                return;
                            }
                            if($scope.selectedRole.length>0&&$scope.selectedModuleType.length<1){
                                $scope.errors.targetRole = '请选择目标角色';
                                return;
                            }

                            let targetattributes = [];
                            angular.forEach($scope.selectedRole, function(role){
                                angular.forEach(role.children, function(child){
                                    angular.forEach($scope.selectedModuleType, function(selected){
                                        if(child.name===selected.name){
                                            targetattributes.push({
                                                userattributes: role.name,
                                                businessattributes: selected.name
                                            });
                                        }
                                    });
                                });
                            });

                            if($scope.opType==='edit'){
                                if($scope.formData.children&&$scope.formData.children.length>0&&$scope.formData.resourcetypeObj.value===1) {
                                    G.alert('该菜单下还有其他菜单或按钮，不能更改为按钮',{type:'error',$container:$element.find('.modal-content')});
                                    return;
                                }
                                
                                service.updateRbacResource({
                                    parentresourceid: $scope.formData.parentresourceid,
                                    ehdrbacresourceid: $scope.formData.ehdrbacresourceid,
                                    resourcename: $scope.formData.resourcename,
                                    resourcetype: $scope.formData.resourcetypeObj.value,
                                    resourceurl: $scope.formData.resourceurl,
                                    description: $scope.formData.description,
                                    applicationflag: $scope.currentFlag,
                                    targetattributes: JSON.stringify(targetattributes)
                                }).then(function(data){
                                    if(data.data.result=='success'){
                                        $('#addNewMenu').modal('hide');
                                        G.alert('修改资源成功!');
                                        $scope.currItem = G.clone($scope.formData);
                                        $timeout(function(){
                                            $scope.$emit('refreshMenu');
                                        },60)
                                    }else{
                                        G.alert(data.data.msg,{type:'error',$container:$element.find('.modal-content')});
                                    }
                                })
                            }else {
                                service.insertRbacResource({
                                    parentresourceid:$scope.parentresourceid,
                                    resourcename:$scope.formData.resourcename,
                                    resourcetype:$scope.formData.resourcetypeObj.value,
                                    resourceurl:$scope.formData.resourceurl,
                                    description:$scope.formData.description,
                                    applicationflag: $scope.currentFlag,
                                    targetattributes: JSON.stringify(targetattributes)
                                }).then(function(data){
                                    if(data.data.result=='success'){
                                        $('#addNewMenu').modal('hide');
                                        G.alert('新建资源成功!');
                                        $scope.$emit('refreshMenu');
                                    }else{
                                        G.alert(data.data.msg,{type:'error',$container:$element.find('.modal-content')});
                                    }
                                })
                            }
                        }

                        function getRoleList(){
                            service.selectPubDictionaryListByParam({
                                parentdictionarycode: 'targetRole',
                                returnformat: 1
                            }).then(function(data){
                                let selectedRoleArray = [];
                                $scope.roleList = [];
                                $scope.moduleTypeList = [];
                                let roleArray = [], roleJSON = [];
                                if($scope.formData.targetattributes){
                                    roleJSON = JSON.parse($scope.formData.targetattributes);
                                }
                                if(data&&data.data&&data.data.data){
                                    let result = data.data.data;
                                    angular.forEach(result,function(item){
                                        item.name = item.dictionaryname;
                                    });
                                    roleArray = G.toTree(result,'targetRole','parentdictionarycode','dictionarycode');
                                    angular.forEach(roleArray,function(type){
                                        $scope.roleList.push(type);
                                        if(Array.isArray(roleJSON)&&roleJSON.length>0){
                                            angular.forEach(roleJSON,function(role){
                                                if(role.userattributes===type.name&&selectedRoleArray.indexOf(role.userattributes)<0){
                                                    type.isChecked = true;
                                                    selectedRoleArray.push(role.userattributes);
                                                    $scope.selectedRole.push(type);
                                                    $scope.moduleTypeList = $scope.moduleTypeList.concat(type.children);
                                                }
                                            });
                                        }
                                    });
                                    angular.forEach($scope.moduleTypeList,function(type){
                                        if(Array.isArray(roleJSON)&&roleJSON.length>0){
                                            angular.forEach(roleJSON,function(role){
                                                if(role.businessattributes===type.name){
                                                    type.isChecked = true;
                                                    $scope.selectedModuleType.push(type);
                                                }
                                            });
                                        }
                                    });
                                }
                            });
                        }

                        $scope.$watch('selectedRole', function(newValue, oldValue){
                            if(newValue){
                                let selectedType = G.clone($scope.selectedModuleType);
                                $scope.selectedModuleType = [];
                                $scope.moduleTypeList = [];
                                angular.forEach(newValue,function(role){
                                    $scope.moduleTypeList = $scope.moduleTypeList.concat(role.children);
                                });
                                angular.forEach($scope.moduleTypeList,function(type){
                                    angular.forEach(selectedType,function(selected){
                                        if(type.name===selected.name){
                                            $scope.selectedModuleType.push(selected);
                                        }
                                    })
                                });
                            }
                        });
                    }
                };
            }]);
        }
    });