define([
    'angular',
    '../basicSelectCityGroup/basicSelectCityGroupDirective',
    '../addNewAreaDialog/addNewAreaDialogService',
    '../directiveFormInput/formSelect',
    '../addNewAreaDialog/addNewAreaDialog.html'
    ], function(
        angular,
        basicSelectCityGroupDirective,
        addNewAreaDialogService,
        formSelect,
        html) {
        return function(app, elem, attrs, scope) {
            basicSelectCityGroupDirective(app, elem, attrs, scope);
            addNewAreaDialogService(app, elem, attrs, scope);
            formSelect(app, elem, attrs, scope);
            app.directive('addNewAreaDialogDirective', ['G', 'addNewAreaDialogService', '$timeout',function(G, service, $timeout) {
                return {
                    template: html,
                    restrict: 'EA',
                    replace: true,
                    scope: {
                        selectedItem: '=',
                        currCity:'=',
                        isUpdate: '='
                    },
                    link: function postLink($scope, $element, $attrs) {

                    },
                    controller: function($scope, $element, $attrs, $transclude,$timeout, $log, $http, G) {

                        $scope.submit = function (){
                            if(!validateFrom()){
                                return;
                            }
                            var param = {
                                marketplacename: $scope.marketName,
                                marketplacetype: $scope.selectedType.dictionaryname,
                                province: $scope.address.province,
                                city: $scope.address.city,
                                region: $scope.address.region
                            };
                            if($scope.isUpdate){
                                param.ehdrbacmarketplaceid = $scope.selectedItem.ehdrbacmarketplaceid;
                                updateMarket(param);
                            }else{
                                addMarket(param);
                            }
                        };

                        function validateFrom(){
                            if($scope.address.region===false){
                                $scope.errors.region = '请选择市场位置';
                                return false;
                            }
                            return true;
                        }

                        function addMarket(param){
                            param.areaname = $scope.currCity.organizationname==='全国'?'总部':$scope.currCity.organizationname;
                            service.addMarket(param).then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    G.alert('添加成功');
                                    $element.modal('hide');
                                    $scope.$emit('addMarketSuccess');
                                }else{
                                    G.errorAlert('添加失败', data);
                                }
                            },function(data){
                                G.errorAlert('添加失败', data);
                            });
                        }

                        function updateMarket(param){
                            param.ehdrbacmarketplaceid = $scope.selectedItem.ehdrbacmarketplaceid;
                            param.areaname = $scope.selectedItem.areaname;

                            service.updateMarket(param).then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    G.alert('更新成功');
                                    $element.modal('hide');
                                    $scope.$emit('addMarketSuccess');
                                }else{
                                    G.errorAlert('更新失败', data);
                                }
                            },function(data){
                                G.errorAlert('更新失败', data);
                            });
                        }

                        function getMarketType(){
                            service.getMarketType({
                                parentdictionarycode: 'marketType'
                            }).then(function(data){
                                if(data&&data.data&&data.data.result==='success'){
                                    $scope.marketType = data.data.data;
                                    if($scope.selectedItem&&$scope.selectedItem.marketplacetype){
                                        angular.forEach($scope.marketType, function(market){
                                            if(market.dictionaryname == $scope.selectedItem.marketplacetype){
                                                $scope.selectedType = market;
                                            }
                                        });
                                    }
                                }
                            });
                        }

                        function initForm(){
                            $scope.errors = {};
                            if($scope.isUpdate){
                                $scope.marketName = $scope.selectedItem.marketplacename;
                                $scope.address = {};
                                $scope.address.province = $scope.selectedItem.province;
                                $scope.address.city = $scope.selectedItem.city;
                                $scope.address.region = $scope.selectedItem.region;
                            }else{
                                $scope.selectedItem = {}
                                $scope.selectedType = '';
                                $scope.marketName = '';
                                $scope.address = {};
                            }
                            $scope.updateData=+(new Date());
                            G.clearAllError();
                        }

                        $('#addNewAreaDialog').on('show.bs.modal',function(e){
                            initForm(); 
                            getMarketType();
                        });

                    }
                };
            }]);
        }
    });